import _map from 'lodash/map';
import _get from 'lodash/get';
import _first from 'lodash/first';
import _slice from 'lodash/slice';
import Api from '../utils/api';

const Slack = {
  inviteUsers({ role, locationIds, slackUsers }) {
    const body = {
      role,
      location_ids: locationIds,
      slack_users: _map(slackUsers, slackUser => {
        const fullNameArray = _get(slackUser, 'name', '').trim().split(' ');
        return {
          name: _first(fullNameArray),
          lastname: _slice(fullNameArray, 1).join(' '),
          email: _get(slackUser, 'email')
        };
      })
    };
    return Api.post('/slack/invite', body);
  },
  listUsers({ canInvite, nextCursor }) {
    if (canInvite) {
      return Api.get('/slack/users', { cursor: nextCursor });
    }
    return null;
  }
};

export default Slack;
