/* global window */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { Link } from 'react-router-dom';
import { compose } from 'recompose';
import LiveChat from 'react-livechat';
import _get from 'lodash/get';
import _size from 'lodash/size';
import { withStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Hidden from '@material-ui/core/Hidden';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import SettingsIcon from '@material-ui/icons/Settings';
import HelpIcon from '@material-ui/icons/LiveHelp';
import LogoutIcon from '@material-ui/icons/ExitToApp';
import Badge from '@material-ui/core/Badge';
import Typography from '@material-ui/core/Typography';
import AvatarImage from '../AvatarImage';
import AlertBox from '../AlertBox';
import { MEMBER_ROLE } from '../../enums/user-roles';
import Session from '../../utils/session';
import Tracker from '../../utils/tracker';
import styles from './styles';
import config from '../../config';
import Routes from '../../routes';

const tabMenu = Object.values(Routes).filter((r) => !r.hideMenu && !r.settingsMenu);

class Header extends PureComponent {
  state = {
    userDataError: false,
    sideMenuOpen: false,
    profileMenuAnchor: null,
    showHelp: false
  }

  onOpenHelp = () => {
    this.setState({ profileMenuAnchor: null }, () => window.open(config.whatsappHelp, '_blank'));
  }

  onLogout = () => {
    this.setState({ profileMenuAnchor: null }, () => {
      if (window.confirm('Voc deseja sair?')) {
        const { history } = this.props;
        Tracker.hit('Logout');
        Session.clear();
        history.push('/');
      }
    });
  }

  onSettings = () => {
    this.setState({ profileMenuAnchor: null }, () => {
      const { history } = this.props;
      Tracker.hit('Settings');
      history.push('/configuracoes/usuarios');
    });
  }

  onToggleSideMenu = () => {
    const { sideMenuOpen } = this.state;
    this.setState({ sideMenuOpen: !sideMenuOpen });
  }

  onChangePage = (e, tabIndex) => {
    const { history } = this.props;
    history.push(tabMenu[tabIndex].path);
  }

  onToggleProfileMenu = e => this.setState({ profileMenuAnchor: e.currentTarget });

  prepareUser = () => {
    const member = Session.getUserData();
    return {
      name: _get(member, 'fullname', ''), email: _get(member, 'email', '')
    };
  }

  prepareMenu = (menuList) => {
    const member = Session.getUserData();
    const hasServices = _get(member, 'role', MEMBER_ROLE) !== MEMBER_ROLE;
    return menuList.filter((menu) => {
      if (!hasServices && menu.path.includes('/servicos')) {
        return false;
      }
      if (!hasServices && menu.path.includes('/meus-pedidos')) {
        return false;
      }
      return true;
    });
  }

  renderAlerts = () => {
    const { alertBox, error } = this.props;
    const { userDataError } = this.state;
    const err = error || userDataError;
    if (alertBox) {
      return alertBox;
    }
    if (err) {
      return (
        <AlertBox
          type="error"
          message="Erro ao carregar dados do servidor"
          actionButton={<b>Recarregar</b>}
          onClickAction={() => window.location.reload()}
        />
      );
    }
    return null;
  }

  renderSideMenuList(menuList, menuTitle = '') {
    const { classes, history } = this.props;
    const filteredMenu = this.prepareMenu(menuList);
    if (_size(filteredMenu)) {
      return (
        <List
          component="nav"
          subheader={
            <ListSubheader component="div">
              <Typography variant="body2" color="textSecondary" component="small">
                <strong className={classes.listItemHeaderText}>{menuTitle}</strong>
              </Typography>
            </ListSubheader>
          }
        >
          {filteredMenu.map((menu, tabKey) => {
            const subMenus = Object.values(menu.submenus || {});
            const hasSubmenu = Boolean(_size(subMenus));
            let listItemProps = {};
            if (!hasSubmenu) {
              listItemProps = {
                component: Link,
                to: menu.path.replace(/:[\w*]+/g, ''),
                selected: (
                  history.location.pathname.includes(menu.path) ||
                  menu.path.includes(history.location.pathname)
                )
              };
            }
            return (
              <div key={tabKey.toString()}>
                <ListItem
                  button={!hasSubmenu}
                  key={menu.path}
                  {...listItemProps}
                >
                  {
                    menu.icon &&
                      <ListItemIcon className={classes.listItemIcon}>
                        {menu.icon}
                      </ListItemIcon>
                  }
                  <ListItemText
                    className={classes.listItemText}
                    disableTypography
                    primary={
                      <Badge
                        badgeContent={menu.badge}
                        color="primary"
                        invisible={!menu.badge}
                        classes={{ badge: classes.badge }}
                      >
                        {menu.label}
                      </Badge>
                    }
                  />
                </ListItem>
                {
                  hasSubmenu &&
                    <List className={classes.submenuList}>
                      {
                        subMenus.map((submenu) => {
                          const subMenuPath = `${menu.path}${submenu.path}`;
                          return (
                            <ListItem
                              button
                              className={classes.submenuItem}
                              key={subMenuPath}
                              component={Link}
                              to={subMenuPath.replace(/:[\w*]+/g, '')}
                              selected={subMenuPath === history.location.pathname}
                            >
                              {
                                submenu.icon &&
                                  <ListItemIcon className={classes.listItemIcon}>
                                    {submenu.icon}
                                  </ListItemIcon>
                              }
                              <ListItemText
                                className={classes.listItemText}
                                disableTypography
                                primary={submenu.label}
                              />
                            </ListItem>
                          );
                        })
                      }
                    </List>
                }
              </div>
            );
          })}
        </List>
      );
    }
    return null;
  }

  renderUserProfile() {
    const { classes } = this.props;
    const { profileMenuAnchor } = this.state;
    const member = Session.getUserData();
    const userName = _get(member, 'fullname', '');
    const memberPhotoURL = _get(member, 'photo_url', '');
    return (
      <List component="nav" className={classes.userProfileList}>
        <ListItem alignItems="flex-start">
          <div className={classes.profileMenuContainer}>
            <AvatarImage name={userName} photoUrl={memberPhotoURL} />
            <span className={classes.userName} onClick={this.onToggleProfileMenu}>
              {userName}
            </span>
            <IconButton size="medium" onClick={this.onToggleProfileMenu}>
              <KeyboardArrowDownIcon />
            </IconButton>
            <Menu
              keepMounted
              anchorEl={profileMenuAnchor}
              open={Boolean(profileMenuAnchor)}
              onClose={() => this.setState({ profileMenuAnchor: null })}
              transformOrigin={{ vertical: 'bottom', horizontal: 'right' }}
            >
              <MenuItem onClick={this.onSettings}>
                <ListItemIcon>
                  <SettingsIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText className={classes.listItemText} primary="Configuraes" />
              </MenuItem>
              <MenuItem onClick={this.onOpenHelp}>
                <ListItemIcon>
                  <HelpIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText className={classes.listItemText} primary="Ajuda" />
              </MenuItem>
              <MenuItem onClick={this.onLogout}>
                <ListItemIcon>
                  <LogoutIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText className={classes.listItemText} primary="Sair" />
              </MenuItem>
            </Menu>
          </div>
        </ListItem>
      </List>
    );
  }

  renderSideMenu(isSmallScreen) {
    const { classes } = this.props;
    const { sideMenuOpen } = this.state;
    return (
      <Drawer
        classes={{ paper: classes.drawer }}
        variant={isSmallScreen ? 'persistent' : 'permanent'}
        anchor="left"
        open={sideMenuOpen}
        ModalProps={{ keepMounted: !isSmallScreen }}
      >
        <div className={classes.headerLogoContainer}>
          {
            isSmallScreen &&
              <div className={classes.closeMenuIcon}>
                <IconButton size="medium" onClick={this.onToggleSideMenu}>
                  <ChevronLeftIcon />
                </IconButton>
              </div>
          }
          <div className={classes.logoContainer}>
            <Link to="/">
              <img
                src={require('../../assets/images/logo-header.png')}
                alt={config.appName}
                className={classes.logo}
              />
            </Link>
          </div>
        </div>
        {this.renderSideMenuList(tabMenu)}
        {this.renderUserProfile()}
      </Drawer>
    );
  }

  render() {
    const { classes } = this.props;
    const { showHelp } = this.state;
    return (
      <React.Fragment>
        <div className={classes.root}>
          <IconButton
            aria-label="open drawer"
            onClick={this.onToggleSideMenu}
            edge="start"
          >
            <MenuIcon />
          </IconButton>
          <Hidden smDown>
            {this.renderSideMenu(false)}
          </Hidden>
          <Hidden mdUp>
            {this.renderSideMenu(true)}
          </Hidden>
          {this.renderAlerts()}
          {
            config.isProduction && showHelp &&
              <LiveChat
                license={config.livechatId}
                visitor={this.prepareUser()}
              />
          }
        </div>
      </React.Fragment>
    );
  }
}

Header.propTypes = {
  history: PropTypes.object,
  alertBox: PropTypes.any,
  error: PropTypes.any,
  classes: PropTypes.object
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter
)(Header);
