import Api from '../utils/api';

const Member = {
  getViewer() {
    return Api.get('/member');
  },
  updateMember({ name, lastname, jobRole }) {
    return Api.put('/member', { name, lastname, job_role: jobRole });
  },
  updateMemberPassword({ password }) {
    return Api.put('/member/change_password', { password });
  },
  revokeSlackUser() {
    return Api.put('/member/revoke_slack');
  }
};

export default Member;
