import FieldNormalizer from 'field-normalizer';

import Api from '../utils/api';

const NotificationSettings = {
  toggleNewTicket() {
    return Api.put('/notification_settings/toggle_new_ticket', {})
      .then(response => FieldNormalizer.toCamelCase(response))
      .catch(error => {
        if (error.errors) error.errors = FieldNormalizer.toCamelCase(error.errors);
        throw error;
      });
  },
  toggleTicketUpdates() {
    return Api.put('/notification_settings/toggle_ticket_updates', {})
      .then(response => FieldNormalizer.toCamelCase(response))
      .catch(error => {
        if (error.errors) error.errors = FieldNormalizer.toCamelCase(error.errors);
        throw error;
      });
  }
};

export default NotificationSettings;
