import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import Api from '../utils/api';

const Tickets = {
  getTickets(params) {
    return Api.get('/tickets', {
      location_id: _get(params, 'locationId'),
      assignee_id: _get(params, 'assigneeId'),
      category_id: _get(params, 'categoryId'),
      deadline: _get(params, 'deadline'),
      priority: _get(params, 'priority'),
      archived: _get(params, 'archived'),
      status: _get(params, 'status'),
      per_page: _get(params, 'perPage'),
      page: _get(params, 'page')
    });
  },
  getTicket(ticketId) {
    if (ticketId) {
      return Api.get(`/tickets/${ticketId}`);
    }
    return null;
  },
  getTicketByCode(code) {
    if (code) {
      return Api.get(`/tickets/codes/${code}`);
    }
    return null;
  },
  updateTicket({ ticket, selectedAssignees }) {
    const body = {
      id: _get(ticket, 'id'),
      title: _get(ticket, 'title'),
      priority: _isEmpty(_get(ticket, 'priority')) ? null : _get(ticket, 'priority'),
      deadline: _get(ticket, 'deadline'),
      location_id: _get(ticket, 'location.id'),
      assignee_ids: selectedAssignees,
      category_id: _get(ticket, 'category.id')
    };
    return Api.put(`/tickets/${_get(ticket, 'id')}`, body);
  },
  moveTicket({ id, status, position, location_id }) {
    return Api.put(`/tickets/${id}/move`, { status, position, location_id });
  },
  archiveTicket({ ticketsIds }) {
    return Api.put('/tickets/archive', { tickets_ids: ticketsIds });
  },
  unarchiveTicket({ ticketsIds }) {
    return Api.put('/tickets/unarchive', { tickets_ids: ticketsIds });
  },
  postTicket({ attachment_data: receivedAttachmentData, ...otherAttrs }) {
    let attachmentData = null;
    if (receivedAttachmentData) {
      attachmentData = {
        id: receivedAttachmentData.id,
        storage: receivedAttachmentData.storage,
        metadata: receivedAttachmentData.metadata,
        base64Data: receivedAttachmentData.base64Data
      };
    }
    return Api.post('/tickets', {
      ...otherAttrs,
      attachment_data: attachmentData,
      priority: _isEmpty(_get(otherAttrs, 'priority')) ? null : _get(otherAttrs, 'priority')
    });
  },
  deleteTicket(ticketId) {
    return Api.delete(`/tickets/${ticketId}`);
  },
  getAssignables() {
    return Api.get('/tickets/assignables');
  },
  exportCsv({ start_date, end_date }) {
    return Api.post('/tickets/export_csv', { start_date, end_date });
  },
  report({ location_id, start_date, end_date }) {
    return Api.post('/tickets/report', { location_id, start_date, end_date });
  },
  markAsRead(ticketId) {
    return Api.put(`/tickets/${ticketId}/mark_as_read`);
  },
  postPublicTicket({
    ticket: { attachment_data: receivedAttachmentData, ...otherAttrs },
    companyId
  }) {
    let attachmentData = null;
    if (receivedAttachmentData) {
      attachmentData = {
        id: receivedAttachmentData.id,
        storage: receivedAttachmentData.storage,
        metadata: receivedAttachmentData.metadata,
        base64Data: receivedAttachmentData.base64Data
      };
    }
    return Api.post(
      `/public_tickets?company_id=${companyId}`,
      { attachment_data: attachmentData, ...otherAttrs }
    );
  }
};

export default Tickets;
