import React, { memo } from 'react';
import PropTypes from 'prop-types';
import { compose } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import {
  TextField, FormControl, FormHelperText,
  FormGroup, FormControlLabel, Checkbox
} from '@material-ui/core';

import {
  CHECKBOX, TEXT_AREA, SELECT
} from '../../enums/fields';
import styles from './styles';

const QuestionInput = memo(({
  id, classes, required, disabled, question, options,
  fieldType, showBlankError, onChangeQuestion, onCheckOption
}) => {
  const showError = showBlankError && required;
  if (fieldType === CHECKBOX) {
    return (
      <FormControl
        component="fieldset"
        className={classes.checkboxControl}
        fullWidth
        error={showError}
      >
        <FormHelperText component="legend">{question}</FormHelperText>
        <FormGroup>
          {options.map(o => (
            <FormControlLabel
              key={o}
              label={o}
              control={
                <Checkbox
                  color="primary"
                  disabled={disabled}
                  onChange={e => onCheckOption(o, e.target.checked, id)}
                />
              }
            />
          ))}
        </FormGroup>
        {showError && <FormHelperText>selecione uma opo</FormHelperText>}
      </FormControl>
    );
  }
  if (fieldType === SELECT) {
    return (
      <FormControl fullWidth error={showError}>
        <TextField
          select
          label={question}
          className={classes.questionInput}
          disabled={disabled}
          InputLabelProps={{ shrink: true }}
          SelectProps={{ native: true }}
          onChange={e => onChangeQuestion(e.target.value, id)}
          variant="outlined"
          error={showError}
        >
          {options.map(o => <option key={o} value={o}>{o}</option>)}
        </TextField>
        {showError && <FormHelperText>selecione uma opo</FormHelperText>}
      </FormControl>
    );
  }
  return (
    <FormControl fullWidth error={showError}>
      <TextField
        label={question}
        className={classes.questionInput}
        multiline={fieldType === TEXT_AREA}
        rows={fieldType === TEXT_AREA ? 4 : 1}
        rowsMax={fieldType === TEXT_AREA ? 4 : 1}
        disabled={disabled}
        InputLabelProps={{ shrink: true }}
        onChange={e => onChangeQuestion(e.target.value, id)}
        variant="outlined"
        error={showError}
      />
      {showError && <FormHelperText>este campo est em branco</FormHelperText>}
    </FormControl>
  );
});

QuestionInput.propTypes = {
  classes: PropTypes.object,
  id: PropTypes.string,
  question: PropTypes.string,
  options: PropTypes.array,
  fieldType: PropTypes.string,
  disabled: PropTypes.bool,
  required: PropTypes.bool,
  showBlankError: PropTypes.bool,
  onCheckOption: PropTypes.func,
  onChangeQuestion: PropTypes.func
};

export default compose(
  withStyles(styles, { withTheme: true })
)(QuestionInput);
