import React, { memo } from 'react';
import { compose } from 'recompose';
import PropTypes from 'prop-types';
import { Link, withRouter } from 'react-router-dom';
import { get as _get } from 'lodash';
import { TableCell, TableRow } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import moment from 'moment';

import AvatarImage from '../../../../components/AvatarImage';
import withViewerData from '../../../../utils/with-viewer-data';
import styles from './styles';

const MyRequestsItemRow = memo(({ viewer, classes, serviceRequest, isSmallScreen }) => {
  if (!viewer) return null;

  const requester = serviceRequest.member;
  const serviceTitle = _get(serviceRequest, 'service.title', '');
  const serviceRequestDate = _get(serviceRequest, 'created_at', '');
  const serviceImage = _get(serviceRequest, 'service.image_url', '');
  if (isSmallScreen) {
    return (
      <TableRow>
        <TableCell>
          <img
            src={serviceImage}
            alt={serviceTitle}
            className={classes.image}
          />
          <p className={classes.title}><b>{serviceTitle}</b></p>
          <p className={classes.subtitle}>
            {`Enviado em ${moment(serviceRequestDate).format('DD/MM/YYYY')}`}
          </p>
          <p>
            <div className={classes.avatarNameContainer}>
              <AvatarImage name={requester.fullname} photoUrl={requester.photo_url} />
              <span className={`${classes.cellText} ${classes.userName}`}>
                {requester.fullname}
              </span>
            </div>
          </p>
          <br />
          <Button
            fullWidth
            variant="outlined"
            component={Link}
            to={`/meus-pedidos/${serviceRequest.id}`}
            color="primary"
            size="large"
          >
            Ver Solicitao
          </Button>
        </TableCell>
      </TableRow>
    );
  }
  return (
    <TableRow>
      <TableCell>
        <div className={classes.container}>
          <img
            src={serviceImage}
            alt={serviceTitle}
            className={classes.image}
          />
          <div className={classes.innerContainerTexts}>
            <p className={classes.title}><b>{serviceTitle}</b></p>
            <p className={classes.subtitle}>{`Enviado em ${moment(serviceRequestDate).format('DD/MM/YYYY')}`}</p>
          </div>
        </div>
      </TableCell>
      <TableCell>
        <div className={classes.avatarNameContainer}>
          <AvatarImage name={requester.fullname} photoUrl={requester.photo_url} />
          <span className={`${classes.cellText} ${classes.userName}`}>
            {requester.fullname}
          </span>
        </div>
      </TableCell>
      <TableCell>
        <div className={classes.buttonContainer}>
          <Button
            variant="outlined"
            component={Link}
            to={`/meus-pedidos/${serviceRequest.id}`}
            color="primary"
            size="large"
          >
            Ver Solicitao
          </Button>
        </div>
      </TableCell>
    </TableRow>
  );
});

MyRequestsItemRow.propTypes = {
  viewer: PropTypes.object,
  classes: PropTypes.object,
  serviceRequest: PropTypes.object,
  isSmallScreen: PropTypes.bool
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter,
  withViewerData
)(MyRequestsItemRow);
