import React, { memo } from 'react';
import _get from 'lodash/get';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { compose, withState, lifecycle } from 'recompose';
import Header from '../../components/Header';
import FormTextField from '../../components/FormFields/FormTextField';
import AlertBox from '../../components/AlertBox';
import SettingsMenu from '../../components/SettingsMenu';
import withRequest from '../../utils/with-request';
import MemberService from '../../services/member';
import Session from '../../utils/session';
import styles from './styles';

const MyProfile = memo(({
  alert, setAlert, classes,
  updateMemberFetch, updateMemberLoading, errors,
  email, name, lastname, setName, setLastname, jobRole, setJobRole
}) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  const onSubmit = () => updateMemberFetch(true);
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  const isDisabled = updateMemberLoading;
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} />
      <section>
        <div className={classes.container}>
          <div className={classes.headerView}>
            <aside>
              <Typography variant="h5">
                <b>Configuraes</b>
              </Typography>
            </aside>
          </div>
        </div>
        <div className={classes.container}>
          <SettingsMenu />
        </div>
        <br />
        <div className={classes.container}>
          <Paper className={classes.paper}>
            <Grid container item xs={12} sm={6} md={4}>
              <Grid item xs={12}>
                <FormTextField
                  errors={_get(errors, 'name', [])}
                  label="Nome"
                  className={classes.input}
                  disabled={isDisabled}
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
              </Grid>
              <Grid item xs={12}>
                <FormTextField
                  errors={_get(errors, 'lastname', [])}
                  label="Sobrenome"
                  className={classes.input}
                  disabled={isDisabled}
                  value={lastname}
                  onChange={(e) => setLastname(e.target.value)}
                />
              </Grid>
              <Grid item xs={12}>
                <FormTextField
                  disabled
                  label="Email"
                  className={classes.input}
                  value={email}
                />
              </Grid>
              <Grid item xs={12}>
                <FormTextField
                  errors={_get(errors, 'jobRole', [])}
                  label="Departamento"
                  className={classes.input}
                  value={jobRole}
                  disabled={isDisabled}
                  onChange={(e) => setJobRole(e.target.value)}
                />
              </Grid>
              <Grid item xs={12}>
                <div className={classes.actions}>
                  <Button
                    onClick={onSubmit}
                    size="large"
                    color="primary"
                    variant="contained"
                    disabled={isDisabled}
                  >
                    Atualizar
                  </Button>
                </div>
              </Grid>
            </Grid>
          </Paper>
        </div>
      </section>
    </div>
  );
});

MyProfile.propTypes = {
  classes: PropTypes.object,
  name: PropTypes.string,
  email: PropTypes.string,
  setName: PropTypes.func,
  lastname: PropTypes.string,
  setLastname: PropTypes.func,
  jobRole: PropTypes.string,
  setJobRole: PropTypes.func,
  updateMemberLoading: PropTypes.bool,
  updateMemberFetch: PropTypes.func,
  alert: PropTypes.object,
  setAlert: PropTypes.func,
  errors: PropTypes.object
};

const withUpdateMember = withRequest(MemberService.updateMember, {
  name: 'updateMember',
  init: false,
  options: ({ name, lastname, jobRole, setAlert,
    setName, setLastname, setJobRole, setErrors }) => ({
    variables: { name, lastname, jobRole },
    onRequestFinished: async () => {
      try {
        const response = await MemberService.getViewer();
        Session.setUserData(_get(response, 'member'));
        setName(name);
        setLastname(lastname);
        setJobRole(jobRole);
        setAlert({ alertMsg: 'Perfil atualizado com sucesso', alertType: 'success', showAlert: true });
        setErrors({});
      } catch {
        setAlert({ alertMsg: 'Erro ao atualizar perfil', alertType: 'error', showAlert: true });
      }
    },
    onRequestError: (e) => {
      setErrors(_get(e, 'errors', {}));
      setAlert({ alertMsg: 'Erro ao atualizar perfil', alertType: 'error', showAlert: true });
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter,
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withState('disabled', 'setDisabled', false),
  withState('name', 'setName', ''),
  withState('lastname', 'setLastname', ''),
  withState('email', 'setEmail', ''),
  withState('jobRole', 'setJobRole', ''),
  withState('errors', 'setErrors', {}),
  withUpdateMember,
  lifecycle({
    componentDidMount() {
      const { setName, setLastname, setEmail, setJobRole } = this.props;
      const member = Session.getUserData();
      setName(_get(member, 'name'));
      setLastname(_get(member, 'lastname'));
      setEmail(_get(member, 'email'));
      setJobRole(_get(member, 'job_role'));
    }
  })
)(MyProfile);
