import React, { memo } from 'react';
import PropTypes from 'prop-types';
import {
  Table, TableBody, TableCell, TableHead, TableRow,
  Button, Typography, Checkbox, Select,
  FormControl, Input, MenuItem
} from '@material-ui/core';
import { withStyles, useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import _get from 'lodash/get';
import _size from 'lodash/size';
import { compose, withPropsOnChange, withState, lifecycle } from 'recompose';
import AvatarImage from '../../../components/AvatarImage';
import { ALL_ROLES } from '../../../enums/user-roles';
import styles from '../styles';

const SlackUserList = memo(({
  users, classes, onRevoke, isRevokeDisabled,
  locations, companyName, checkedAll, setCheckedAll,
  selectedLocations, setSelectedLocations, canDisconnect,
  selectedRole, setSelectedRole, isInviteDisabled, onShowMore, setDisableShowMore,
  selectedUsers, setSelectedUsers, onInvite, nextCursor, disableShowMore
}) => {
  const handleCheckedAll = (event) => {
    const { checked } = event.target;
    setCheckedAll(checked);
    if (checked) {
      setSelectedUsers(users.map(u => JSON.stringify(u)));
    } else {
      setSelectedUsers([]);
    }
  };
  const handleSelectUser = (event) => {
    const { checked, value } = event.target;
    if (checked) {
      setSelectedUsers(selectedUsers.concat(value));
    } else {
      setSelectedUsers(selectedUsers.filter(s => s !== value));
    }
  };
  const handleRoleSelect = (event) => {
    setSelectedRole(event.target.value);
  };
  const handleLocationSelect = (event) => {
    setSelectedLocations(event.target.value);
  };
  const onSubmitInvite = () => {
    try {
      const invite = {
        role: selectedRole,
        locationIds: selectedLocations,
        slackUsers: selectedUsers.map(s => JSON.parse(s))
      };
      onInvite(invite);
    } catch {
      setSelectedUsers([]);
      setSelectedLocations([]);
      setSelectedRole('');
    }
  };
  const handleShowMore = () => {
    setDisableShowMore(true);
    onShowMore();
  };
  const theme = useTheme();
  const smallScreen = useMediaQuery(theme.breakpoints.down('xs'));
  const isInviteComplete = _size(selectedRole) && _size(selectedLocations) && _size(selectedUsers);
  const disableInvite = isInviteDisabled || !isInviteComplete;
  return (
    <section>
      <div className={classes.container}>
        <div className={classes.headerView}>
          <aside>
            <Typography variant="h6">
              <b>{`Slack conectado: ${companyName}`}</b>
            </Typography>
          </aside>
          <aside>
            {
              canDisconnect &&
                <Button
                  onClick={onRevoke}
                  color="default"
                  variant="outlined"
                  className={classes.floatButton}
                  disabled={isRevokeDisabled}
                >
                  Desconectar Slack
                </Button>
            }
          </aside>
        </div>
      </div>
      <div className={classes.container}>
        <Table size={smallScreen ? 'small' : 'medium'}>
          <TableHead>
            <TableRow>
              <TableCell className={classes.tableCheckBox}>
                <Checkbox
                  checked={checkedAll}
                  onChange={handleCheckedAll}
                  disabled={isInviteDisabled}
                  color="primary"
                />
              </TableCell>
              <TableCell className={classes.tableUser}>
                {!smallScreen && <b>{`USURIOS (${_size(selectedUsers)})`}</b>}
              </TableCell>
              <TableCell className={classes.tableInvite} colSpan={smallScreen ? 2 : 1}>
                <FormControl className={classes.formControl}>
                  <Select
                    displayEmpty
                    className={classes.selectFilter}
                    input={<Input />}
                    value={selectedRole}
                    onChange={handleRoleSelect}
                    disabled={isInviteDisabled}
                    variant="outlined"
                  >
                    <MenuItem value="" disabled>Permisso</MenuItem>
                    {ALL_ROLES.map(r => (
                      <MenuItem key={r.value} value={r.value}>{r.label}</MenuItem>
                    ))}
                  </Select>
                </FormControl>
                <FormControl className={classes.formControl}>
                  <Select
                    multiple
                    displayEmpty
                    className={classes.selectFilter}
                    input={<Input />}
                    value={selectedLocations}
                    onChange={handleLocationSelect}
                    disabled={isInviteDisabled}
                    variant="outlined"
                    renderValue={(selected) => {
                      const total = _size(selected);
                      if (total) {
                        return `${total} ${total > 1 ? 'locais' : 'local'}`;
                      }
                      return 'Local';
                    }}
                  >
                    {locations.map(l => (
                      <MenuItem key={l.id} value={l.id}>{l.name}</MenuItem>
                    ))}
                  </Select>
                </FormControl>
                <FormControl className={classes.formControl}>
                  <Button
                    color="primary"
                    size="medium"
                    variant="contained"
                    className={classes.floatButton}
                    disabled={disableInvite}
                    onClick={onSubmitInvite}
                  >
                    Convidar
                  </Button>
                </FormControl>
              </TableCell>
            </TableRow>
          </TableHead>
          {
            _size(users) ?
              <TableBody>
                {
                  users.map(user => {
                    const userValue = JSON.stringify(user);
                    return (
                      <TableRow key={user.email}>
                        <TableCell className={classes.tableCheckBox}>
                          <Checkbox
                            checked={selectedUsers.includes(userValue)}
                            onChange={handleSelectUser}
                            disabled={isInviteDisabled}
                            value={userValue}
                            color="primary"
                          />
                        </TableCell>
                        <TableCell colSpan={2} className={classes.tableUser}>
                          <div className={classes.profileMenuContainer}>
                            <AvatarImage name={_get(user, 'name')} photoUrl={_get(user, 'photo_url', '')} />
                            <span className={classes.userName}>
                              <b>{_get(user, 'name')}</b>
                              <br />
                              <Typography variant="body2" color="textSecondary" component="small">
                                {_get(user, 'email')}
                              </Typography>
                            </span>
                          </div>
                        </TableCell>
                      </TableRow>
                    );
                  })
                }
                {
                  nextCursor ?
                    <TableRow>
                      <TableCell colSpan={3} className={classes.textCenter}>
                        <Button
                          color="primary"
                          size="medium"
                          variant="contained"
                          disabled={disableShowMore}
                          onClick={handleShowMore}
                        >
                          Ver mais usurios
                        </Button>
                      </TableCell>
                    </TableRow>
                    :
                    null
                }
              </TableBody>
              :
              null
          }
        </Table>
      </div>
    </section>
  );
});

SlackUserList.propTypes = {
  classes: PropTypes.object,
  users: PropTypes.array,
  checkedAll: PropTypes.bool,
  setCheckedAll: PropTypes.func,
  disableShowMore: PropTypes.bool,
  setDisableShowMore: PropTypes.func,
  onRevoke: PropTypes.func,
  onShowMore: PropTypes.func,
  companyName: PropTypes.string,
  locations: PropTypes.array,
  setSelectedLocations: PropTypes.func,
  selectedLocations: PropTypes.array,
  setSelectedRole: PropTypes.func,
  selectedRole: PropTypes.string,
  setSelectedUsers: PropTypes.func,
  selectedUsers: PropTypes.array,
  nextCursor: PropTypes.string,
  isRevokeDisabled: PropTypes.bool,
  isInviteDisabled: PropTypes.bool,
  onInvite: PropTypes.func,
  canDisconnect: PropTypes.bool
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('checkedAll', 'setCheckedAll', true),
  withState('selectedLocations', 'setSelectedLocations', []),
  withState('selectedUsers', 'setSelectedUsers', []),
  withState('selectedRole', 'setSelectedRole', ''),
  withState('disableShowMore', 'setDisableShowMore', false),
  lifecycle({
    componentDidMount() {
      const { users, setSelectedUsers } = this.props;
      setSelectedUsers(users.map(u => JSON.stringify(u)));
    }
  }),
  withPropsOnChange(['users'], ({
    users, setDisableShowMore, setCheckedAll, setSelectedUsers
  }) => {
    setDisableShowMore(false);
    setCheckedAll(true);
    setSelectedUsers(users.map(u => JSON.stringify(u)));
  })
)(SlackUserList);
