import React, { memo } from 'react';
import { compose } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import { Box, Paper } from '@material-ui/core';
import PropTypes from 'prop-types';

import styles from '../../styles';
import { MEMBER_ROLE } from '../../../enums/user-roles';
import withWrapper from '../with-wrapper';
import withViewerData from '../../../utils/with-viewer-data';
import NewTicketsSwitch from './NewTicketsSwitch';
import TicketUpdatessSwitch from './TicketUpdatessSwitch';

const Notifications = ({
  classes, setAlert, viewer
}) => {
  return (
    <Box mt={5}>
      <Paper className={classes.paper}>
        {
          viewer.role !== MEMBER_ROLE && (
            <NewTicketsSwitch setAlert={setAlert} />
          )
        }
        <TicketUpdatessSwitch setAlert={setAlert} />
      </Paper>
    </Box>
  );
};
Notifications.propTypes = {
  classes: PropTypes.object,
  setAlert: PropTypes.func,
  viewer: PropTypes.object
};

export default compose(
  memo,
  withWrapper,
  withStyles(styles, { withTheme: true }),
  withViewerData
)(Notifications);
