/* global window */
import React, { memo } from 'react';
import { compose, withState } from 'recompose';
import PropTypes from 'prop-types';
import { map } from 'lodash';
import { TableCell, TableRow, Button, IconButton } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import DeleteIcon from '@material-ui/icons/Delete';
import AvatarImage from '../../../components/AvatarImage';
import withViewerData from '../../../utils/with-viewer-data';
import Tracker from '../../../utils/tracker';
import TicketCategoryFormModal from '../TicketCategoryFormModal';
import TicketsCategoriesService from '../../../services/ticket-categories';
import styles from './styles';

const EditButtonBase = ({
  ticketCategory, refetch,
  openModal, setOpenModal, setAlert
}) => {
  const onClick = () => {
    Tracker.hit('TicketCategoryModal - Opened');
    setOpenModal(true);
  };
  const onClose = async () => {
    Tracker.hit('TicketCategoryModal - Closed');
    setOpenModal(false);
  };
  return (
    <React.Fragment>
      <Button
        variant="outlined"
        size="medium"
        onClick={onClick}
      >
        Editar
      </Button>
      <TicketCategoryFormModal
        ticketCategory={ticketCategory}
        onClose={onClose}
        setAlert={setAlert}
        refetch={refetch}
        openModal={openModal}
      />
    </React.Fragment>
  );
};
EditButtonBase.propTypes = {
  ticketCategory: PropTypes.object,
  openModal: PropTypes.bool,
  setOpenModal: PropTypes.func,
  setAlert: PropTypes.func,
  refetch: PropTypes.func
};

const EditButton = compose(
  withState('openModal', 'setOpenModal', false)
)(EditButtonBase);

const TicketCategoriesItemRow = memo(({
  viewer, index, classes, setAlert, ticketCategory, loading, setLoading,
  refetch, isManager
}) => {
  const onRemove = async () => {
    if (window.confirm('Deseja realmente excluir essa categoria?')) {
      setLoading(true);
      try {
        await TicketsCategoriesService.toggleCategory(ticketCategory.id);
        setAlert({ alertMsg: 'A categoria foi excluda', alertType: 'success', showAlert: true });
      } catch {
        setAlert({ alertMsg: 'Erro ao excluir categoria', alertType: 'error', showAlert: true });
      }
      setLoading(false);
      refetch();
    }
  };
  const { deadlineInDays } = ticketCategory;
  if (!viewer) return null;
  return (
    <TableRow className={index % 2 ? classes.bgWhite : classes.bgGray}>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{ticketCategory.title}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        {
          map(ticketCategory.assignees, assignee => (
            <div key={assignee.id} className={classes.avatarNameContainer}>
              <AvatarImage
                name={assignee.fullname}
                photoUrl={assignee.photoUrl}
              />
              <span className={`${classes.cellText} ${classes.userName}`}>
                {assignee.fullname}
              </span>
            </div>
          ))
        }
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>
          {
            deadlineInDays
              ? `${deadlineInDays} ${deadlineInDays > 1 ? 'dias teis' : 'dia til'}`
              : ''
          }
        </span>
      </TableCell>
      {
        isManager &&
          <TableCell className={classes.tableSmallCell}>
            <EditButton
              ticketCategory={ticketCategory}
              setAlert={setAlert}
              refetch={refetch}
            />
            &nbsp;&nbsp;&nbsp;
            <IconButton disabled={loading} onClick={onRemove}>
              <DeleteIcon />
            </IconButton>
          </TableCell>
      }
    </TableRow>
  );
});

TicketCategoriesItemRow.propTypes = {
  index: PropTypes.number,
  ticketCategory: PropTypes.object,
  viewer: PropTypes.object,
  classes: PropTypes.object,
  setAlert: PropTypes.func,
  refetch: PropTypes.func,
  isManager: PropTypes.bool,
  setLoading: PropTypes.func,
  loading: PropTypes.bool
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('loading', 'setLoading', false),
  withViewerData
)(TicketCategoriesItemRow);
