/* global window */
import React from 'react';
import { compose } from 'recompose';
import moment from 'moment';
import { withStyles } from '@material-ui/core/styles';
import { Typography, Grid, Paper, Button } from '@material-ui/core';
import PropTypes from 'prop-types';
import { get, map } from 'lodash';
import { USER_MANAGER_ROLES } from '../../enums/user-roles';
import ServiceRequestEnums from '../../enums/service-request';
import withNotAuthorizedHandler from '../../utils/with-not-authorized-handler';
import { withMainLayout } from '../../components/MainLayout';
import Loading from '../../components/Loading';
import withRequest from '../../utils/with-request';
import Screen from '../../utils/screen';
import ServiceRequestsService from '../../services/service-request';
import styles from './styles';

const { STATUS_CANCELED } = ServiceRequestEnums;
const MyRequestDetails = ({
  classes, serviceRequestData, serviceRequestLoading, deleteServiceRequestFetch,
  deleteServiceRequestLoading
}) => {
  const isMobile = Screen.isMobile();
  const serviceRequest = get(serviceRequestData, 'service_request', '');
  const serviceData = get(serviceRequest, 'service_data', '');
  const serviceRequestStatus = get(serviceRequest, 'status', '');
  const isCanceled = serviceRequestStatus === STATUS_CANCELED;
  const onCancelServiceRequest = () => {
    if (window.confirm('Deseja excluir o pedido?')) {
      deleteServiceRequestFetch(true);
    }
  };
  return (
    <React.Fragment>
      { serviceRequestLoading ?
        <Loading />
        :
        <React.Fragment>
          { serviceData ?
            <React.Fragment>
              <Grid container>
                <Grid item xs={12} md={8}>
                  <Typography variant="h5">
                    <b>{serviceData.title}</b>
                  </Typography>
                </Grid>
                <Grid item xs={12} md={4}>
                  {!isCanceled &&
                    <Button
                      className={isMobile ? classes.mobileButton : ''}
                      variant="outlined"
                      color="secondary"
                      size="large"
                      onClick={onCancelServiceRequest}
                      disabled={deleteServiceRequestLoading}
                    >
                      Cancelar
                    </Button>}
                </Grid>
              </Grid>
              <Grid container>
                <Grid item xs={12} md={5}>
                  <p className={classes.serviceSubtitle}><b>{serviceData.subtitle}</b></p>
                  <p className={classes.serviceDescription}>{serviceData.description}</p>
                </Grid>
                <Grid item xs={12} md={3} />
                <Grid item xs={12} md={9}>
                  <Paper className={classes.paperContainer}>
                    <span className={classes.paperDateInfo}>
                      {`Enviado em ${moment(serviceRequest.created_at).format('DD/MM/YYYY')}`}
                    </span>
                    {map(get(serviceRequest, 'details', '').match(/(.+)\n.+/g), item => {
                      const [question, response] = item.split('\n');
                      return (
                        <div key={question}>
                          <p className={classes.paperTitle}>
                            <b>{ question }</b>
                          </p>
                          <p className={classes.paperSubtitle}>
                            {response}
                          </p>
                        </div>
                      );
                    })}
                  </Paper>
                </Grid>
              </Grid>
            </React.Fragment>
            :
            ''}
        </React.Fragment>}
    </React.Fragment>
  );
};

const withServiceRequest = withRequest(ServiceRequestsService.getServiceRequest, {
  name: 'serviceRequest',
  options: ({ match, setAlert }) => ({
    variables: (get(match, 'params.id')),
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao carregar solicitao',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

const withDeleteServiceRequest = withRequest(ServiceRequestsService.deleteServiceRequest, {
  name: 'deleteServiceRequest',
  init: false,
  options: ({ match, setAlert, serviceRequestFetch }) => ({
    variables: (get(match, 'params.id')),
    onRequestFinished: () => {
      setAlert({
        alertMsg: 'Solicitao cancelada com sucesso',
        alertType: 'success',
        showAlert: true
      });
      serviceRequestFetch(true);
    },
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao cancelar solicitao',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

MyRequestDetails.propTypes = {
  classes: PropTypes.object,
  serviceRequestData: PropTypes.object,
  serviceRequestLoading: PropTypes.bool,
  deleteServiceRequestFetch: PropTypes.func,
  deleteServiceRequestLoading: PropTypes.bool
};

export default compose(
  withMainLayout,
  withServiceRequest,
  withDeleteServiceRequest,
  withNotAuthorizedHandler({
    condition: (viewer) => !USER_MANAGER_ROLES.includes(viewer.role)
  }),
  withStyles(styles, { withTheme: true })
)(MyRequestDetails);
