import React, { memo } from 'react';
import { Grid } from '@material-ui/core';
import PropTypes from 'prop-types';
import {
  FormTextField, FormZipCodeField, FormSelectField
} from '../../../../../components/FormFields';
import STATES from '../../../../../enums/states';
import Loading from '../../../../../components/Loading';

const LocationForm = memo(({
  formData, setFormData, isLoading
}) => {
  if (isLoading) return <Loading />;
  const { validationErrors } = formData;
  const errors = validationErrors || {};
  return (
    <Grid container direction="row" spacing={1}>
      <Grid item xs={12} sm={6}>
        <FormTextField
          errors={errors.name}
          label="Apelido"
          placeholder="Endereo principal, Nome da sede, etc"
          disabled={isLoading}
          value={formData.name}
          onChange={(e) => setFormData({ name: e.target.value })}
        />
      </Grid>
      <Grid item xs={12} sm={6}>
        <FormTextField
          errors={errors.address}
          label="Endereo completo"
          placeholder="Rua Monteiro Lobato, 555"
          disabled={isLoading}
          value={formData.address}
          onChange={(e) => setFormData({ address: e.target.value })}
        />
      </Grid>
      <Grid item xs={12} sm={6}>
        <FormTextField
          errors={errors.complement}
          label="Complemento"
          disabled={isLoading}
          value={formData.complement}
          onChange={(e) => setFormData({ complement: e.target.value })}
        />
      </Grid>
      <Grid item xs={12} sm={6}>
        <FormTextField
          errors={errors.neighborhood}
          label="Bairro"
          disabled={isLoading}
          value={formData.neighborhood}
          onChange={(e) => setFormData({ neighborhood: e.target.value })}
        />
      </Grid>
      <Grid item xs={12} sm={6}>
        <FormTextField
          errors={errors.city}
          label="Cidade"
          disabled={isLoading}
          value={formData.city}
          onChange={(e) => setFormData({ city: e.target.value })}
        />
      </Grid>
      <Grid item xs={6} sm={3}>
        <FormSelectField
          errors={errors.state}
          label="Estado"
          disabled={isLoading}
          value={formData.state}
          onChange={(e) => setFormData({ state: e.target.value })}
          options={STATES}
          emptyOptionLabel="Estado"
        />
      </Grid>
      <Grid item xs={6} sm={3}>
        <FormZipCodeField
          errors={errors.zip_code}
          label="CEP"
          disabled={isLoading}
          value={formData.zip_code}
          onChangeText={(zip_code) => setFormData({ zip_code })}
        />
      </Grid>
    </Grid>
  );
});

LocationForm.propTypes = {
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  isLoading: PropTypes.bool
};

export default LocationForm;
