import React, { memo } from 'react';
import { compose, withState } from 'recompose';
import { IconButton, Menu, MenuItem } from '@material-ui/core';
import { MoreVert as MoreVertIcon } from '@material-ui/icons';
import { withStyles } from '@material-ui/core/styles';
import { withRouter } from 'react-router';
import PropTypes from 'prop-types';
import GetAppIcon from '@material-ui/icons/GetApp';
import AssignmentIcon from '@material-ui/icons/Assignment';
import ChromeReaderModeIcon from '@material-ui/icons/ChromeReaderMode';
import DashboardIcon from '@material-ui/icons/Dashboard';
import ArchiveIcon from '@material-ui/icons/Archive';

import Tracker from '../../../utils/tracker';
import ExportTicketsCsvModal from './ExportTicketsCsvModal';
import PublicNewTicketLinkModal from './PublicNewTicketLinkModal';
import styles from '../styles';

const ITEM_HEIGHT = 48;

const TicketsMenuButtonBase = ({
  history, setAlert, anchorEl, setAnchorEl,
  isExportModalOpen, setIsExportModalOpen,
  isPublicNewTicketsOpen, setIsPublicNewTicketsOpen,
  isArchived, onToggleArchived, classes
}) => {
  const open = Boolean(anchorEl);
  const handleOpenMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleCloseMenu = () => {
    setAnchorEl(null);
  };
  const onExportTicketsClick = () => {
    Tracker.hit('ExportTicketsCsvModal - Opened');
    setIsExportModalOpen(true);
    handleCloseMenu();
  };
  const onExportTicketsClose = () => {
    Tracker.hit('ExportTicketsCsvModal - Closed');
    setIsExportModalOpen(false);
  };
  const onToggleArchivedClick = () => {
    const archivedState = !isArchived;
    Tracker.hit(`Tickets${archivedState ? 'Archived' : ''} - Viewer`);
    handleCloseMenu();
    onToggleArchived(archivedState);
  };
  const onReportClick = () => {
    history.push('/tickets/relatorio');
  };
  const onPublicNewTicketsClick = () => {
    Tracker.hit('PublicNewTicketsModal - Opened');
    handleCloseMenu();
    setIsPublicNewTicketsOpen(true);
  };
  const onPublicNewTicketsClose = () => {
    Tracker.hit('PublicNewTicketsModal - Closed');
    setIsPublicNewTicketsOpen(false);
  };
  return (
    <div>
      <IconButton
        aria-label="more"
        aria-controls="long-menu"
        aria-haspopup="true"
        onClick={handleOpenMenu}
      >
        <MoreVertIcon />
      </IconButton>
      <Menu
        id="long-menu"
        anchorEl={anchorEl}
        keepMounted
        open={open}
        onClose={handleCloseMenu}
        PaperProps={{ style: { maxHeight: ITEM_HEIGHT * 4.5 } }}
      >
        <MenuItem onClick={onToggleArchivedClick}>
          {isArchived ?
            <DashboardIcon className={classes.iconMenu} />
            :
            <ArchiveIcon className={classes.iconMenu} />}
          {isArchived ? 'Ver no arquivados' : 'Ver arquivados'}
        </MenuItem>
        <MenuItem onClick={onPublicNewTicketsClick}>
          <ChromeReaderModeIcon className={classes.iconMenu} />
          Formlrio pblico
        </MenuItem>
        <MenuItem onClick={onReportClick}>
          <AssignmentIcon className={classes.iconMenu} />
          Relatrios
        </MenuItem>
        <MenuItem onClick={onExportTicketsClick}>
          <GetAppIcon className={classes.iconMenu} />
          Exportar tickets
        </MenuItem>
      </Menu>
      <PublicNewTicketLinkModal
        setAlert={setAlert}
        isModalOpen={isPublicNewTicketsOpen}
        onClose={onPublicNewTicketsClose}
      />
      <ExportTicketsCsvModal
        setAlert={setAlert}
        isModalOpen={isExportModalOpen}
        onClose={onExportTicketsClose}
      />
    </div>
  );
};
TicketsMenuButtonBase.propTypes = {
  history: PropTypes.object,
  setAlert: PropTypes.func,
  anchorEl: PropTypes.object,
  setAnchorEl: PropTypes.func,
  classes: PropTypes.object,
  isArchived: PropTypes.bool,
  onToggleArchived: PropTypes.func,
  isExportModalOpen: PropTypes.bool,
  setIsExportModalOpen: PropTypes.func,
  isPublicNewTicketsOpen: PropTypes.bool,
  setIsPublicNewTicketsOpen: PropTypes.func
};

const TicketsMenuButton = compose(
  memo,
  withStyles(styles, { withTheme: true }),
  withRouter,
  withState('anchorEl', 'setAnchorEl', null),
  withState('isExportModalOpen', 'setIsExportModalOpen', false),
  withState('isPublicNewTicketsOpen', 'setIsPublicNewTicketsOpen', false)
)(TicketsMenuButtonBase);

export default TicketsMenuButton;
