import React, { memo } from 'react';
import { compose } from 'recompose';
import { withRouter } from 'react-router';
import { withStyles } from '@material-ui/core/styles';
import { Grid, Typography, Paper, Button } from '@material-ui/core';
import moment from 'moment';
import { get } from 'lodash';
import PropTypes from 'prop-types';

import withViewerData from '../../../../utils/with-viewer-data';
import Formatter from '../../../../utils/formatter';
import CancelPlanButton from './CancelPlanButton';
import UndoCancelationButton from './UndoCancelationButton';
import styles from './styles';

const CurrentSubscriptionBlock = ({
  classes, setAlert, viewer, subscription, refetch, history
}) => {
  if (!viewer || !subscription) return null;

  const currentPlan = get(subscription, 'plan', {});
  const currentPaidUsers = get(viewer, 'company.total_paid_users', 0);
  return (
    <Paper className={classes.tablePaper}>
      <Grid container justify="center" alignItems="flex-start">
        <Grid item xs={12} sm align="center">
          <Typography variant="h6" className={classes.boxItemText}>
            Administradores
          </Typography>
          <Typography variant="h5" className={classes.boxItemText}>
            <b>{`${currentPaidUsers}/${currentPlan.seats}`}</b>
          </Typography>
          <Button
            variant="outlined"
            className={classes.boxItemText}
            onClick={() => history.push('/configuracoes/usuarios')}
          >
            GERENCIAR USURIOS
          </Button>
        </Grid>
        <Grid item xs={12} sm align="center" className={classes.centerBox}>
          <Typography variant="h6" className={classes.boxItemText}>
            Valor da assinatura
          </Typography>
          <div className={classes.boxItemText}>
            <Typography variant="h5">
              <b>{Formatter.toCurrency(subscription.currentPrice)}</b>
            </Typography>
            <small>
              {
                subscription.willCancelAt ?
                  `Termina em: ${moment(subscription.willCancelAt).format('DD/MM/YYYY')}`
                  :
                  `Prxima cobrana: ${moment(subscription.periodEnd).format('DD/MM/YYYY')}`
              }
            </small>
          </div>
          {
            !subscription.willCancelAt && (
              <Button
                variant="outlined"
                className={classes.updateSeats}
                onClick={() => history.push('/assinar-plano')}
              >
                ADICIONAR LUGARES
              </Button>
            )
          }
        </Grid>
        <Grid item xs={12} sm align="center">
          <Typography variant="h6" className={classes.boxItemText}>
            Plano atual
          </Typography>
          <Typography variant="h5" className={classes.boxItemText}>
            <b>{currentPlan.name}</b>
          </Typography>
          {
            subscription.willCancelAt ? (
              <UndoCancelationButton
                setAlert={setAlert}
                className={classes.boxItemText}
                subscription={subscription}
                onSuccess={refetch}
              />
            ) : (
              <CancelPlanButton
                setAlert={setAlert}
                className={classes.boxItemText}
                subscription={subscription}
                onCancelSuccess={refetch}
              />
            )
          }
        </Grid>
      </Grid>
    </Paper>
  );
};

CurrentSubscriptionBlock.propTypes = {
  classes: PropTypes.object,
  setAlert: PropTypes.func,
  viewer: PropTypes.object,
  subscription: PropTypes.object,
  refetch: PropTypes.func,
  history: PropTypes.object
};

export default compose(
  memo,
  withStyles(styles, { withTheme: true }),
  withViewerData,
  withRouter
)(CurrentSubscriptionBlock);
