import React, { memo } from 'react';
import { get } from 'lodash';
import { compose, withHandlers } from 'recompose';
import { Button } from '@material-ui/core';
import PropTypes from 'prop-types';

import withRequest from '../../../../../utils/with-request';
import SubscriptionsService from '../../../../../services/subscriptions';

const CancelPlanButton = ({ className, onClick }) => (
  <Button className={className} onClick={onClick}>
    CANCELAR PLANO
  </Button>
);

CancelPlanButton.propTypes = {
  className: PropTypes.any,
  onClick: PropTypes.func
};

const withCancel = withRequest(SubscriptionsService.cancel, {
  name: 'cancel',
  init: false,
  options: ({ setAlert, subscription, onCancelSuccess }) => ({
    variables: subscription.id,
    onRequestFinished: () => {
      setAlert({
        alertMsg: 'Cancelamento de assinatura foi agendado com sucesso',
        alertType: 'success',
        showAlert: true
      });
      if (onCancelSuccess) onCancelSuccess();
    },
    onRequestError: error => {
      setAlert({
        alertMsg: get(error, 'error', 'No foi possvel cancelar sua assinatura.'),
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  memo,
  withCancel,
  withHandlers({
    onClick: ({ cancelFetch }) => () => {
      /* global window */
      if (window.confirm('Deseja mesmo cancelar sua assinatura?')) cancelFetch(true);
    }
  })
)(CancelPlanButton);
