import React, { memo } from 'react';
import { isEmpty as _isEmpty, get as _get } from 'lodash';
import { compose, withState } from 'recompose';
import { TableRow, TableCell, Switch } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';

import withRequest from '../../../../utils/with-request';
import LocationService from '../../../../services/locations';
import EditLocationButton from '../EditLocationButton';
import styles from './styles';

const LocationsItemRow = memo(({
  classes, setAlert, refetch, index, location,
  toggleActiveFetch, toggleActiveLoading, isActive, setIsActive
}) => {
  const { name, complement, address, city, state } = location || {};
  const addressLabel = complement ? `${address}, ${complement}` : address;
  const cityLabel = city && state ? `${city}/${state}` : '';
  return (
    <TableRow className={index % 2 ? classes.bgWhite : classes.bgGray}>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{name}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{addressLabel}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{cityLabel}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <Switch
          color="primary"
          disabled={toggleActiveLoading}
          checked={isActive}
          onChange={() => {
            setIsActive(!isActive);
            toggleActiveFetch(true);
          }}
        />
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <EditLocationButton
          setAlert={setAlert}
          onUpdated={refetch}
          location={location}
        />
      </TableCell>
    </TableRow>
  );
});

LocationsItemRow.propTypes = {
  classes: PropTypes.object,
  setAlert: PropTypes.func,
  index: PropTypes.number,
  refetch: PropTypes.func,
  location: PropTypes.object,
  toggleActiveFetch: PropTypes.func,
  toggleActiveLoading: PropTypes.bool,
  isActive: PropTypes.bool,
  setIsActive: PropTypes.func
};

const withToggleActive = withRequest(LocationService.toggleActive, {
  name: 'toggleActive',
  init: false,
  options: ({ location, refetch, setAlert, isActive, setIsActive }) => {
    return {
      variables: location.id,
      onRequestFinished: () => {
        refetch();
      },
      onRequestError: (e) => {
        setAlert({
          alertMsg: _get(e, 'error', 'Erro ao desabilitar este local')
        });
        setIsActive(!isActive);
      }
    };
  }
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('isActive', 'setIsActive', ({ location }) => _isEmpty(location.deleted_at)),
  withToggleActive
)(LocationsItemRow);
