import { mapValues } from 'lodash';

import Api from '../utils/api';

const ServiceRequest = {
  createServiceRequest({ serviceId, dynamicQuestions: dynamicQuestionsProp }) {
    const dynamicQuestions = mapValues(dynamicQuestionsProp, item => {
      if (item.originalFileObj) {
        return {
          id: item.id,
          storage: item.storage,
          metadata: item.metadata,
          base64Data: item.base64Data
        };
      }

      return item;
    });
    return Api.post('/service_requests', {
      service_id: serviceId,
      dynamic_questions: dynamicQuestions
    });
  },
  getServiceRequests({ status }) {
    return Api.get('/service_requests', { status });
  },
  getServiceRequest(id) {
    return Api.get(`/service_requests/${id}`);
  },
  deleteServiceRequest(id) {
    return Api.put(`/service_requests/${id}`);
  }
};

export default ServiceRequest;
