/* global window */
import React, { memo } from 'react';
import { get as _get } from 'lodash';
import {
  withStyles, Button, LinearProgress
} from '@material-ui/core';
import { AttachFile as AttachFileIcon, HighlightOff as HighlightOffIcon } from '@material-ui/icons';
import PropTypes from 'prop-types';

import styles from './styles';

const FilePreviewButton = memo(({
  file, onRemoveFile, outterContainerClassName, disabled,
  classes
}) => {
  const isStillLoading = _get(file, 'progressPercent') < 100;
  return (
    <div className={`${classes.outterContainer} ${outterContainerClassName}`}>
      <Button
        variant="outlined"
        size="large"
        onClick={() => file.url && window.open(file.url, '_blank')}
        disabled={disabled || isStillLoading}
      >
        <div>
          <div className={classes.innerButtonContainer}>
            <AttachFileIcon />
            <div>
              <span className={classes.filename}>{_get(file, 'metadata.filename')}</span>
              <br />
              <small className={classes.sizeText}>
                { Math.round(_get(file, 'metadata.size') / 1000) }
                KB
              </small>
            </div>
          </div>
          {
            (isStillLoading) && (
              <LinearProgress
                className={classes.progressBar}
                variant="determinate"
                value={file.progressPercent}
              />
            )
          }
        </div>
      </Button>
      {
        (!disabled && onRemoveFile) && (
          <HighlightOffIcon
            onClick={() => onRemoveFile(file)}
            className={classes.cancelButton}
          />
        )
      }
    </div>
  );
});

FilePreviewButton.propTypes = {
  file: PropTypes.object,
  onRemoveFile: PropTypes.func,
  outterContainerClassName: PropTypes.string,
  disabled: PropTypes.bool,
  classes: PropTypes.object
};

export default withStyles(styles)(FilePreviewButton);
