import React, { memo } from 'react';
import { compose, withHandlers } from 'recompose';
import { Typography, Switch } from '@material-ui/core';
import PropTypes from 'prop-types';

import NotificationSettingsService from '../../../services/notification-settings';
import withRequest from '../../../utils/with-request';
import withViewerData from '../../../utils/with-viewer-data';
import Session from '../../../utils/session';

const TicketUpdatesSwitch = ({
  viewer, loading, onSubmit
}) => (
  <div>
    <Switch
      color="primary"
      disabled={loading}
      checked={viewer.notify_ticket_updates}
      onChange={onSubmit}
    />
    <Typography variant="body1" component="span">
      Receber notificaes de atualizaes de tickets que fao parte
    </Typography>
  </div>
);
TicketUpdatesSwitch.propTypes = {
  viewer: PropTypes.object,
  loading: PropTypes.bool,
  onSubmit: PropTypes.func
};

const withSubmit = withRequest(NotificationSettingsService.toggleTicketUpdates, {
  init: false,
  props: ({ data }) => data || {},
  options: ({ setAlert, viewer }) => ({
    onRequestFinished: () => {
      Session.setUserData({ ...viewer, notify_ticket_updates: !viewer.notify_ticket_updates });
    },
    onRequestError: (err) => {
      const alertMsg = err.errors
        ? Object.values(err.errors).flat().join()
        : 'Erro ao comunicar com servidor';
      setAlert({
        alertMsg,
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  memo,
  withViewerData,
  withSubmit,
  withHandlers({
    onSubmit: ({ fetch }) => () => fetch(true)
  })
)(TicketUpdatesSwitch);
