import React, { memo } from 'react';
import { get, map, zipObject } from 'lodash';
import { compose } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';
import { Typography, Grid, Paper, Button } from '@material-ui/core';
import { withRouter } from 'react-router';
import PropTypes from 'prop-types';

import ServiceService from '../../../services/service';
import ServiceRequestService from '../../../services/service-request';
import withRequest from '../../../utils/with-request';
import withFormData from '../../../utils/with-form-data';
import uploadFile from '../../../utils/upload-files';
import { CHECKBOX, ATTACHMENT } from '../../../enums/fields';
import Loading from '../../../components/Loading';
import { FormDynamicField } from '../../../components/FormFields';
import styles from './styles';

const NewServiceRequest = ({
  classes,
  serviceData,
  formData, setFormData,
  createServiceRequestFetch, createServiceRequestLoading: isSubmitLoading
}) => {
  const { validationErrors } = formData;
  const errors = validationErrors || {};
  const onSubmit = () => createServiceRequestFetch(true);

  const handleEnterOnKeyDown = (e) => {
    if (e.ctrlKey && e.keyCode === 13) {
      onSubmit();
    }
  };

  const { service } = serviceData;
  return (
    <React.Fragment>
      <Typography className={classes.title} variant="h4">
        { service.title }
      </Typography>
      <Grid container className={classes.subtitlesContainer}>
        <Grid item xs={6}>
          <Typography variant="subtitle1">
            <b>{ service.subtitle }</b>
          </Typography>
        </Grid>
      </Grid>
      <Grid container className={classes.textContentsContainer}>
        <Grid item xs={6}>
          <Typography variant="body2">
            {service.description}
          </Typography>
        </Grid>
        <Grid item xs={6} className={classes.marginLeftContainer}>
          <ul className={classes.benefitsList}>
            {
              map(service.benefits, benefit => (
                <li key={benefit}>
                  <Typography variant="body2">{benefit}</Typography>
                </li>
              ))
            }
          </ul>
        </Grid>
      </Grid>
      <Paper className={`${classes.container} ${classes.paper}`}>
        <Grid container item xs={12} sm={8} md={6} spacing={2}>
          <Grid item xs={12}>
            {
              map(service.form_questions, q => (
                <div key={q.id} className={classes.questionContainer}>
                  <Typography variant="subtitle1">
                    <b>{q.question}</b>
                  </Typography>
                  <FormDynamicField
                    fieldType={q.field_type}
                    onKeyDown={handleEnterOnKeyDown}
                    value={formData[q.question]}
                    onChangeValue={value => setFormData({ [q.question]: value })}
                    errors={errors[`dynamic_questions.${q.question}`]}
                    options={map(q.options, o => ({ label: o, value: o }))}
                    emptyOptionLabel=" "
                    uploadFileFunc={uploadFile}
                    required={q.required}
                    disabled={isSubmitLoading}
                  />
                </div>
              ))
            }
            <div className={classes.actions}>
              <Button
                className={classes.goBackButton}
                component={Link}
                to="/servicos"
                color="default"
                size="large"
                disabled={isSubmitLoading}
              >
                Voltar
              </Button>
              <Button
                variant="contained"
                color="primary"
                size="large"
                onClick={onSubmit}
                disabled={isSubmitLoading}
              >
                {isSubmitLoading ? 'Aguarde...' : 'Enviar'}
              </Button>
            </div>
          </Grid>
        </Grid>
      </Paper>
    </React.Fragment>
  );
};
NewServiceRequest.propTypes = {
  classes: PropTypes.object,
  serviceData: PropTypes.object,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  createServiceRequestFetch: PropTypes.func,
  createServiceRequestLoading: PropTypes.bool
};

const withService = withRequest(ServiceService.getService, {
  name: 'service',
  options: ({ match }) => ({ variables: match.params.serviceId })
});

const withCreateServiceRequest = withRequest(ServiceRequestService.createServiceRequest, {
  name: 'createServiceRequest',
  init: false,
  options: ({ serviceData, formData, resetFormData, setFormData, setAlert }) => {
    const { changedFields } = formData;
    return {
      variables: { serviceId: get(serviceData, 'service.id'), dynamicQuestions: changedFields },
      onRequestFinished: () => {
        setAlert({
          alertMsg: 'Recebemos! Em breve nosso especialista vai entrar em contato com voc!',
          alertType: 'success',
          showAlert: true
        });
        resetFormData();
      },
      onRequestError: error => {
        setAlert({
          alertMsg: 'Erro ao solicitar servio', alertType: 'error', showAlert: true
        });
        setFormData({ validationErrors: get(error, 'errors', {}) });
      }
    };
  }
});

/* eslint-disable react/prop-types */
const withLoading = (WrappedComponent => props => (
  !props.serviceLoading ? <WrappedComponent {...props} /> : <Loading />
));
/* eslint-enable react/prop-types */

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter,
  withService,
  withLoading,
  memo,
  withFormData((props) => (
    zipObject(
      map(get(props, 'serviceData.service.form_questions'), q => (
        q.question
      )),
      map(get(props, 'serviceData.service.form_questions'), q => {
        if (q.field_type === ATTACHMENT) return {};
        if (q.field_type === CHECKBOX) return [];
        return '';
      })
    )
  )),
  withCreateServiceRequest
)(NewServiceRequest);
