import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import { Button } from '@material-ui/core';
import {
  Add as AddIcon, Remove as RemoveIcon
} from '@material-ui/icons';
import PropTypes from 'prop-types';

import styles from './styles';

const FormNumberWithButtonsField = ({
  value, onChangeText, disabled, min, max,
  classes
}) => (
  <React.Fragment>
    <Button
      className={classes.button}
      size="large"
      variant="contained"
      onClick={() => {
        const newVal = Number(value) - 1;
        if (min && newVal < min) return;
        onChangeText(newVal);
      }}
      disabled={disabled}
    >
      <RemoveIcon />
    </Button>
    <b className={classes.valueText}>
      { value }
    </b>
    <Button
      className={classes.button}
      size="large"
      variant="contained"
      onClick={() => {
        const newVal = Number(value) + 1;
        if (max && newVal > max) return;
        onChangeText(newVal);
      }}
      disabled={disabled}
    >
      <AddIcon />
    </Button>
  </React.Fragment>
);
FormNumberWithButtonsField.propTypes = {
  value: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  onChangeText: PropTypes.func,
  disabled: PropTypes.bool,
  min: PropTypes.number,
  max: PropTypes.number,
  classes: PropTypes.object
};

export default withStyles(styles, { withTheme: true })(FormNumberWithButtonsField);
