import React, { memo } from 'react';
import { get as _get, size as _size } from 'lodash';
import { compose, withState } from 'recompose';
import PropTypes from 'prop-types';
import {
  Table, TableBody, TableCell, TableHead, TableRow,
  Paper, Button, Typography, FormGroup, FormControlLabel, Checkbox
} from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import Session from '../../../utils/session';
import withRequest from '../../../utils/with-request';
import MembersService from '../../../services/members';
import { USER_MANAGER_ROLES } from '../../../enums/user-roles';
import withNotAuthorizedHandler from '../../../utils/with-not-authorized-handler';
import Loading from '../../../components/Loading';
import NewMemberModal from '../NewMemberModal';
import MembersItemRow from '../MembersItemRow';
import Tracker from '../../../utils/tracker';
import styles from './styles';

const MembersTable = memo(({
  membersData, membersLoading: loading, openCreateMemberModal,
  setOpenCreateMemberModal, classes, membersFetch,
  setAlert, onlyActive, setOnlyChecked
}) => {
  const loggedMemberId = _get(Session.getUserData(), 'id');
  const members = _get(membersData, 'members');
  const onCreateMember = () => {
    setOpenCreateMemberModal(true);
    Tracker.hit('NewMemberModal - Opened');
  };
  const onClose = () => {
    setOpenCreateMemberModal(false);
  };

  const handleChange = (event) => {
    setOnlyChecked(event.target.checked);
    membersFetch(true);
  };
  return (
    <React.Fragment>
      <div className={classes.container}>
        <div className={classes.headerView}>
          <aside>
            <Typography variant="h6">
              <b>{`Usurios (${_size(members)})`}</b>
            </Typography>
          </aside>
          <aside>
            <div className={classes.headerButtonAndActiveFilter}>
              <FormGroup row>
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={onlyActive}
                      onChange={handleChange}
                      color="primary"
                      disabled={loading}
                    />
                  }
                  label="Usurios ativos"
                />
              </FormGroup>
              <Button
                size="large"
                color="primary"
                variant="contained"
                onClick={onCreateMember}
              >
                CONVIDAR USURIO
              </Button>
            </div>
          </aside>
        </div>
      </div>
      <div className={classes.container}>
        <Paper className={classes.tablePaper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell className={classes.tableCell}>NOME</TableCell>
                <TableCell className={classes.tableCell}>EMAIL</TableCell>
                <TableCell className={classes.tableCell}>PERMISSO DE ACESSO</TableCell>
                <TableCell className={classes.tableCell}>LOCAIS</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {
                loading ?
                  <TableRow>
                    <TableCell colSpan={5} align="center">
                      <Loading />
                    </TableCell>
                  </TableRow>
                  :
                  (
                    _size(members) ?
                      (members.map((member, i) => {
                        return (
                          <MembersItemRow
                            loggedMemberId={loggedMemberId}
                            refetch={membersFetch}
                            setAlert={setAlert}
                            index={i}
                            key={member.id}
                            member={member}
                            loading={loading}
                          />
                        );
                      }))
                      :
                      <TableRow>
                        <TableCell className={classes.empty} colSpan={5} align="center">
                          Nenhum usurio cadastrado
                        </TableCell>
                      </TableRow>
                  )
              }
              {
                openCreateMemberModal &&
                <NewMemberModal onClose={onClose} setAlert={setAlert} refetch={membersFetch} />
              }
            </TableBody>
          </Table>
        </Paper>
      </div>
    </React.Fragment>
  );
});

MembersTable.propTypes = {
  membersLoading: PropTypes.bool,
  membersData: PropTypes.object,
  membersFetch: PropTypes.func,
  classes: PropTypes.object,
  openCreateMemberModal: PropTypes.bool,
  setOpenCreateMemberModal: PropTypes.func,
  setAlert: PropTypes.func,
  onlyActive: PropTypes.bool,
  setOnlyChecked: PropTypes.func
};

const withMembers = withRequest(MembersService.getMembers, {
  name: 'members',
  options: ({ setAlert, onlyActive }) => ({
    variables: { onlyActive },
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao carregar dados do servidor',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('openCreateMemberModal', 'setOpenCreateMemberModal', false),
  withState('onlyActive', 'setOnlyChecked', true),
  withNotAuthorizedHandler({
    condition: (viewer) => !USER_MANAGER_ROLES.includes(viewer.role)
  }),
  withMembers
)(MembersTable);
