import React from 'react';
import { compose } from 'recompose';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import MembersService from '../../../../services/members';
import withRequest from '../../../../utils/with-request';
import Tracker from '../../../../utils/tracker';

const UnblockMemberButton = ({ unblockMemberFetch, unblockMember }) => {
  return (
    <React.Fragment>
      <Button
        variant="outlined"
        size="large"
        onClick={() => unblockMemberFetch(true)}
        disabled={unblockMember}
      >
        Reativar conta
      </Button>
    </React.Fragment>
  );
};

UnblockMemberButton.propTypes = {
  unblockMemberFetch: PropTypes.func,
  unblockMember: PropTypes.bool
};

const withUnblockMember = withRequest(MembersService.unblockMember, {
  name: 'unblockMember',
  init: false,
  options: ({ memberId, setAlert, refetch }) => ({
    variables: memberId,
    onRequestFinished: () => {
      setAlert({
        alertMsg: 'Usurio desbloqueado com sucesso',
        alertType: 'success',
        showAlert: true
      });
      Tracker.hit('UnblockUser', { memberId });
      refetch(false);
    },
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao desbloquear usurio',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  withUnblockMember
)(UnblockMemberButton);
