import React, { memo } from 'react';
import PropTypes from 'prop-types';
import { compose, withState } from 'recompose';
import { withRouter } from 'react-router';
import { Link } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import CircularProgress from '@material-ui/core/CircularProgress';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import Grid from '@material-ui/core/Grid';
import {
  includes as _includes, values as _values,
  get as _get, mapValues as _mapValues,
  first as _first
} from 'lodash';
import styles from './styles';
import config from '../../config';
import AlertBox from '../../components/AlertBox';
import { FormTextField } from '../../components/FormFields';
import TermsOfUse from '../../components/TermsOfUse';
import Session from '../../utils/session';
import Tracker from '../../utils/tracker';
import withRequest from '../../utils/with-request';
import MemberService from '../../services/member';
import AuthService from '../../services/auth';

const Signup = memo(({
  formData, setAlert, alert, errors,
  setFormData, submitFormDataFetch, submitFormDataLoading
}) => {
  const handleForm = (attribute) => setFormData({ ...formData, ...attribute });
  const onSubmitForm = () => submitFormDataFetch(true);
  const {
    memberName, memberLastname, memberEmail,
    memberPhone, companyName, memberPassword
  } = formData;
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  const disabledButton = _includes(_values(formData), '') || submitFormDataLoading;
  return (
    <div style={styles.container}>
      {showAlert && <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />}
      <Grid container spacing={2}>
        <Grid item xs={12} sm={6}>
          <div style={styles.contentLeft}>
            <div style={styles.form}>
              <Typography component="h1" variant="h4">
                Acesse todos servios que precisar para seu ambiente de trabalho
              </Typography>
              <br />
              <br />
              <Typography component="h1" variant="h6" style={styles.checkItem}>
                <CheckBoxIcon style={styles.checkIcon} />
                Atendimento dedicado
              </Typography>
              <Typography component="h1" variant="h6" style={styles.checkItem}>
                <CheckBoxIcon style={styles.checkIcon} />
                Fornecedores qualificados
              </Typography>
              <Typography component="h1" variant="h6" style={styles.checkItem}>
                <CheckBoxIcon style={styles.checkIcon} />
                Ferramentas para facilitar a gesto
              </Typography>
              <br />
              <br />
              <br />
              <Typography component="h1" variant="h6">
                <strong>
                  &ldquo;Me surpreendeu de mais o servio, super solcitos com tudo.&rdquo;
                  - Mariana, BossaBox
                </strong>
              </Typography>
            </div>
          </div>
        </Grid>
        <Grid item xs={12} sm={6}>
          <div style={styles.contentRight}>
            <form onSubmit={onSubmitForm} style={styles.form}>
              <img
                src={require('../../assets/images/logo-header.png')}
                width={175}
                alt={config.appName}
                style={styles.logo}
              />
              <Typography component="h1" variant="h5" style={styles.title}>
                Crie sua conta grtis
              </Typography>
              <Grid container spacing={2}>
                <Grid item xs={12} md={6} style={styles.gridItem}>
                  <FormTextField
                    label="Seu nome*"
                    disabled={submitFormDataLoading}
                    errors={_get(errors, 'member_name')}
                    value={memberName}
                    onChange={e => handleForm({ memberName: e.target.value })}
                  />
                </Grid>
                <Grid item xs={12} md={6} style={styles.gridItem}>
                  <FormTextField
                    label="Sobrenome*"
                    disabled={submitFormDataLoading}
                    errors={_get(errors, 'member_lastname')}
                    value={memberLastname}
                    onChange={e => handleForm({ memberLastname: e.target.value })}
                  />
                </Grid>
                <Grid item xs={12} style={styles.gridItem}>
                  <FormTextField
                    label="Email do trabalho*"
                    disabled={submitFormDataLoading}
                    errors={_get(errors, 'member_email')}
                    value={memberEmail}
                    onChange={e => handleForm({ memberEmail: e.target.value })}
                  />
                </Grid>
                <Grid item xs={12} style={styles.gridItem}>
                  <FormTextField
                    label="Telefone com DDD*"
                    placeholder="Somente nmeros, ex: 11988884444"
                    type="tel"
                    disabled={submitFormDataLoading}
                    errors={_get(errors, 'member_phone')}
                    value={memberPhone}
                    onChange={e => {
                      const phone = (e.target.value || '').replace(/[\D]+/g, '').slice(0, 15);
                      handleForm({ memberPhone: phone });
                    }}
                  />
                </Grid>
                <Grid item xs={12} style={styles.gridItem}>
                  <FormTextField
                    label="Nome da empresa*"
                    disabled={submitFormDataLoading}
                    errors={_get(errors, 'company_name')}
                    value={companyName}
                    onChange={e => handleForm({ companyName: e.target.value })}
                  />
                </Grid>
                <Grid item xs={12} style={styles.gridItem}>
                  <FormTextField
                    label="Definir senha*"
                    placeholder="Informe 6 ou mais dgitos"
                    style={styles.textFieldMarginBottom}
                    errors={_get(errors, 'member_password')}
                    disabled={submitFormDataLoading}
                    value={memberPassword}
                    type="password"
                    onChange={e => handleForm({ memberPassword: e.target.value })}
                  />
                </Grid>
                <Grid item xs={12}>
                  <Button
                    disabled={disabledButton}
                    fullWidth
                    variant="contained"
                    color="primary"
                    size="large"
                    style={styles.button}
                    onClick={onSubmitForm}
                  >
                    {
                      submitFormDataLoading ?
                        <CircularProgress
                          color="primary"
                          variant="indeterminate"
                          disableShrink
                          thickness={5}
                        />
                        :
                        'CRIAR CONTA'
                    }
                  </Button>
                </Grid>
              </Grid>
            </form>
            <span>
              {'J possui conta? '}
              <Link to="/">Fazer login</Link>
            </span>
            <TermsOfUse />
          </div>
        </Grid>
      </Grid>
    </div>
  );
});

Signup.propTypes = {
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  submitFormDataFetch: PropTypes.func,
  submitFormDataLoading: PropTypes.bool,
  errors: PropTypes.object,
  alert: PropTypes.object,
  setAlert: PropTypes.func
};

const initialFormData = {
  memberName: '',
  memberLastname: '',
  memberEmail: '',
  memberPhone: '',
  memberPassword: '',
  companyName: ''
};

const withSubmitFormData = withRequest(AuthService.postSignup, {
  name: 'submitFormData',
  init: false,
  options: ({ formData, setAlert, history, setErrors }) => ({
    variables: { ...formData },
    onRequestFinished: async () => {
      const { memberEmail, memberPassword, memberName, memberLastname } = formData;
      const trackProps = { email: memberEmail, name: memberName, lastname: memberLastname };
      Tracker.hit('Signup - SubmitSuccess', trackProps);
      setAlert({
        alertMsg: 'Conta criada com sucesso, bem-vindo a Lobby!',
        alertType: 'success',
        showAlert: true
      });
      const { token } = await AuthService.postSignin(memberEmail, memberPassword);
      await Session.setUserToken(token);
      const response = await MemberService.getViewer();
      Session.setUserData(_get(response, 'member'));
      setTimeout(() => history.push('/servicos'), 100);
    },
    onRequestError: (e) => {
      const { memberEmail, memberName, memberLastname } = formData;
      const errors = _mapValues(_get(e, 'errors', {}), (err) => _first(err));
      const trackProps = { email: memberEmail, name: memberName, lastname: memberLastname, errors };
      Tracker.hit('Signup - SubmitError', trackProps);
      setErrors(errors);
      setAlert({
        alertMsg: 'Erro ao realizar cadastro',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  memo,
  withRouter,
  withState('formData', 'setFormData', initialFormData),
  withState('errors', 'setErrors', {}),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withSubmitFormData
)(Signup);
