import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import { compose } from 'recompose';
import { withRouter } from 'react-router';
import { withStyles } from '@material-ui/core/styles';
import { Tooltip, IconButton } from '@material-ui/core';
import InfoIcon from '@material-ui/icons/Info';
import AddIcon from '@material-ui/icons/Add';
import Kanban from '../../../components/Kanban';
import TicketCard from '../TicketCard';
import TicketModal from '../TicketModal';
import styles from './styles';
import MenuForDone from '../MenuForDone';

const TicketBoard = memo(({
  tickets, categories, assignees, loading, onClickCard,
  onCloseCardModal, lockBoard, ticketToOpenModal, onDragCard,
  setAlert, doneTickets, ticketsFetch, isManager, history
}) => (
  <React.Fragment>
    <Kanban
      actionPending={
        <IconButton
          aria-label="New Ticket"
          color="primary"
          onClick={() => history.push('/tickets/novo')}
        >
          <AddIcon />
        </IconButton>
      }
      actionDone={
        <React.Fragment>
          <Tooltip
            title="Ateno: Tickets concludos acima de 10 dias sero automaticamente arquivados."
            arrow
            placement="top"
          >
            <InfoIcon color="disabled" />
          </Tooltip>
          {
            isManager &&
              <MenuForDone
                doneTickets={doneTickets}
                setAlert={setAlert}
                ticketsFetch={ticketsFetch}
              />
          }
        </React.Fragment>
      }
      data={tickets || []}
      disableDragDrop={lockBoard}
      loading={loading}
      renderCard={(t) => (
        <TicketCard
          ticket={t}
          loading={loading}
          onClickCard={onClickCard}
          onCloseModal={onCloseCardModal}
        />
      )}
      onDragEnd={({ draggableId, destination }) => {
        const params = {
          variables: {
            id: draggableId,
            status: _get(destination, 'droppableId'),
            position: _get(destination, 'index')
          }
        };
        onDragCard(params);
      }}
      columnStyle={styles.column}
      columnHeaderStyle={styles.header}
      columnTitleStyle={styles.headerTitle}
      columnWrapperStyle={styles.wrapper}
    />
    <TicketModal
      open={!_isEmpty(ticketToOpenModal)}
      ticket={ticketToOpenModal}
      categories={categories}
      assignees={assignees}
      onClose={onCloseCardModal}
      setAlert={setAlert}
    />
  </React.Fragment>
));

TicketBoard.propTypes = {
  setAlert: PropTypes.func,
  tickets: PropTypes.array,
  categories: PropTypes.array,
  assignees: PropTypes.array,
  lockBoard: PropTypes.bool,
  loading: PropTypes.bool,
  history: PropTypes.object,
  ticketToOpenModal: PropTypes.object,
  onCloseCardModal: PropTypes.func,
  onDragCard: PropTypes.func,
  onClickCard: PropTypes.func,
  doneTickets: PropTypes.array,
  ticketsFetch: PropTypes.func,
  isManager: PropTypes.bool
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter
)(TicketBoard);
