import React, { memo } from 'react';
import { get as _get, map as _map } from 'lodash';
import { compose, withPropsOnChange, withHandlers } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import {
  Grid,
  Typography,
  FormControl,
  FormHelperText,
  Checkbox,
  FormControlLabel
} from '@material-ui/core';
import PropTypes from 'prop-types';

import { ALL_ROLES, ADMIN_ROLE } from '../../../../enums/user-roles';
import withErrorBoundary from '../../../../utils/with-error-boundary';
import withViewerData from '../../../../utils/with-viewer-data';
import Loading from '../../../../components/Loading';
import FormTextField from '../../../../components/FormFields/FormTextField';
import FormPhoneField from '../../../../components/FormFields/FormPhoneField';
import FormSelectField from '../../../../components/FormFields/FormSelectField';
import Session from '../../../../utils/session';
import styles from './styles';

const BaseMemberForm = memo(({
  setLocationOptionChecked, isLoading,
  formData, setFormData, classes, isSubmiting
}) => {
  if (isLoading) return <Loading />;
  const { locationsOptions, validationErrors } = formData;
  const errors = validationErrors || {};
  const { role, id } = Session.getUserData();
  return (
    <React.Fragment>
      <Grid container direction="row" spacing={1}>
        <Grid item xs={12} sm={6}>
          <FormTextField
            errors={_get(errors, 'name', [])}
            label="Nome"
            disabled={isSubmiting}
            value={_get(formData, 'name') || ''}
            onChangeText={name => setFormData({ name })}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormTextField
            errors={_get(errors, 'lastname', [])}
            label="Sobrenome"
            disabled={isSubmiting}
            value={_get(formData, 'lastname') || ''}
            onChangeText={lastname => setFormData({ lastname })}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormTextField
            errors={_get(errors, 'email', [])}
            label="E-mail"
            disabled
            value={_get(formData, 'email') || ''}
            onChangeText={email => setFormData({ email })}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormPhoneField
            errors={_get(errors, 'phone', [])}
            label="Telefone celular"
            disabled={isSubmiting}
            value={_get(formData, 'phone') || ''}
            onChangeValue={phone => setFormData({ phone })}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormTextField
            label="Cargo"
            value={_get(formData, 'job_role') || ''}
            onChangeText={jobRole => setFormData({ job_role: jobRole })}
          />
        </Grid>
        <Grid item xs={12} sm={6} />
        { !(role === ADMIN_ROLE && _get(formData, 'id') === id) &&
          <Grid item xs={12} sm={6}>
            <Typography variant="h6">
              Permisso
            </Typography>
            <FormSelectField
              errors={_get(errors, 'role', [])}
              label="Nvel de acesso"
              value={_get(formData, 'role') || ''}
              disabled={isSubmiting}
              onChange={(e) => setFormData({ role: e.target.value })}
              options={ALL_ROLES}
            />
          </Grid> }
        <Grid item xs={12}>
          <Typography variant="h6">
            Locais de acesso
          </Typography>
          <FormControl fullWidth error={!!errors.location_ids}>
            {errors.location_ids && <FormHelperText>{errors.location_ids}</FormHelperText>}
            {
              locationsOptions.map((option) => (
                <FormControlLabel
                  key={option.value}
                  classes={{ label: classes.checkboxLabel }}
                  control={
                    <Checkbox
                      checked={option.checked}
                      disabled={isSubmiting}
                      onChange={(e) => {
                        setLocationOptionChecked({
                          locationId: option.value, checked: e.target.checked
                        });
                      }}
                      value={option.value}
                    />
                  }
                  label={option.label}
                />
              ))
            }
          </FormControl>
        </Grid>
      </Grid>
    </React.Fragment>
  );
});
BaseMemberForm.propTypes = {
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  setLocationOptionChecked: PropTypes.func,
  isLoading: PropTypes.bool,
  isSubmiting: PropTypes.bool,
  classes: PropTypes.object
};

const MemberForm = compose(
  withErrorBoundary,
  withStyles(styles, { withTheme: true }),
  withViewerData,
  withHandlers({
    setLocationOptionChecked: ({ formData, setFormData }) => ({ locationId, checked }) => {
      const { locationsOptions } = formData;
      setFormData({
        locationsOptions: _map(locationsOptions, (option) => {
          return option.value === locationId ? { ...option, checked } : option;
        })
      });
    }
  }),
  withPropsOnChange(['viewer', 'member'], (props) => {
    const { member, viewer, setFormData } = props;
    if (!viewer) return null;

    const memberLocations = _get(member, 'locations', []);
    const locationsOptions = _get(viewer, 'locations', []).map(viewerLocation => ({
      label: viewerLocation.name,
      value: viewerLocation.id,
      checked: memberLocations.some(location => viewerLocation.id === location.id)
    }));
    const formDataUpdated = {
      locationsOptions,
      ...member
    };
    setFormData({
      ...formDataUpdated,
      previousValues: formDataUpdated
    });
    return true;
  })
)(BaseMemberForm);

export default MemberForm;
