import React, { memo } from 'react';
import { get as _get } from 'lodash';
import { compose } from 'recompose';
import { Box, Button } from '@material-ui/core';
import PropTypes from 'prop-types';
import TicketsService from '../../../../services/tickets';
import withRequest from '../../../../utils/with-request';
import withFormData from '../../../../utils/with-form-data';
import StandardDialog from '../../../../components/StandardDialog';
import ExportTicketsCsvForm from './ExportTicketsCsvForm';

const ExportTicketsCsvModalBase = ({
  isModalOpen, onClose: onCloseArg,
  formData, setFormData, resetFormData, exportCsvFetch, exportCsvLoading
}) => {
  const onSubmit = () => exportCsvFetch(true);
  const onClose = () => {
    resetFormData();
    setTimeout(onCloseArg, 10);
  };
  return (
    isModalOpen && (
      <StandardDialog
        open={isModalOpen}
        onClose={onClose}
        modalTitle="Exportar todos os tickets"
        content={
          <ExportTicketsCsvForm
            formData={formData}
            setFormData={setFormData}
            isLoading={exportCsvLoading}
          />
        }
        actions={
          <React.Fragment>
            <Button
              color="default"
              size="large"
              variant="contained"
              onClick={onClose}
              disabled={exportCsvLoading}
            >
              CANCELAR
            </Button>
            <Box ml={2}>
              <Button
                color="primary"
                size="large"
                variant="contained"
                onClick={onSubmit}
                disabled={exportCsvLoading}
              >
                EXPORTAR
              </Button>
            </Box>
          </React.Fragment>
        }
      />
    )
  );
};

ExportTicketsCsvModalBase.propTypes = {
  isModalOpen: PropTypes.bool,
  onClose: PropTypes.func,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  resetFormData: PropTypes.func,
  exportCsvFetch: PropTypes.func,
  exportCsvLoading: PropTypes.bool
};

const withExportCsv = withRequest(TicketsService.exportCsv, {
  name: 'exportCsv',
  init: false,
  options: ({ formData, setFormData, setAlert, onClose }) => {
    const { validationErrors, ...formFields } = formData;

    return {
      variables: formFields,
      onRequestFinished: (data) => {
        setAlert({
          alertMsg: _get(data, 'flash_message.message'),
          alertType: 'success',
          showAlert: true
        });
        onClose();
      },
      onRequestError: error => {
        setAlert({
          alertMsg: 'Erro ao tentar exportar CSV',
          alertType: 'error',
          showAlert: true
        });
        setFormData({ validationErrors: _get(error, 'errors', {}) });
      }
    };
  }
});

const ExportTicketsCsvModal = compose(
  memo,
  withFormData(() => ({ start_date: null, end_date: null })),
  withExportCsv
)(ExportTicketsCsvModalBase);

export default ExportTicketsCsvModal;
