import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _get from 'lodash/get';
import { compose } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Paper from '@material-ui/core/Paper';
import _findIndex from 'lodash/findIndex';
import { withRouter } from 'react-router';
import Session from '../../utils/session';
import { MEMBER_ROLE, AGENT_ROLE } from '../../enums/user-roles';
import Routes from '../../routes';
import styles from './styles';

const HIDE_FOR_MEMBERS = [
  'TicketCategories', 'Locations', 'Members', 'Invoices', 'MyPlan', 'Slack'
];
const HIDE_FOR_NON_TICKETS = ['TicketCategories', 'Locations', 'Slack'];
const HIDE_FOR_AGENTS = [
  'TicketCategories', 'Locations', 'Members', 'Invoices', 'MyPlan', 'Slack'
];

const prepareTabMenu = () => {
  let tabMenu = Object.values(Routes).filter(r => !r.hideMenu && r.settingsMenu);
  const viewer = Session.getUserData();
  const isMemberRole = _get(viewer, 'role', MEMBER_ROLE) === MEMBER_ROLE;
  const hasTicketSupport = _get(viewer, 'company.has_ticket_support', false);
  if (isMemberRole) {
    tabMenu = tabMenu.filter(r => !HIDE_FOR_MEMBERS.includes(r.trackPage));
  }
  if (viewer.role === AGENT_ROLE) {
    tabMenu = tabMenu.filter(r => !HIDE_FOR_AGENTS.includes(r.trackPage));
  }
  if (!hasTicketSupport) {
    tabMenu = tabMenu.filter(r => !HIDE_FOR_NON_TICKETS.includes(r.trackPage));
  }
  return tabMenu;
};

const SettingsMenu = memo(({ history, classes }) => {
  const tabMenu = prepareTabMenu();
  return (
    <Paper square className={classes.root}>
      <Tabs
        value={_findIndex(tabMenu, t => history.location.pathname === t.path)}
        indicatorColor="primary"
        textColor="primary"
        scrollButtons="auto"
        variant="scrollable"
        onChange={(e, index) => history.push(tabMenu[index].path)}
      >
        {tabMenu.map((t, index) => <Tab key={t.path} label={t.label} index={index} />)}
      </Tabs>
    </Paper>
  );
});

SettingsMenu.propTypes = {
  history: PropTypes.object,
  classes: PropTypes.object
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter
)(SettingsMenu);
