import React, { memo } from 'react';
import { compose } from 'recompose';
import { Grid, Typography } from '@material-ui/core';
import PropTypes from 'prop-types';

import {
  FormTextField, FormNumberField, FormPhoneField, FormZipCodeField
} from '../../../components/FormFields';

const CustomerFieldset = ({
  isLoading, setFormData,
  formData: { cnpj, addressZipcode, addressNumber, addressComplementary, phone }
}) => {
  return (
    <React.Fragment>
      <Typography variant="h6">
        <b>Dados do cliente</b>
      </Typography>
      <Grid container alignItems="center">
        <Grid container alignItems="center" spacing={1}>
          <Grid item xs={12} sm={6}>
            <FormNumberField
              label="CNPJ"
              placeholder="CNPJ da empresa (Somente nmeros)"
              disabled={isLoading}
              value={cnpj}
              onChangeText={val => setFormData({ cnpj: val })}
              max={99999999999999}
            />
          </Grid>
          <Grid item xs={8} sm={6}>
            <FormPhoneField
              label="Telefone"
              placeholder="Contato da empresa"
              disabled={isLoading}
              value={phone}
              onChangeText={val => setFormData({ phone: val })}
            />
          </Grid>
        </Grid>
        <Grid container alignItems="center" spacing={1}>
          <Grid item xs={8} sm={5}>
            <FormZipCodeField
              label="CEP"
              placeholder="CEP da empresa"
              disabled={isLoading}
              value={addressZipcode}
              onChangeText={val => setFormData({ addressZipcode: val })}
            />
          </Grid>
          <Grid item xs={6} sm={3}>
            <FormNumberField
              label="Nmero"
              placeholder="Nmero logradouro do CEP"
              disabled={isLoading}
              value={addressNumber}
              onChangeText={val => setFormData({ addressNumber: val })}
            />
          </Grid>
          <Grid item xs={6} sm={4}>
            <FormTextField
              label="Complemento"
              placeholder="Complemento do CEP"
              disabled={isLoading}
              value={addressComplementary}
              onChangeText={val => setFormData({ addressComplementary: val })}
            />
          </Grid>
        </Grid>
      </Grid>
    </React.Fragment>
  );
};

CustomerFieldset.propTypes = {
  isLoading: PropTypes.bool,
  setFormData: PropTypes.func,
  formData: PropTypes.shape({
    cnpj: PropTypes.string,
    phone: PropTypes.string,
    addressZipcode: PropTypes.string,
    addressNumber: PropTypes.string,
    addressComplementary: PropTypes.string
  })
};

export default compose(
  memo
)(CustomerFieldset);
