import React, { memo } from 'react';
import {
  compose, withState, withHandlers
} from 'recompose';
import PropTypes from 'prop-types';

import withRequest from '../../../../utils/with-request';
import AttachButton from '../../../../components/AttachButton';
import TicketCommentsService from '../../../../services/ticket-comments';

const CommentAttachButtonBase = ({
  file, setFile, onUploadSuccess, disabled,
  onUploadError
}) => (
  <AttachButton
    value={file}
    onChange={setFile}
    onUploadSuccess={onUploadSuccess}
    disabled={disabled}
    accept="image/*,.doc,.docx,.xls,.xlsx,.pdf,.xml"
    buttonProps={{ style: { backgroundColor: '#fff' } }}
    sizeLimit={10}
    onUploadError={onUploadError}
  />
);
CommentAttachButtonBase.propTypes = {
  file: PropTypes.object,
  disabled: PropTypes.bool,
  setFile: PropTypes.func,
  onUploadSuccess: PropTypes.func,
  onUploadError: PropTypes.func
};

const withSendTicketComment = withRequest(TicketCommentsService.sendComment, {
  name: 'sendTicketComment',
  init: false,
  options: ({ setFile, onAttachmentSent, onUploadError }) => ({
    onRequestFinished: () => {
      setFile(null);
      if (onAttachmentSent) onAttachmentSent();
    },
    onRequestError: err => {
      setFile(null);
      onUploadError(err);
    }
  })
});

const CommentAttachButton = compose(
  memo,
  withState('file', 'setFile', null),
  withSendTicketComment,
  withHandlers({
    onUploadSuccess: ({
      ticketId, isInternal, setFile, sendTicketCommentFetch
    }) => result => {
      setFile({ ...result, progressPercent: 99 });
      sendTicketCommentFetch(true, null, null, {
        variables: {
          id: ticketId, attachmentData: result, content: null, isInternal
        }
      });
    },
    onUploadError: ({ onUploadError, setFile }) => err => {
      setFile(null);
      if (onUploadError) onUploadError(err);
    }
  })
)(CommentAttachButtonBase);

export default CommentAttachButton;
