import React, { memo } from 'react';
import { compose } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import PropTypes from 'prop-types';

import withErrorBoundary from '../../../utils/with-error-boundary';
import { withMainLayout } from '../../../components/MainLayout';
import SettingsMenu from '../../../components/SettingsMenu';
import styles from './styles';

const WrapperBase = ({
  classes, setAlert, WrappedComponent, ...otherProps
}) => {
  const EnhancedWrappedComponent = withErrorBoundary(WrappedComponent);
  return (
    <React.Fragment>
      <div className={classes.headerView}>
        <aside>
          <Typography variant="h5">
            <b>Configuraes</b>
          </Typography>
        </aside>
      </div>
      <SettingsMenu />
      <EnhancedWrappedComponent setAlert={setAlert} {...otherProps} />
    </React.Fragment>
  );
};

WrapperBase.propTypes = {
  classes: PropTypes.object,
  setAlert: PropTypes.func,
  WrappedComponent: PropTypes.oneOfType([PropTypes.func, PropTypes.object])
};

const Wrapper = compose(
  withMainLayout,
  memo,
  withStyles(styles, { withTheme: true })
)(WrapperBase);

const withWrapper = WrappedComponent => props => (
  <Wrapper {...props} WrappedComponent={WrappedComponent} />
);

export default withWrapper;
