/* global window */
import React, { memo } from 'react';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { compose, lifecycle, withState } from 'recompose';
import _get from 'lodash/get';
import _set from 'lodash/set';
import _size from 'lodash/size';
import _sortBy from 'lodash/sortBy';
import _uniqBy from 'lodash/uniqBy';
import SlackUserList from './SlackUserList';
import SlackIntegration from './SlackIntegration';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import Loading from '../../components/Loading';
import SettingsMenu from '../../components/SettingsMenu';
import MemberService from '../../services/member';
import SlackService from '../../services/slack';
import { USER_MANAGER_ROLES } from '../../enums/user-roles';
import Tracker from '../../utils/tracker';
import withRequest from '../../utils/with-request';
import Session from '../../utils/session';
import config from '../../config';
import styles from './styles';

const SLACK_INTEGRATION_MSGS = {
  ok: 'Conta Slack integrada com sucesso',
  not_authorized: 'No foi possvel integrar sua conta Slack',
  wrong_member: ' necessrio logar no Slack com mesmo email de login Lobby'
};

const Slack = memo(({
  classes, alert, setAlert, revokeSlackFetch, revokeSlackLoading, canInvite, slackUsers,
  slackUsersFetch, slackUsersLoading, inviteSlackUsersFetch, inviteSlackUsersLoading, nextCursor
}) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  const member = Session.getUserData();
  const companyName = _get(member, 'company.name');
  const slackTeamId = _get(member, 'company.slack_team_id');
  const slackUserId = _get(member, 'slack_user_id');
  const locations = _sortBy(_get(member, 'locations', []), ['name']);
  const onAuthUserOnSlack = () => {
    Tracker.hit('SlackIntegration - AuthUser');
    let url = `${config.slackOAuthURL}?scope=${config.slackOAuthScope}&team=${slackTeamId}`;
    url += `&client_id=${config.slackClientId}&user_scope=${config.slackOAuthUserScope}`;
    url += `&redirect_uri=${config.slackRedirectURL}`;
    window.location.href = url;
  };
  const onInstallLobbyOnSlack = () => {
    Tracker.hit('SlackIntegration - InstallLobby');
    let url = `${config.slackOAuthURL}?scope=${config.slackOAuthScope}`;
    url += `&client_id=${config.slackClientId}&user_scope=${config.slackOAuthUserScope}`;
    url += `&redirect_uri=${config.slackRedirectURL}`;
    window.location.href = url;
  };
  const onRevokeSlack = () => {
    if (window.confirm('Tem certeza em realizar essa ao?')) {
      Tracker.hit('SlackIntegration - RevokeUser');
      revokeSlackFetch(true);
    }
  };
  const onInviteSlackUsers = (invite) => {
    inviteSlackUsersFetch(true, '', null, { variables: invite });
  };
  const onShowMoreSlackUsers = () => slackUsersFetch(false);
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} />
      <section>
        <div className={classes.container}>
          <div className={classes.headerView}>
            <aside>
              <Typography variant="h5">
                <b>Configuraes</b>
              </Typography>
            </aside>
          </div>
        </div>
        <div className={classes.container}>
          <SettingsMenu />
        </div>
        <br />
        {
          slackUsersLoading ?
            <Loading />
            :
            canInvite && _size(slackUsers) ?
              <SlackUserList
                users={slackUsers}
                onRevoke={onRevokeSlack}
                onInvite={onInviteSlackUsers}
                onShowMore={onShowMoreSlackUsers}
                isRevokeDisabled={revokeSlackLoading}
                isInviteDisabled={inviteSlackUsersLoading}
                canDisconnect={Boolean(slackUserId)}
                nextCursor={nextCursor}
                locations={locations}
                companyName={companyName}
              />
              :
              <SlackIntegration
                onAuthUser={onAuthUserOnSlack}
                onInstallLobby={onInstallLobbyOnSlack}
                onRevoke={onRevokeSlack}
                isDisabled={revokeSlackLoading}
              />
        }
      </section>
    </div>
  );
});

Slack.propTypes = {
  classes: PropTypes.object,
  alert: PropTypes.object,
  setAlert: PropTypes.func,
  revokeSlackFetch: PropTypes.func,
  revokeSlackLoading: PropTypes.bool,
  canInvite: PropTypes.bool,
  slackUsers: PropTypes.array,
  slackUsersFetch: PropTypes.func,
  slackUsersLoading: PropTypes.bool,
  nextCursor: PropTypes.string,
  inviteSlackUsersFetch: PropTypes.func,
  inviteSlackUsersLoading: PropTypes.bool
};

const withRevokeSlack = withRequest(MemberService.revokeSlackUser, {
  name: 'revokeSlack',
  init: false,
  options: ({ setAlert }) => ({
    onRequestFinished: async () => {
      setAlert({ alertMsg: 'A conta Slack foi desconectada', alertType: 'success', showAlert: true });
      const member = Session.getUserData();
      _set(member, 'slack_user_id', null);
      Session.setUserData(member);
    },
    onRequestError: () => {
      setAlert({ alertMsg: 'Erro ao desconectar conta Slack', alertType: 'error', showAlert: true });
    }
  })
});

const withListSlackUsers = withRequest(SlackService.listUsers, {
  name: 'slackUsers',
  options: ({ setAlert, slackUsers, setSlackUsers, setNextCursor, nextCursor, canInvite }) => ({
    variables: { canInvite, nextCursor },
    onRequestFinished: (data) => {
      const currentUsers = slackUsers.concat(_get(data, 'users', []));
      setSlackUsers(_sortBy(_uniqBy(currentUsers, 'email'), ['name']));
      setNextCursor(_get(data, 'next_cursor', ''));
    },
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao carregar lista de usurios slack',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

const withInviteSlackUsers = withRequest(SlackService.inviteUsers, {
  name: 'inviteSlackUsers',
  init: false,
  options: ({ setAlert, slackUsersFetch }) => ({
    onRequestFinished: () => {
      setAlert({
        alertMsg: 'Convite enviado com sucesso',
        alertType: 'success',
        showAlert: true
      });
      slackUsersFetch(true);
    },
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao carregar convidar usurios slack',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  withRouter,
  withStyles(styles, { withTheme: true }),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withState('canInvite', 'setCanInvite', false),
  withState('slackUsers', 'setSlackUsers', []),
  withState('nextCursor', 'setNextCursor', ''),
  withRevokeSlack,
  withListSlackUsers,
  withInviteSlackUsers,
  lifecycle({
    async componentDidMount() {
      const { location, setAlert, setCanInvite } = this.props;
      const query = new URLSearchParams(location.search);
      const integrationStatus = query.get('status');
      const member = Session.getUserData();
      const role = _get(member, 'role');
      const slackTeamId = _get(member, 'company.slack_team_id');
      const slackUserId = _get(member, 'slack_user_id');
      const canInviteSlackUsers = USER_MANAGER_ROLES.includes(role) && slackTeamId && slackUserId;
      setCanInvite(Boolean(canInviteSlackUsers));
      if (integrationStatus) {
        const alertMsg = SLACK_INTEGRATION_MSGS[integrationStatus];
        const alertType = integrationStatus === 'ok' ? 'success' : 'error';
        setAlert({ alertMsg, alertType, showAlert: true });
        window.history.pushState('', '', '/#/configuracoes/slack');
      }
    }
  })
)(Slack);
