import React, { memo } from 'react';
import { first, get, find, isEmpty } from 'lodash';
import { compose, withProps, withHandlers, withState } from 'recompose';
import { Box, Typography, Grid, Button } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import ErrorIcon from '@material-ui/icons/Error';
import { withRouter } from 'react-router';
import moment from 'moment';
import PropTypes from 'prop-types';

import config from '../../config';
import { USER_MANAGER_ROLES } from '../../enums/user-roles';
import withNotAuthorizedHandler from '../../utils/with-not-authorized-handler';
import withFormData from '../../utils/with-form-data';
import withRequest from '../../utils/with-request';
import PagarmeApi from '../../utils/pagarme-api';
import Session from '../../utils/session';
import { withMainLayout } from '../../components/MainLayout';
import Loading from '../../components/Loading';
import PlansService from '../../services/plans';
import MemberService from '../../services/member';
import CreditCardsService from '../../services/credit-cards';
import SubscriptionsService from '../../services/subscriptions';
import CreditCardFieldset from './CreditCardFieldset';
import CustomerFieldset from './CustomerFieldset';
import CheckoutSummary from './CheckoutSummary';
import styles from './styles';

const PLAN_DURATION_DAYS = config.isProduction ? 30 : 1;

const PlanSubscription = ({
  formData, setFormData, onSubmit, createLoading, updateLoading,
  preloadError, preloadLoading, currentCreditCard, setCurrentCreditCard,
  nextCharge, isDowngrade, hasActiveSubscription, history, plan, classes
}) => {
  if (preloadLoading) return <Loading />;

  const submitLoading = updateLoading || createLoading;
  const isDisableSubmit = submitLoading || preloadError;
  if (isEmpty(plan)) {
    return (
      <div className={classes.centerHeaderView}>
        <ErrorIcon className={classes.emptyHeaderIcon} />
        <b className={classes.emptyHeaderTitle}>
          Sistema de assinatura esta temporariamente fora de servio
        </b>
      </div>
    );
  }
  return (
    <Grid item xs={12} sm={9}>
      <Typography variant="h5">
        <b>Assinar plano</b>
      </Typography>
      <Box mt={3} mb={2}>
        <CreditCardFieldset
          currentCreditCard={currentCreditCard}
          formData={formData}
          setFormData={setFormData}
          isLoading={submitLoading}
          onSetNewCreditCard={() => setCurrentCreditCard(null)}
        />
        {
          !hasActiveSubscription &&
            <Box mt={2}>
              <CustomerFieldset
                formData={formData}
                setFormData={setFormData}
                disabled={submitLoading}
              />
            </Box>
        }
        <Box mt={2}>
          <CheckoutSummary
            formData={formData}
            setFormData={setFormData}
            disabled={submitLoading}
            nextCharge={nextCharge}
            isDowngrade={isDowngrade}
          />
        </Box>
      </Box>
      <Grid justify="flex-end" container item xs={12}>
        <Button
          color="default"
          size="large"
          onClick={() => history.push('/configuracoes/meu-plano')}
        >
          Voltar
        </Button>
        &nbsp;&nbsp;&nbsp;
        <Button
          color="primary"
          size="large"
          variant="contained"
          onClick={onSubmit}
          disabled={isDisableSubmit}
        >
          {hasActiveSubscription ? 'Atualizar Plano' : 'Assinar Plano'}
        </Button>
      </Grid>
    </Grid>
  );
};

PlanSubscription.propTypes = {
  history: PropTypes.object,
  classes: PropTypes.object,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  plan: PropTypes.object,
  preloadLoading: PropTypes.bool,
  preloadError: PropTypes.bool,
  currentCreditCard: PropTypes.object,
  setCurrentCreditCard: PropTypes.func,
  isDowngrade: PropTypes.bool,
  nextCharge: PropTypes.object,
  hasActiveSubscription: PropTypes.bool,
  createLoading: PropTypes.bool,
  updateLoading: PropTypes.bool,
  onSubmit: PropTypes.func
};

const prepareCreditCard = async ({ currentCreditCard, formFields }) => {
  let creditCardHash;
  let creditCardId;
  try {
    if (isEmpty(currentCreditCard)) {
      creditCardHash = await PagarmeApi.encryptCreditCard(formFields);
    } else {
      creditCardId = get(currentCreditCard, 'id');
    }
  } catch (e) {
    console.error(e);
    creditCardHash = null;
  }
  return { creditCardHash, creditCardId };
};

const dataSource = () => Promise.all([
  PlansService.list().catch(e => e),
  CreditCardsService.list().catch(e => e),
  SubscriptionsService.get().catch(e => e)
]);

const withPreload = withRequest(dataSource, {
  name: 'preload',
  props: ({ preloadData }) => {
    if (!preloadData) return {};
    return {
      plan: first(get(find(preloadData, p => p.plans), 'plans')),
      currentSubscription: get(find(preloadData, p => p.subscription), 'subscription')
    };
  },
  options: ({ setAlert, setCurrentCreditCard }) => ({
    onRequestFinished: (data) => {
      const creditCard = first(get(find(data, d => d.creditCards), 'creditCards'));
      setCurrentCreditCard(creditCard);
    },
    onRequestError: (error) => {
      console.error(error);
      setAlert({
        alertMsg: 'Erro ao carregar informaes sobre assinar plano',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

const withCreate = withRequest(SubscriptionsService.create, {
  name: 'create',
  init: false,
  options: async ({
    formData, setFormData, resetFormData,
    currentCreditCard, setAlert, plan, history
  }) => {
    const { validationErrors, ...formFields } = formData;
    const planSeats = get(first(formFields.items), 'amount');
    const creditCardData = await prepareCreditCard({ formFields, currentCreditCard });
    return {
      variables: {
        ...formData,
        ...creditCardData,
        planName: plan.planName,
        planSeats
      },
      onRequestFinished: async () => {
        setAlert({
          alertMsg: 'Assinatura de plano efetuada com sucesso.',
          alertType: 'success',
          showAlert: true
        });
        resetFormData();
        const response = await MemberService.getViewer();
        Session.setUserData(get(response, 'member'));
        setTimeout(() => {
          history.push('/configuracoes/meu-plano');
        }, 2000);
      },
      onRequestError: error => {
        setAlert({
          alertMsg: get(error, 'error', 'Erro ao assinar plano, fique tranquilo, nenhuma cobrana foi efetuada.'),
          alertType: 'error',
          showAlert: true
        });
        setFormData({ validationErrors: get(error, 'errors', {}) });
      }
    };
  }
});

const withUpdate = withRequest(SubscriptionsService.update, {
  name: 'update',
  init: false,
  options: async ({
    currentSubscription, currentCreditCard, setAlert,
    formData, setFormData, resetFormData, plan, history
  }) => {
    const { validationErrors, ...formFields } = formData;
    const planSeats = get(first(formFields.items), 'amount');
    const creditCardData = await prepareCreditCard({ formFields, currentCreditCard });
    return {
      variables: {
        id: currentSubscription.id,
        planName: plan.planName,
        planSeats,
        ...creditCardData
      },
      onRequestFinished: async () => {
        setAlert({
          alertMsg: 'Assinatura de plano atualizada com sucesso.',
          alertType: 'success',
          showAlert: true
        });
        resetFormData();
        const response = await MemberService.getViewer();
        Session.setUserData(get(response, 'member'));
        setTimeout(() => {
          history.push('/configuracoes/meu-plano');
        }, 2000);
      },
      onRequestError: error => {
        setAlert({
          alertMsg: get(error, 'error', 'Erro ao atualizar plano, fique tranquilo, nenhuma cobrana foi efetuada.'),
          alertType: 'error',
          showAlert: true
        });
        setFormData({ validationErrors: get(error, 'errors', {}) });
      }
    };
  }
});

const calculateRefund = ({ periodStart, periodEnd, currentPrice }) => {
  const endDate = moment(periodEnd).endOf('day');
  const startDate = moment(periodStart).endOf('day');

  const planDuration = endDate.diff(startDate, 'days');
  const oldPlanRemainingDays = moment(endDate).diff(moment(), 'days');
  const oldPlanUnusedValue = (oldPlanRemainingDays / planDuration) * currentPrice;

  return oldPlanUnusedValue;
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withMainLayout,
  withNotAuthorizedHandler({
    condition: (viewer) => !USER_MANAGER_ROLES.includes(viewer.role)
  }),
  withRouter,
  memo,
  withState('currentCreditCard', 'setCurrentCreditCard', null),
  withPreload,
  withProps(({ currentSubscription }) => {
    const periodEnd = get(currentSubscription, 'periodEnd');
    return {
      hasActiveSubscription: periodEnd && new Date(periodEnd) > new Date()
    };
  }),
  withFormData((props) => {
    const company = get(props, 'viewer.company');
    const mainPlan = get(props, 'plan');
    const currentSubscription = get(props, 'currentSubscription');
    const hasActiveSubscription = get(props, 'hasActiveSubscription');
    const amount = get(currentSubscription, 'plan.seats') || get(company, 'total_paid_users') || 1;
    const refundValue = hasActiveSubscription ? calculateRefund(currentSubscription) : 0;
    const refundItem = {
      title: 'Valor de desconto do perodo atual',
      value: refundValue
    };
    const refundItems = refundValue ? [refundItem] : [];
    return {
      cardHolderName: '',
      cardNumber: '',
      cardCvv: null,
      cardExpirationDate: '',
      cnpj: '',
      addressZipcode: '',
      addressNumber: '',
      addressComplementary: '',
      phone: '',
      items: [
        {
          title: get(mainPlan, 'planName'),
          description: 'Acesso a ferramenta de gesto das solicitaes dos colaboradores de forma centralizada. Seus colaboradores podem abrir tickets para voc e seu time facilmente, seja no computador, celular, QR code ou integrado ao Slack. Tenha mais visibilidade e produtividade na gesto das suas tarefas.',
          unitPrice: get(mainPlan, 'planPrice'),
          amount
        }
      ],
      refundItems
    };
  }),
  withProps(({ currentSubscription, hasActiveSubscription, formData }) => {
    const mainItem = first(formData.items);
    const currentTotalSeats = get(currentSubscription, 'plan.seats');
    const selectedSeats = get(mainItem, 'amount');
    const isDowngrade = selectedSeats < currentTotalSeats;
    const nextChargeDate = hasActiveSubscription
      ? moment(currentSubscription.periodEnd)
      : moment().add(PLAN_DURATION_DAYS, 'days');
    const nextCharge = hasActiveSubscription ? {
      title: `Prxima cobrana em ${nextChargeDate.format('DD/MM/YYYY')}`,
      value: mainItem.unitPrice * selectedSeats
    } : null;
    return { isDowngrade, nextCharge };
  }),
  withCreate,
  withUpdate,
  withHandlers({
    onSubmit: ({ createFetch, updateFetch, hasActiveSubscription }) => () => (
      hasActiveSubscription ? updateFetch(true) : createFetch(true)
    )
  })
)(PlanSubscription);
