import React, { memo } from 'react';
import { get } from 'lodash';
import { compose, withHandlers } from 'recompose';
import { Button } from '@material-ui/core';
import PropTypes from 'prop-types';

import withRequest from '../../../../../utils/with-request';
import SubscriptionsService from '../../../../../services/subscriptions';

const UndoCancelationButton = ({
  className,
  onClick
}) => (
  <Button
    variant="outlined"
    className={className}
    onClick={onClick}
  >
    REATIVAR PLANO
  </Button>
);
UndoCancelationButton.propTypes = {
  className: PropTypes.any,
  onClick: PropTypes.func
};

const withUndoCancelation = withRequest(SubscriptionsService.undoCancelation, {
  name: 'undocancelation',
  init: false,
  options: ({ setAlert, onSuccess }) => ({
    onRequestFinished: () => {
      setAlert({
        alertMsg: 'Sua assinatura foi reativada sucesso.',
        alertType: 'success',
        showAlert: true
      });
      if (onSuccess) onSuccess();
    },
    onRequestError: (error) => {
      console.error(error);
      setAlert({
        alertMsg: get(error, 'error', 'No foi possvel reativar sua assinatura.'),
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  memo,
  withUndoCancelation,
  withHandlers({
    onClick: ({ undocancelationFetch }) => () => undocancelationFetch(true)
  })
)(UndoCancelationButton);
