import { get } from 'lodash';
import { compose, withState, lifecycle, branch, renderComponent } from 'recompose';

import Session from './session';

const withViewerData = compose(
  withState('viewer', 'setViewer', { loading: true }),
  withState('subscriptionReference', 'setSubscriptionReference', null),
  lifecycle({
    componentDidMount() {
      const viewer = Session.getUserData();
      this.props.setViewer(viewer);

      const subscriptionReference = Session.subscribeOnViewerChange(updatedViewer => {
        this.props.setViewer(updatedViewer);
      });
      this.props.setSubscriptionReference(subscriptionReference);
    },
    componentWillUnmount() {
      Session.unsubscribeOnViewerChange(this.props.subscriptionReference);
    }
  }),
  branch(props => get(props, 'viewer.loading'), renderComponent(() => null))
);

export default withViewerData;
