import React, { memo } from 'react';
import { compose, lifecycle, withState } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import ErrorIcon from '@material-ui/icons/Error';
import PropTypes from 'prop-types';

import ErrorTracker from '../error-tracker';
import styles from './styles';

const ErrorFallbackBase = memo(({ classes }) => (
  <div className={classes.outterContainer}>
    <ErrorIcon className={classes.emptyHeaderIcon} />
    <b className={classes.emptyHeaderTitle}>
      Ocorreu um problema, por favor, tente mais tarde
    </b>
  </div>
));
ErrorFallbackBase.propTypes = {
  classes: PropTypes.object
};
const ErrorFallback = withStyles(styles, { withTheme: true })(ErrorFallbackBase);

/* eslint-disable react/prop-types */
const withErrorFallback = WrappedComponent => props => (
  props.hasError ? <ErrorFallback /> : <WrappedComponent {...props} />
);
/* eslint-enable react/prop-types */

export default compose(
  withState('hasError', 'setHasError', false),
  lifecycle({
    componentDidCatch(error, extra) {
      ErrorTracker.capture(error, { extra });

      const { setHasError } = this.props;
      setHasError(true);
    }
  }),
  withErrorFallback
);
