import React, { memo } from 'react';
import { sortBy, concat, get } from 'lodash';
import { Grid, Button } from '@material-ui/core';
import PropTypes from 'prop-types';
import {
  FormDateField, FormSelectField
} from '../../../components/FormFields';

const ALL_LOCATIONS = { id: 'ALL', name: 'Todos' };

const Form = ({
  viewer, setFormData, isLoading, onSubmit,
  formData: {
    location_id, start_date, end_date, validationErrors: errors
  }
}) => {
  const locations = concat(ALL_LOCATIONS, sortBy(get(viewer, 'locations', []), ['name']));
  return (
    <React.Fragment>
      <Grid container spacing={3} alignItems="center">
        <Grid item xs={12} sm={2}>
          <FormSelectField
            variant="standard"
            label="Local"
            errors={errors.location_id}
            disabled={isLoading}
            value={location_id}
            onChangeText={newVal => setFormData({ location_id: newVal })}
            options={locations}
            optionValueField="id"
            optionLabelField="name"
          />
        </Grid>
        <Grid item xs={12} sm={2}>
          <FormDateField
            variant="standard"
            errors={errors.start_date}
            label="Data de incio"
            disabled={isLoading}
            value={start_date}
            onChangeValue={newVal => setFormData({ start_date: newVal })}
            disableFuture
          />
        </Grid>
        <Grid item xs={12} sm={2}>
          <FormDateField
            variant="standard"
            errors={errors.end_date}
            label="Data de fim"
            disabled={isLoading}
            value={end_date}
            onChangeValue={newVal => setFormData({ end_date: newVal })}
            disableFuture
          />
        </Grid>
        <Grid item xs={12} sm={3}>
          <Button
            color="primary"
            size="large"
            variant="contained"
            onClick={onSubmit}
            disabled={isLoading}
          >
            Gerar Relatrio
          </Button>
        </Grid>
      </Grid>
    </React.Fragment>
  );
};

Form.propTypes = {
  viewer: PropTypes.object,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  isLoading: PropTypes.bool,
  onSubmit: PropTypes.func
};

export default memo(Form);
