import React, { memo } from 'react';
import { get, isEmpty } from 'lodash';
import { compose, withState } from 'recompose';
import { Box } from '@material-ui/core';
import PropTypes from 'prop-types';

import { USER_MANAGER_ROLES } from '../../../enums/user-roles';
import withRequest from '../../../utils/with-request';
import withViewerData from '../../../utils/with-viewer-data';
import SubscriptionsService from '../../../services/subscriptions';
import withNotAuthorizedHandler from '../../../utils/with-not-authorized-handler';
import withWrapper from '../with-wrapper';
import PlanAlert from '../../../components/PlanAlert';
import Loading from '../../../components/Loading';
import NoSubscriptionBlock from './NoSubscriptionBlock';
import CurrentSubscriptionBlock from './CurrentSubscriptionBlock';
import InvoicesTable from './InvoicesTable';

const MyPlan = ({
  viewer, setAlert,
  subscriptionData, subscriptionLoading, subscriptionError, subscriptionFetch
}) => {
  let subscriptionBlock;
  const trialExpiration = get(viewer, 'company.trial_expiration');
  const subscription = get(subscriptionData, 'subscription');
  if (subscriptionLoading) return <Loading />;

  if (isEmpty(subscriptionError)) {
    const hasActiveSubscription = subscription && (
      new Date(subscription.periodEnd) > new Date()
    );
    subscriptionBlock = hasActiveSubscription ? (
      <CurrentSubscriptionBlock
        setAlert={setAlert}
        subscription={subscription}
        refetch={() => subscriptionFetch(true)}
      />
    ) : (
      <NoSubscriptionBlock setAlert={setAlert} />
    );
  }
  return (
    <React.Fragment>
      <PlanAlert
        trialExpiration={trialExpiration}
        willDowngradeAt={get(subscription, 'willDowngradeAt')}
        willCancelAt={get(subscription, 'willCancelAt')}
      />
      <Box mb={4} mt={2}>
        {subscriptionBlock}
        <InvoicesTable setAlert={setAlert} />
      </Box>
    </React.Fragment>
  );
};

MyPlan.propTypes = {
  viewer: PropTypes.object,
  setAlert: PropTypes.func,
  subscriptionLoading: PropTypes.bool,
  subscriptionData: PropTypes.object,
  subscriptionError: PropTypes.object,
  subscriptionFetch: PropTypes.func
};

const withSubscription = withRequest(SubscriptionsService.get, {
  name: 'subscription',
  options: ({ setAlert }) => ({
    onRequestError() {
      setAlert({
        alertMsg: 'Erro ao carregar dados do servidor',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  memo,
  withNotAuthorizedHandler({
    condition: (viewer) => !USER_MANAGER_ROLES.includes(viewer.role)
  }),
  withViewerData,
  withState('loading', 'setLoading', true),
  withWrapper,
  withSubscription
)(MyPlan);
