import React, { memo } from 'react';
import { get as _get } from 'lodash';
import { compose, withState, withHandlers } from 'recompose';
import { Button } from '@material-ui/core';
import PropTypes from 'prop-types';

import Tracker from '../../../../utils/tracker';
import withRequest from '../../../../utils/with-request';
import LocationService from '../../../../services/locations';
import LocationFormModal from '../LocationFormModal';

const initialFormData = {
  validationErrors: {},
  name: '',
  address: '',
  neighborhood: '',
  city: '',
  state: '',
  zip_code: '',
  complement: ''
};

const NewLocationButtonBase = memo(({
  isModalOpen, setIsModalOpen, onClose,
  formData, setFormData, createLocationFetch, createLocationLoading
}) => {
  const onButtonClick = () => {
    Tracker.hit('NewLocationModal - Opened');
    setIsModalOpen(true);
  };
  return (
    <React.Fragment>
      <Button
        color="primary"
        size="large"
        variant="contained"
        onClick={onButtonClick}
      >
        NOVO LOCAL
      </Button>
      {
        isModalOpen && (
          <LocationFormModal
            open={isModalOpen}
            onClose={onClose}
            modalTitle="Novo Local"
            formData={formData}
            setFormData={setFormData}
            onSubmit={() => createLocationFetch(true)}
            isLoading={createLocationLoading}
          />
        )
      }
    </React.Fragment>
  );
});

NewLocationButtonBase.propTypes = {
  isModalOpen: PropTypes.bool,
  setIsModalOpen: PropTypes.func,
  onClose: PropTypes.func,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  createLocationFetch: PropTypes.func,
  createLocationLoading: PropTypes.bool
};

const withCreateLocation = withRequest(LocationService.createLocation, {
  name: 'createLocation',
  init: false,
  options: ({ formData, setFormData, setAlert, onCreated, onClose }) => {
    const { validationErrors, ...formFields } = formData;

    return {
      variables: formFields,
      onRequestFinished: () => {
        setAlert({
          alertMsg: 'Local criado com sucesso', alertType: 'success', showAlert: true
        });
        onCreated();
        onClose();
      },
      onRequestError: error => {
        setAlert({
          alertMsg: 'Erro ao criar local', alertType: 'error', showAlert: true
        });
        setFormData({ validationErrors: _get(error, 'errors', {}) });
      }
    };
  }
});

const NewLocationButton = compose(
  withState('isModalOpen', 'setIsModalOpen', false),
  withState('formData', 'setFormDataRaw', initialFormData),
  withHandlers({
    setFormData: ({ setFormDataRaw, formData }) => (attrs) => {
      setFormDataRaw({ ...formData, ...attrs });
    }
  }),
  withHandlers({
    onClose: ({ setFormData, setIsModalOpen }) => () => {
      Tracker.hit('NewLocationModal - Closed');
      setFormData(initialFormData);
      setIsModalOpen(false);
    }
  }),
  withCreateLocation
)(NewLocationButtonBase);

export default NewLocationButton;
