import React, { memo } from 'react';
import PropTypes from 'prop-types';
import { compose } from 'recompose';
import _get from 'lodash/get';
import {
  Paper, Button, Typography
} from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import Session from '../../../utils/session';
import styles from '../styles';

const SlackIntegration = memo(({
  classes, onInstallLobby, onAuthUser, onRevoke, isDisabled
}) => {
  const member = Session.getUserData();
  const slackUserId = _get(member, 'slack_user_id');
  const slackTeamId = _get(member, 'company.slack_team_id');
  return (
    <div className={classes.container}>
      <Paper className={classes.paper}>
        <section className={classes.slackBox}>
          <div className={classes.textCenter}>
            <img
              src={require('../../../assets/images/slack-integration.png')}
              alt="Integrao Lobby com Slack"
              className={classes.slackIntegration}
            />
            <br />
            <br />
            <Typography variant="h6" component="strong">
              <b>Crie tickets atravs do Slack</b>
            </Typography>
            <br />
            <Typography variant="body2" color="textSecondary">
              <b>Leva menos de um minuto</b>
            </Typography>
          </div>
          <br />
          <div className={classes.textCenter}>
            <Typography variant="subtitle2">
              Colaboradores podero criar tickets
              <br />
              via atalho Lobby ou comando
              <b> /lobby </b>
              no Slack
            </Typography>
            <br />
            {
              slackTeamId ?
                (
                  slackUserId ?
                    <Button
                      onClick={onRevoke}
                      size="large"
                      color="default"
                      variant="outlined"
                      disabled={isDisabled}
                    >
                      Desconectar Slack
                    </Button>
                    :
                    <Button
                      onClick={onAuthUser}
                      size="large"
                      color="primary"
                      variant="contained"
                      disabled={isDisabled}
                    >
                      Integrar Slack
                    </Button>
                )
                :
                (
                  <Button
                    onClick={onInstallLobby}
                    size="large"
                    color="primary"
                    variant="contained"
                    disabled={isDisabled}
                  >
                    Instalar Lobby no Slack
                  </Button>
                )
            }
          </div>
        </section>
      </Paper>
    </div>
  );
});

SlackIntegration.propTypes = {
  classes: PropTypes.object,
  onInstallLobby: PropTypes.func,
  onAuthUser: PropTypes.func,
  onRevoke: PropTypes.func,
  isDisabled: PropTypes.bool
};

export default compose(
  withStyles(styles, { withTheme: true })
)(SlackIntegration);
