import Api from '../utils/api';

const Uploads = {
  genericUploadsParams() {
    return Api.get('/uploads/params');
  },
  uploadToApi({ file, progressHandler }) {
    const rawResult = Api.post(
      '/uploads/upload', file.formData, null,
      {
        onUploadProgress: progressEvent => {
          const progressPercent = Math.round((progressEvent.loaded * 100) / progressEvent.total);
          progressHandler({
            progressPercent: progressPercent < 95 ? progressPercent : 95,
            originalFileName: file.name
          });
        }
      }
    );
    const result = rawResult
      .then(({ id, storage, metadata: { filename, size, mime_type } }) => ({
        id, storage, metadata: { filename, size, mime_type }, progressPercent: 100
      }));
    result.cancelRequest = rawResult.cancelRequest;

    return result;
  }
};

export default Uploads;
