/* global document */
import React, { memo } from 'react';
import PropTypes from 'prop-types';
import {
  get as _get, map as _map, size as _size, sortBy as _sortBy,
  find as _find, findIndex as _findIndex, toLower as _toLower,
  set as _set, clone as _clone, isEmpty as _isEmpty,
  first as _first, compact as _compact, reject as _reject
} from 'lodash';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';
import {
  withStyles, Typography, Paper, Grid, useMediaQuery,
  FormControl, Button, FormHelperText, TextField
} from '@material-ui/core';
import { useTheme } from '@material-ui/core/styles';
import ErrorIcon from '@material-ui/icons/Error';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import { compose, lifecycle, withState, withHandlers } from 'recompose';
import MomentUtils from '@date-io/moment';
import moment from 'moment';
import Header from '../../components/Header';
import TicketAds from '../../components/TicketAds';
import AlertBox from '../../components/AlertBox';
import FormTextField from '../../components/FormFields/FormTextField';
import FormSelectField from '../../components/FormFields/FormSelectField';
import TicketsService from '../../services/tickets';
import TicketCategoriesService from '../../services/ticket-categories';
import withRequest from '../../utils/with-request';
import Session from '../../utils/session';
import TicketsEnum from '../../enums/tickets';
import { AGENT_ROLES, USER_MANAGER_ROLES } from '../../enums/user-roles';
import { QRCODE, PORTAL } from '../../enums/request-sources';
import { SELECT } from '../../enums/fields';

import AttachButton from '../../components/AttachButton';
import QuestionInput from '../../components/QuestionInput';
import config from '../../config';
import styles from './styles';

const { PRIORITIES, PRIORITY_NORMAL } = TicketsEnum;

const emptyTicket = () => {
  const member = Session.getUserData();
  const locations = _sortBy(_get(member, 'locations', []), ['name']);
  return {
    title: '',
    description: '',
    category_id: '',
    deadline: null,
    priority: PRIORITY_NORMAL,
    source: PORTAL,
    location_id: _get(_first(locations), 'id'),
    attachment_data: null,
    ticket_form_questions: _clone([])
  };
};

const prepareTicketFormQuestions = (categories, categoryId) => {
  const currentCategory = _find(categories, c => c.id === categoryId);
  const ticketFormQuestions = _get(currentCategory, 'ticketFormQuestions', []);
  return _map(ticketFormQuestions, t => {
    const isSelectBox = t.fieldType === SELECT;
    const value = isSelectBox ? _compact([_first(_get(t, 'options'))]) : [];
    return { ...t, value };
  });
};

const findForSelectedCategory = (categories, selected) => {
  const categoryFound = _find(categories, (c) => {
    return _toLower(c.title) === _toLower(selected) || c.id === selected;
  });
  return _get(categoryFound, 'id') || _get(_first(categories), 'id');
};

const findForSelectedLocation = (locations, selected) => {
  const locationFound = _find(locations, (l) => {
    return _toLower(l.name) === _toLower(selected) || l.id === selected;
  });
  return _get(locationFound, 'id') || _get(_first(locations), 'id');
};

const NewTicket = memo(({
  classes, alert, setAlert, ticket, setTicketState,
  ticketLoading, ticketFetch, errors,
  ticketCategoriesLoading, categories, setShowQuestionError,
  isUploading, setIsUploading, showQuestionError
}) => {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const member = Session.getUserData();
  const hasTicketSupport = _get(member, 'company.has_ticket_support');
  const isManager = USER_MANAGER_ROLES.includes(_get(member, 'role'));
  if (hasTicketSupport) {
    const { showAlert, alertMsg, alertType, actionButton, onClickAction } = alert;
    const onClearAlert = () => setAlert({
      alertMsg: '', alertType: '', showAlert: false, actionButton: ''
    });
    let alertBox = null;
    if (showAlert) {
      alertBox = (
        <AlertBox
          message={alertMsg}
          type={alertType}
          onClose={onClearAlert}
          actionButton={actionButton}
          onClickAction={onClickAction}
        />
      );
    }
    const locations = _sortBy(_get(member, 'locations', []), ['name']);
    const isAgentUser = AGENT_ROLES.includes(_get(member, 'role'));
    const isDisabled = ticketCategoriesLoading || ticketLoading;
    const isSubmitDisabled = isDisabled || isUploading;
    const hasFormQuestionBlankErrors = () => {
      const questions = _get(ticket, 'ticket_form_questions', []);
      const requireds = _reject(questions, q => !q.required);
      return Boolean(_size(_reject(requireds, r => _size(r.value))));
    };
    const onSubmitTicket = () => {
      if (_isEmpty(locations)) {
        setAlert({
          alertMsg: 'No foi informado o local da ocorrncia',
          alertType: 'error',
          showAlert: true
        });
      } else {
        const hasQuestionErrors = hasFormQuestionBlankErrors();
        setShowQuestionError(hasQuestionErrors);
        if (!hasQuestionErrors) {
          ticketFetch(true);
        }
      }
    };
    const onChangeCategory = (e) => {
      const categoryId = e.target.value;
      setTicketState({
        category_id: categoryId,
        ticket_form_questions: prepareTicketFormQuestions(categories, categoryId)
      });
      setShowQuestionError(false);
    };
    const onChangeQuestionInput = (value, id) => {
      const questions = _get(ticket, 'ticket_form_questions', []);
      const index = _findIndex(questions, t => t.id === id);
      _set(ticket, `ticket_form_questions[${index}].value`, [value]);
      setShowQuestionError(hasFormQuestionBlankErrors());
    };
    const onChangeOptionInput = (option, checked, id) => {
      const questions = _get(ticket, 'ticket_form_questions', []);
      const index = _findIndex(questions, t => t.id === id);
      let { value } = questions[index];
      if (checked) {
        value = value.concat(option);
      } else {
        value = value.filter(o => o !== option);
      }
      _set(ticket, `ticket_form_questions[${index}].value`, value);
      setShowQuestionError(hasFormQuestionBlankErrors());
    };
    const ticketFormQuestions = _get(ticket, 'ticket_form_questions', []);
    return (
      <div className={classes.root}>
        <Header alertBox={alertBox} />
        <section>
          <div className={classes.container}>
            <div className={classes.headerView}>
              <aside>
                <Typography variant="h5">
                  <b>Novo ticket</b>
                </Typography>
              </aside>
            </div>
          </div>
          <br />
          <form className={classes.container}>
            <Paper className={classes.paper}>
              <Typography variant="h6">
                <b>Solicitao</b>
              </Typography>
              <Grid container item xs={12} sm={8} md={6} spacing={2}>
                <Grid item xs={12}>
                  <FormTextField
                    label="No que podemos te ajudar?"
                    errors={_get(errors, 'title', [])}
                    className={classes.input}
                    disabled={isDisabled}
                    placeholder="Ex.: Banheiro entupido, mquina de caf com defeito, etc"
                    value={_get(ticket, 'title', '')}
                    onChange={e => setTicketState({ title: e.target.value })}
                  />
                  {
                    _size(locations) > 1 &&
                      <FormSelectField
                        label="Local"
                        errors={_get(errors, 'location_id', [])}
                        className={classes.input}
                        disabled={isDisabled}
                        value={_get(ticket, 'location_id')}
                        onChange={e => setTicketState({ location_id: e.target.value })}
                        options={locations}
                        optionValueField="id"
                        optionLabelField="name"
                      />
                  }
                  <FormSelectField
                    label="Categoria"
                    errors={_get(errors, 'category_id', [])}
                    className={classes.input}
                    disabled={isDisabled}
                    value={_get(ticket, 'category_id')}
                    onChange={onChangeCategory}
                    options={categories}
                    optionValueField="id"
                    optionLabelField="title"
                  />
                  {_map(ticketFormQuestions, t => (
                    <QuestionInput
                      key={_get(t, 'id')}
                      id={_get(t, 'id')}
                      question={t.question}
                      options={_get(t, 'options')}
                      fieldType={t.fieldType}
                      required={t.required}
                      disabled={isSubmitDisabled}
                      showBlankError={showQuestionError}
                      onChangeQuestion={onChangeQuestionInput}
                      onCheckOption={onChangeOptionInput}
                    />
                  ))}
                  <FormTextField
                    label="Descrio"
                    multiline
                    rows={4}
                    rowsMax={4}
                    errors={_get(errors, 'comments') || _get(errors, 'description') || []}
                    className={classes.input}
                    disabled={isDisabled}
                    placeholder="Descreva mais detalhes sobre esta ocorrncia..."
                    value={_get(ticket, 'description', '')}
                    onChange={e => setTicketState({ description: e.target.value })}
                  />
                </Grid>
              </Grid>
              {
                isAgentUser &&
                  <React.Fragment>
                    <br />
                    <Typography variant="h6" className={classes.deadline}>
                      <b>Deadline</b>
                    </Typography>
                    <Grid container item xs={12} sm={8} md={6} spacing={2}>
                      <Grid item xs={12} sm={6}>
                        <FormControl fullWidth>
                          <MuiPickersUtilsProvider
                            libInstance={moment}
                            utils={MomentUtils}
                            locale={config.locale}
                          >
                            <DatePicker
                              autoOk
                              label="Prazo"
                              clearable
                              animateYearScrolling
                              className={classes.input}
                              disabled={isDisabled}
                              invalidDateMessage="Formato invlido"
                              variant="outlined"
                              inputVariant="outlined"
                              InputLabelProps={{ shrink: true }}
                              value={_get(ticket, 'deadline')}
                              onChange={e => setTicketState({ deadline: e })}
                              format="DD/MM/YYYY"
                              cancelLabel="Cancelar"
                              clearLabel="Limpar"
                            />
                          </MuiPickersUtilsProvider>
                        </FormControl>
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <FormControl fullWidth error={!!errors.priority}>
                          <TextField
                            select
                            label="Prioridade"
                            error={!!errors.priority}
                            className={fullScreen ? classes.priorityIput : ''}
                            disabled={isDisabled}
                            value={_get(ticket, 'priority')}
                            InputLabelProps={{ shrink: true }}
                            SelectProps={{ native: true }}
                            onChange={e => setTicketState({ priority: e.target.value })}
                            variant="outlined"
                          >
                            {_map(PRIORITIES, p => (
                              <option value={p.key} key={p.key}>{p.label}</option>
                            ))}
                          </TextField>
                          {errors.priority && <FormHelperText>{errors.priority}</FormHelperText>}
                        </FormControl>
                      </Grid>
                    </Grid>
                  </React.Fragment>
              }
              <br />
              <Typography variant="h6">
                <b>Mais detalhes</b>
              </Typography>
              <br />
              <Grid container item xs={12} sm={8} md={6} spacing={2}>
                <Grid item xs={12}>
                  <div>
                    <AttachButton
                      value={_get(ticket, 'attachment_data', null)}
                      onChange={file => {
                        setTicketState({ attachment_data: file });
                        setIsUploading(_get(file, 'progressPercent', 100) < 100);
                      }}
                      disabled={isDisabled}
                      accept="image/*,.doc,.docx,.xls,.xlsx,.pdf,.xml"
                    />
                  </div>
                  <div className={classes.actions}>
                    <Button
                      component={Link}
                      to="/tickets"
                      color="default"
                      size="large"
                      disabled={isDisabled}
                      className={classes.cancel}
                    >
                      Voltar
                    </Button>
                    <Button
                      variant="contained"
                      color="primary"
                      size="large"
                      disabled={isSubmitDisabled}
                      onClick={onSubmitTicket}
                    >
                      {isSubmitDisabled ? 'Aguarde...' : 'Enviar'}
                    </Button>
                  </div>
                </Grid>
              </Grid>
            </Paper>
          </form>
        </section>
      </div>
    );
  }
  return (
    <div className={classes.root}>
      <Header />
      <section>
        <div className={classes.container}>
          <Grid container spacing={1}>
            <div className={classes.centerHeaderView}>
              {
                isManager ?
                  <TicketAds />
                  :
                  <React.Fragment>
                    <ErrorIcon className={classes.emptyHeaderIcon} />
                    <b className={classes.emptyHeaderTitle}>
                      Essa conta no possui o servio de tickets
                    </b>
                  </React.Fragment>
              }
            </div>
          </Grid>
        </div>
      </section>
    </div>
  );
});

NewTicket.propTypes = {
  classes: PropTypes.object,
  categories: PropTypes.array,
  ticket: PropTypes.object,
  errors: PropTypes.object,
  ticketFetch: PropTypes.func,
  ticketLoading: PropTypes.bool,
  ticketCategoriesLoading: PropTypes.bool,
  setShowQuestionError: PropTypes.func,
  showQuestionError: PropTypes.bool,
  setTicketState: PropTypes.func,
  alert: PropTypes.object,
  setAlert: PropTypes.func,
  isUploading: PropTypes.bool,
  setIsUploading: PropTypes.func
};

const withTicketCategories = withRequest(TicketCategoriesService.getCategories, {
  name: 'ticketCategories',
  options: ({ setTicketState, setCategories, selectedCategory, selectedLocation }) => ({
    onRequestFinished(data) {
      const member = Session.getUserData();
      const categories = _sortBy(_get(data, 'ticketCategories', []), ['title']);
      const locations = _sortBy(_get(member, 'locations', []), ['name']);
      const categoryId = findForSelectedCategory(categories, selectedCategory);
      const locationId = findForSelectedLocation(locations, selectedLocation);
      setCategories(categories);
      setTicketState({
        category_id: categoryId,
        location_id: locationId,
        ticket_form_questions: prepareTicketFormQuestions(categories, categoryId)
      });
    }
  })
});

const withNewTicket = withRequest(TicketsService.postTicket, {
  name: 'ticket',
  init: false,
  options: ({ ticket, setTicketState, setAlert, categories, history, setErrors }) => ({
    variables: ticket,
    onRequestFinished(data) {
      document.querySelector('form').reset();
      const categoryId = _get(_first(categories), 'id');
      setTicketState({
        ...emptyTicket(),
        category_id: categoryId,
        ticket_form_questions: prepareTicketFormQuestions(categories, categoryId)
      });
      const newTicketId = _get(data, 'ticket.id');
      setAlert({
        alertMsg: 'Ticket registrado com sucesso',
        alertType: 'success',
        showAlert: true,
        actionButton: 'Ver ticket',
        onClickAction: () => history.push(`/tickets/${newTicketId}`)
      });
      setErrors({});
    },
    onRequestError(err) {
      setErrors(_get(err, 'errors', {}));
      setAlert({
        alertMsg: _get(err, 'error') || 'Erro ao registrar esse ticket',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter,
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withState('ticket', 'setTicket', emptyTicket),
  withState('categories', 'setCategories', []),
  withState('showQuestionError', 'setShowQuestionError', false),
  withState('isUploading', 'setIsUploading', false),
  withState('selectedCategory', 'setSelectedCategory'),
  withState('selectedLocation', 'setSelectedLocation'),
  withState('errors', 'setErrors', {}),
  withHandlers({
    setTicketState: ({ setTicket, ticket }) => (attrs) => {
      setTicket({ ...ticket, ...attrs });
    }
  }),
  withTicketCategories,
  withNewTicket,
  lifecycle({
    componentDidMount() {
      const { location, setTicketState, setSelectedCategory, setSelectedLocation } = this.props;
      const query = new URLSearchParams(location.search);
      const utmSource = (query.get('utm_source') || '').toUpperCase();
      setSelectedCategory(query.get('category') || query.get('category_id') || query.get('categoria'));
      setSelectedLocation(query.get('location') || query.get('location_id') || query.get('local'));
      if (QRCODE === utmSource) {
        setTicketState({ source: QRCODE });
      }
    }
  })
)(NewTicket);
