import React, { memo } from 'react';
import { compose } from 'recompose';
import { withRouter } from 'react-router';
import { withStyles } from '@material-ui/core/styles';
import { Grid, Typography, Paper, Button } from '@material-ui/core';
import PropTypes from 'prop-types';

import styles from './styles';

const NoSubscriptionBlock = ({ classes, history }) => (
  <Paper className={classes.tablePaper}>
    <Grid container justify="center" alignItems="flex-start">
      <Grid item xs={12} sm align="center" className={classes.boxPad}>
        <Typography variant="h6">
          Plano atual
        </Typography>
        <Typography variant="h6">
          <b>Essencial (Gratuito)</b>
        </Typography>
        <Button
          variant="contained"
          color="primary"
          size="large"
          className={classes.boxItemText}
          onClick={() => history.push('/assinar-plano')}
        >
          ASSINAR PLANO PROFISSIONAL
        </Button>
      </Grid>
    </Grid>
  </Paper>
);
NoSubscriptionBlock.propTypes = {
  classes: PropTypes.object,
  history: PropTypes.object
};

export default compose(
  memo,
  withStyles(styles, { withTheme: true }),
  withRouter
)(NoSubscriptionBlock);
