import React, { memo } from 'react';
import {
  FormControl, FormGroup, FormControlLabel, FormHelperText, Checkbox
} from '@material-ui/core';
import { isEmpty, isArray, get, includes, filter, concat } from 'lodash';
import PropTypes from 'prop-types';

import styles from './styles';

const FormCheckboxListField = ({
  errors, value = [], onChangeValue = () => {},
  disabled,
  options, optionValueField = 'value', optionLabelField = 'label'
}) => (
  <FormControl fullWidth margin="normal" error={!isEmpty(errors)}>
    <FormGroup>
      {options.map(option => {
        const label = get(option, optionLabelField);
        const optionValue = get(option, optionValueField);
        return (
          <FormControlLabel
            key={optionValue}
            label={label}
            control={
              <Checkbox
                color="primary"
                checked={includes(value, optionValue)}
                disabled={disabled}
                onChange={e => {
                  let newValue;
                  if (e.target.checked) {
                    newValue = concat(value, optionValue);
                  } else {
                    newValue = filter(value, i => i !== optionValue);
                  }
                  onChangeValue(newValue);
                }}
              />
            }
          />
        );
      })}
    </FormGroup>
    {
      !isEmpty(errors) && (
        <FormHelperText style={styles.errors}>
          {isArray(errors) ? errors.join(', ') : errors}
        </FormHelperText>
      )
    }
  </FormControl>
);

FormCheckboxListField.propTypes = {
  errors: PropTypes.any,
  value: PropTypes.array,
  onChangeValue: PropTypes.func,
  disabled: PropTypes.bool,
  options: PropTypes.array,
  optionValueField: PropTypes.string,
  optionLabelField: PropTypes.string
};

export default memo(FormCheckboxListField);
