import * as Sentry from '@sentry/browser';

import config from '../config';
import Session from './session';

class ErrorTracker {
  static setup() {
    if (config.isProduction) {
      Sentry.init({ dsn: config.sentryDsn, environment: config.env });
      Sentry.setExtra('server', config.apiUrl);
      const viewer = this.getViewerData();
      Sentry.setUser(viewer);
    }
  }

  static async capture(error, receivedOpts = {}) {
    if (error) {
      if (config.isProduction) {
        const viewer = this.getViewerData();
        Sentry.setUser(viewer);
        Object.keys(receivedOpts).forEach(k => Sentry.setExtra(k, receivedOpts[k]));
        setTimeout(() => Sentry.captureException(error), 10);
      } else {
        console.warn(error);
      }
    }
  }

  static getViewerData() {
    try {
      const member = Session.getUserData();
      if (member && member.id) {
        const { id, email } = member;
        return { id, email };
      }
      return {};
    } catch {
      return {};
    }
  }
}

export default ErrorTracker;
