import { map, split, compact, get, trim } from 'lodash';
import FieldNormalizer from 'field-normalizer';

import Api from '../utils/api';

const TicketCategories = {
  getCategories(includeDeleteds) {
    let body = {};
    if (includeDeleteds) {
      body = { deleted: includeDeleteds };
    }
    return Api.get('/ticket_categories', body)
      .then(response => FieldNormalizer.toCamelCase(response))
      .catch(error => {
        if (error.errors) error.errors = FieldNormalizer.toCamelCase(error.errors);
        throw error;
      });
  },
  updateCategory({ id, title, assigneeIds, deadlineInDays, ticketFormQuestions }) {
    const body = FieldNormalizer.toSnakeCase({
      id,
      title,
      assigneeIds,
      deadlineInDays,
      ticketFormQuestions: map(ticketFormQuestions, f => ({
        id: f.id,
        question: get(f, 'question'),
        fieldType: get(f, 'fieldType'),
        required: get(f, 'required'),
        options: compact(split(get(f, 'options', ''), ',').map(o => trim(o)))
      }))
    });
    return Api.put(`/ticket_categories/${id}`, body)
      .then(response => FieldNormalizer.toCamelCase(response))
      .catch(error => {
        if (error.errors) error.errors = FieldNormalizer.toCamelCase(error.errors);
        throw error;
      });
  },
  createCategory({ title, deadlineInDays, assigneeIds }) {
    const body = FieldNormalizer.toSnakeCase({ title, deadlineInDays, assigneeIds });
    return Api.post('/ticket_categories', body)
      .then(response => FieldNormalizer.toCamelCase(response))
      .catch(error => {
        if (error.errors) error.errors = FieldNormalizer.toCamelCase(error.errors);
        throw error;
      });
  },
  toggleCategory(id) {
    return Api.put(`/ticket_categories/${id}/toggle_active`);
  },
  getCategoriesByCompany(companyId) {
    if (companyId) {
      return Api.get('/public_ticket_categories', { company_id: companyId });
    }
    return null;
  }
};

export default TicketCategories;
