import React, { useEffect } from 'react';
import Tracker from './tracker';

const withTracker = (WrappedComponent, screen) => {
  const HOC = (props) => {
    useEffect(() => {
      Tracker.trackPageView(screen);
    });
    return <WrappedComponent {...props} />;
  };
  return HOC;
};

export default withTracker;
