import React, { memo } from 'react';
import { get, isEmpty, map, concat, cloneDeep } from 'lodash';
import { compose, withProps, withHandlers } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import { Box, Grid, Button, FormGroup, FormControl, FormControlLabel, Switch } from '@material-ui/core';
import PropTypes from 'prop-types';

import Tracker from '../../../utils/tracker';
import withFormData from '../../../utils/with-form-data';
import withRequest from '../../../utils/with-request';
import TicketsService from '../../../services/tickets';
import TicketsCategoriesService from '../../../services/ticket-categories';

import StandardDialog from '../../../components/StandardDialog';
import { FIELDS_WITH_OPTIONS, FIELDS_FOR_CATEGORIES, TEXT_FIELD } from '../../../enums/fields';
import { FormTextField, FormSelectField, FormNumberField } from '../../../components/FormFields';
import styles from './styles';

const TicketCategoryFormModal = ({
  classes, openModal, formData: {
    validationErrors,
    title, assigneeIds, deadlineInDays, ticketFormQuestions
  }, setFormData, onSubmit, loading, assignables,
  onClose, disableNextButton, onAddFormQuestion, onRemoveFormQuestion, onUpdateFormQuestion,
  isNew
}) => (
  <StandardDialog
    onClose={onClose}
    modalTitle={isNew ? 'Nova categoria' : 'Editar categoria'}
    content={
      <Grid container direction="row" spacing={1}>
        <Grid item xs={12}>
          <FormTextField
            disabled={loading}
            label="Ttulo"
            errors={validationErrors.title}
            value={title || ''}
            onChangeValue={(newVal) => setFormData({ title: newVal })}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormSelectField
            multiple
            label="Responsveis"
            errors={validationErrors.assigneeIds}
            value={assigneeIds}
            disabled={loading}
            onChangeValue={(newVal) => setFormData({ assigneeIds: newVal })}
            options={assignables.map((a) => ({ label: a.fullname, value: a.id }))}
            emptyOptionLabel="Nenhum responsvel"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormNumberField
            label="Prazo de resoluo (em dias)"
            disabled={loading}
            errors={validationErrors.deadlineInDays}
            value={deadlineInDays || ''}
            onChangeValue={(newVal) => setFormData({ deadlineInDays: newVal })}
          />
        </Grid>
        <Grid item xs={12}>
          {
            map(ticketFormQuestions, (fq, i) => (
              <Grid
                container
                direction="row"
                spacing={1}
                key={i.toString()}
              >
                <Grid item xs={2} sm={1}>
                  <FormControl fullWidth margin="normal">
                    <Button
                      onClick={() => onRemoveFormQuestion(i)}
                      variant="contained"
                      disabled={loading}
                      className={classes.deleteVisitorButton}
                    >
                      -
                    </Button>
                  </FormControl>
                </Grid>
                <Grid item xs={10} sm={11}>
                  <FormTextField
                    disabled={loading}
                    label="Ttulo da pergunta"
                    errors={get(validationErrors, `ticketFormQuestions[${i}].question`)}
                    value={fq.question}
                    onChangeValue={newVal => onUpdateFormQuestion('question', newVal, i)}
                  />
                </Grid>
                <Grid item xs={2} sm={1} />
                <Grid item xs={6} sm={8}>
                  <FormSelectField
                    disabled={loading}
                    label="Tipo do campo resposta"
                    errors={get(validationErrors, `ticketFormQuestions[${i}].fieldType`)}
                    value={fq.fieldType}
                    onChangeValue={newVal => onUpdateFormQuestion('fieldType', newVal, i)}
                    options={FIELDS_FOR_CATEGORIES}
                    optionValueField="key"
                  />
                </Grid>
                <Grid item xs={4} sm={3}>
                  <FormGroup className={classes.toggleRequired}>
                    <FormControlLabel
                      control={
                        <Switch
                          color="primary"
                          disabled={loading}
                          checked={!!get(fq, 'required')}
                          value=""
                          onChange={() => onUpdateFormQuestion('required', !fq.required, i)}
                        />
                      }
                      label={<small>Obrigatrio</small>}
                    />
                  </FormGroup>
                </Grid>
                <Grid item xs={2} sm={1} />
                {
                  FIELDS_WITH_OPTIONS.includes(fq.fieldType) &&
                    <Grid item xs={10} sm={11}>
                      <FormTextField
                        disabled={loading}
                        label="Opes de respostas"
                        placeholder="Separe por vrgula, ex: Item1,Item2,Item3"
                        errors={get(validationErrors, `ticketFormQuestions[${i}].options`)}
                        value={fq.options}
                        onChangeValue={newVal => onUpdateFormQuestion('options', newVal, i)}
                      />
                    </Grid>
                }
              </Grid>
            ))
          }
        </Grid>
      </Grid>
    }
    actions={
      <React.Fragment>
        <Box
          style={{ width: '100%' }}
          display="flex"
          justifyContent={isNew ? 'flex-end' : 'space-between'}
        >
          {
            !isNew && (
              <Button
                color="primary"
                size="large"
                variant="outlined"
                disabled={loading}
                onClick={onAddFormQuestion}
              >
                ADICIONAR PERGUNTA
              </Button>
            )
          }
          <Box display="flex">
            <Button
              color="default"
              size="large"
              variant="contained"
              onClick={onClose}
              disabled={loading}
            >
              CANCELAR
            </Button>
            <Box ml={2}>
              <Button
                color="primary"
                size="large"
                variant="contained"
                onClick={onSubmit}
                disabled={disableNextButton}
              >
                SALVAR
              </Button>
            </Box>
          </Box>
        </Box>
      </React.Fragment>
    }
    fullWidth
    open={openModal}
  />
);

TicketCategoryFormModal.propTypes = {
  classes: PropTypes.object,
  openModal: PropTypes.bool,
  onClose: PropTypes.func,
  onSubmit: PropTypes.func,
  loading: PropTypes.bool,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  disableNextButton: PropTypes.bool,
  assignables: PropTypes.array,
  onAddFormQuestion: PropTypes.func,
  onRemoveFormQuestion: PropTypes.func,
  onUpdateFormQuestion: PropTypes.func,
  isNew: PropTypes.bool
};

const withAssignables = withRequest(TicketsService.getAssignables, {
  name: 'assignables',
  props: ({ assignablesData }) => ({ assignables: get(assignablesData, 'members', []) })
});
const withCreate = withRequest(TicketsCategoriesService.createCategory, {
  name: 'create',
  init: false,
  options: async ({
    formData, setFormData, resetFormData, setAlert,
    onClose, refetch
  }) => ({
    variables: formData.changedFields,
    onRequestFinished: () => {
      Tracker.hit('TicketCategory - Created');
      resetFormData();
      refetch(true);
      setAlert({
        alertMsg: 'Categoria criada com sucesso',
        alertType: 'success',
        showAlert: true
      });
      onClose();
    },
    onRequestError: error => {
      setFormData({ validationErrors: get(error, 'errors', {}) });
      setAlert({
        alertMsg: 'Erro ao criar categoria',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});
const withUpdate = withRequest(TicketsCategoriesService.updateCategory, {
  name: 'update',
  init: false,
  options: async ({
    formData, setFormData, resetFormData, setAlert,
    onClose, refetch, ticketCategory
  }) => {
    return {
      variables: {
        ...formData.changedFields,
        ticketFormQuestions: formData.ticketFormQuestions,
        id: ticketCategory.id
      },
      onRequestFinished: () => {
        Tracker.hit('TicketCategory - Updated');
        resetFormData();
        refetch(true);
        setAlert({
          alertMsg: 'Categoria atualizada com sucesso',
          alertType: 'success',
          showAlert: true
        });
        onClose();
      },
      onRequestError: error => {
        setFormData({ validationErrors: get(error, 'errors', {}) });
        setAlert({
          alertMsg: 'Erro ao atualizar categoria',
          alertType: 'error',
          showAlert: true
        });
      }
    };
  }
});

export default compose(
  withStyles(styles, { withTheme: true }),
  memo,
  withFormData((props) => {
    if (props.ticketCategory) return props.ticketCategory;

    return { title: '', assigneeIds: [], deadlineInDays: '', ticketFormQuestions: [] };
  }),
  withHandlers({
    onClose: ({ onClose, resetFormData }) => () => {
      resetFormData();
      onClose();
    }
  }),
  withAssignables,
  withCreate,
  withUpdate,
  withProps(({ formData, assignablesLoading, createLoading, updateLoading, ticketCategory }) => {
    const loading = assignablesLoading || createLoading || updateLoading;
    return {
      loading,
      disableNextButton: (loading || isEmpty(formData.title)),
      isNew: !ticketCategory
    };
  }),
  withHandlers({
    onSubmit: ({ createFetch, updateFetch, ticketCategory }) => () => (
      ticketCategory ? updateFetch(true) : createFetch(true)
    ),
    onAddFormQuestion: ({ setFormData, formData: { ticketFormQuestions } }) => () => {
      const emptyQuestion = { question: '', fieldType: TEXT_FIELD, options: '', required: false };
      setFormData({
        ticketFormQuestions: concat(ticketFormQuestions, emptyQuestion)
      });
    },
    onRemoveFormQuestion: ({ setFormData, formData: { ticketFormQuestions } }) => (index) => {
      ticketFormQuestions.splice(index, 1);
      setFormData({
        ticketFormQuestions: cloneDeep(ticketFormQuestions)
      });
    },
    onUpdateFormQuestion: ({
      setFormData, formData: { ticketFormQuestions }
    }) => (field, value, index) => {
      ticketFormQuestions[index][field] = value;
      setFormData({
        ticketFormQuestions: cloneDeep(ticketFormQuestions)
      });
    }
  })
)(TicketCategoryFormModal);
