import React, { memo } from 'react';
import { get as _get, set as _set, reject as _reject } from 'lodash';
import { compose } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import { Paper, Typography } from '@material-ui/core';
import PropTypes from 'prop-types';

import { USER_MANAGER_ROLES } from '../../../enums/user-roles';
import LocationsService from '../../../services/locations';
import withRequest from '../../../utils/with-request';
import Session from '../../../utils/session';
import withNotAuthorizedHandler from '../../../utils/with-not-authorized-handler';
import withWrapper from '../with-wrapper';
import LocationsTable from './LocationsTable';
import NewLocationButton from './NewLocationButton';
import styles from './styles';

const Locations = memo(({
  locationsData, locationsLoading, locationsFetch, classes, setAlert
}) => {
  return (
    <React.Fragment>
      <div className={classes.headerContainer}>
        <Typography variant="h6">
          <b>{`Locais (${_get(locationsData, 'meta.totalCount', 0)})`}</b>
        </Typography>
        <NewLocationButton
          setAlert={setAlert}
          onCreated={locationsFetch}
        />
      </div>
      <Paper className={classes.tablePaper}>
        <LocationsTable
          setAlert={setAlert}
          locationsData={locationsData}
          locationsLoading={locationsLoading}
          locationsFetch={locationsFetch}
        />
      </Paper>
    </React.Fragment>
  );
});

Locations.propTypes = {
  locationsData: PropTypes.object,
  locationsLoading: PropTypes.bool,
  locationsFetch: PropTypes.func,
  classes: PropTypes.object,
  setAlert: PropTypes.func
};

const withLocations = withRequest(LocationsService.getLocations, {
  name: 'locations',
  options: ({ setAlert }) => ({
    variables: { listInactive: true, listAll: true },
    onRequestFinished: (data) => {
      const locations = _reject(_get(data, 'locations', []), l => _get(l, 'deleted_at'));
      Session.setUserData(_set(Session.getUserData(), 'locations', locations));
    },
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao carregar dados do servidor',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  withWrapper,
  withNotAuthorizedHandler({
    condition: (viewer) => !USER_MANAGER_ROLES.includes(viewer.role)
  }),
  withStyles(styles, { withTheme: true }),
  withLocations
)(Locations);
