import React, { memo } from 'react';
import { get, size } from 'lodash';
import { compose, withState } from 'recompose';
import PropTypes from 'prop-types';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Paper,
  Button,
  Typography
} from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import withRequest from '../../../utils/with-request';
import withNotAuthorizedHandler from '../../../utils/with-not-authorized-handler';
import TicketCategoriesService from '../../../services/ticket-categories';
import { USER_MANAGER_ROLES } from '../../../enums/user-roles';
import Loading from '../../../components/Loading';
import TicketCategoriesItemRow from '../TicketCategoriesItemRow';
import Session from '../../../utils/session';
import Tracker from '../../../utils/tracker';
import TicketCategoryFormModal from '../TicketCategoryFormModal';
import styles from './styles';

const TicketCategoriesTable = memo(({
  ticketCategories, ticketCategoriesLoading, openTicketCategoryModal,
  setOpenTicketCategoryModal, classes, setAlert, ticketCategoriesFetch
}) => {
  const viewer = Session.getUserData();
  const isManager = USER_MANAGER_ROLES.includes(get(viewer, 'role'));
  const isLoading = ticketCategoriesLoading;
  const onOpenNewTicketCategory = () => {
    Tracker.hit('TicketCategory - Opened');
    setOpenTicketCategoryModal(true);
  };
  const onClose = () => {
    Tracker.hit('TicketCategory - Closed');
    setOpenTicketCategoryModal(false);
  };
  const colSpan = isManager ? 4 : 3;
  return (
    <React.Fragment>
      <div className={classes.container}>
        <div className={classes.headerView}>
          <aside>
            <Typography variant="h6">
              <b>{`Categorias (${size(ticketCategories)})`}</b>
            </Typography>
          </aside>
          <aside>
            {
              isManager &&
                <Button
                  size="large"
                  color="primary"
                  variant="contained"
                  onClick={onOpenNewTicketCategory}
                >
                  Nova Categoria
                </Button>
            }
          </aside>
        </div>
      </div>
      <div className={classes.container}>
        <Paper className={classes.tablePaper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell className={classes.tableCell}>TTULO</TableCell>
                <TableCell className={classes.tableCell}>RESPONSVEIS</TableCell>
                <TableCell className={classes.tableCell}>PRAZO DE RESOLUO</TableCell>
                {isManager && <TableCell className={classes.tableCell}>AES</TableCell>}
              </TableRow>
            </TableHead>
            <TableBody>
              {
                isLoading ?
                  <TableRow>
                    <TableCell colSpan={colSpan} align="center">
                      <Loading />
                    </TableCell>
                  </TableRow>
                  :
                  (
                    size(ticketCategories) ?
                      (ticketCategories.map((tc, i) => {
                        return (
                          <TicketCategoriesItemRow
                            isManager={isManager}
                            setAlert={setAlert}
                            index={i}
                            key={tc.id}
                            ticketCategory={tc}
                            isLoading={isLoading}
                            refetch={ticketCategoriesFetch}
                          />
                        );
                      }))
                      :
                      <TableRow>
                        <TableCell className={classes.empty} colSpan={colSpan} align="center">
                          Nenhuma categoria cadastrada
                        </TableCell>
                      </TableRow>
                  )
              }
              <TicketCategoryFormModal
                onClose={onClose}
                openModal={(isManager && openTicketCategoryModal)}
                setAlert={setAlert}
                refetch={ticketCategoriesFetch}
              />
            </TableBody>
          </Table>
        </Paper>
      </div>
    </React.Fragment>
  );
});
TicketCategoriesTable.propTypes = {
  ticketCategoriesLoading: PropTypes.bool,
  ticketCategories: PropTypes.array,
  classes: PropTypes.object,
  openTicketCategoryModal: PropTypes.bool,
  setOpenTicketCategoryModal: PropTypes.func,
  ticketCategoriesFetch: PropTypes.func,
  setAlert: PropTypes.func
};

const withTicketCategories = withRequest(TicketCategoriesService.getCategories, {
  name: 'ticketCategories',
  props: ({ ticketCategoriesData }) => ({
    ticketCategories: get(ticketCategoriesData, 'ticketCategories')
  }),
  options: ({ setAlert }) => ({
    variables: false,
    onRequestError: () => {
      setAlert({ alertMsg: 'Erro ao carregar categorias', alertType: 'error', showAlert: true });
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('openTicketCategoryModal', 'setOpenTicketCategoryModal', false),
  withNotAuthorizedHandler({
    condition: (viewer) => !USER_MANAGER_ROLES.includes(viewer.role)
  }),
  withTicketCategories
)(TicketCategoriesTable);
