import FieldNormalizer from 'field-normalizer';
import _first from 'lodash/first';
import _last from 'lodash/last';

import Api from '../utils/api';

const Subscriptions = {
  async get() {
    return Api.get('/subscription')
      .then(response => FieldNormalizer.toCamelCase(response));
  },
  async create({
    phone, cnpj, addressZipcode, addressNumber, addressComplementary,
    planName, planSeats, creditCardHash, creditCardId
  }) {
    const phoneData = (phone || '').split(' ');
    const body = {
      phoneDdd: _first(phoneData).replace(/[\D]+/g, ''),
      phoneNumber: _last(phoneData).replace(/[\D]+/g, ''),
      cnpj: (cnpj || '').replace(/[\D]+/g, ''),
      addressZipcode: (addressZipcode || '').replace(/[\D]+/g, ''),
      addressNumber,
      addressComplementary,
      planName,
      planSeats,
      creditCardHash,
      creditCardId
    };
    const normalizedParams = FieldNormalizer.toSnakeCase(body);
    return Api.post('/subscriptions', normalizedParams)
      .then(response => FieldNormalizer.toCamelCase(response));
  },
  async update({ id, planName, planSeats, creditCardHash, creditCardId }) {
    const normalizedParams = FieldNormalizer.toSnakeCase(
      { planName, planSeats, creditCardHash, creditCardId }
    );
    return Api.put(`/subscriptions/${id}`, normalizedParams)
      .then(response => FieldNormalizer.toCamelCase(response));
  },
  async cancel(id) {
    return Api.delete(`/subscriptions/${id}`)
      .then(response => FieldNormalizer.toCamelCase(response));
  },
  async undoCancelation() {
    return Api.put('/subscription/undo_cancelation/')
      .then(response => FieldNormalizer.toCamelCase(response));
  }
};

export default Subscriptions;
