import mixpanel from 'mixpanel-browser';
import _merge from 'lodash/merge';
import _get from 'lodash/get';
import Session from './session';
import config from '../config';

mixpanel.init(config.mixpanelToken);

const Tracker = {
  prepareTrackUser() {
    const member = Session.getUserData();
    if (member && member.id) {
      const trackUser = {
        app_id: config.appNamespace,
        app_name: config.appName,
        user_id: _get(member, 'id'),
        email: _get(member, 'email'),
        phone: _get(member, 'phone'),
        name: _get(member, 'fullname'),
        company: _get(member, 'company.name'),
        company_id: _get(member, 'company_id'),
        created_at: _get(member, 'created_at'),
        verified_at: _get(member, 'verified_at')
      };
      mixpanel.identify(trackUser.user_id);
      mixpanel.people.set(trackUser);
      return trackUser;
    }
    return {};
  },
  hit(event, props) {
    if (config.isProduction) {
      const trackUser = this.prepareTrackUser();
      const trackProps = _merge(props, trackUser);
      return mixpanel.track(event, trackProps);
    }
    return console.info(event);
  },
  trackPageView(screen) {
    this.hit(`${screen} - Viewed`, { screen });
  }
};

export default Tracker;
