import React from 'react';
import PropTypes from 'prop-types';

import FormTextField from './FormTextField';

const maskZipcode = (value) => {
  if (value == null) return null;

  const regex = /(\d{0,5})-?(\d{0,3})/;
  // eslint-disable-next-line
  const [_, first, second] = regex.exec(value);
  if (second === '') {
    return first;
  }

  return `${first}-${second}`;
};

const FormZipCodeField = ({
  value, placeholder = '00000-000', ...otherProps
}) => (
  <FormTextField
    value={value || ''}
    maxLength={9}
    maskFunction={newValue => maskZipcode(newValue)}
    type="tel"
    placeholder={placeholder}
    {...otherProps}
  />
);

FormZipCodeField.propTypes = {
  value: PropTypes.string,
  placeholder: PropTypes.string
};

export default FormZipCodeField;
