import React from 'react';
import PropTypes from 'prop-types';

import FormTextField from './FormTextField';

const decimalSeparator = ',';

const maskNumber = ({ oldValue, newValue, min, max, isInteger }) => {
  if (newValue === null) return null;

  const newValueNumber = Number.parseFloat(newValue);
  if ((min && newValueNumber < min) || (max && newValueNumber > max)) return oldValue;

  const regex = isInteger ? /\d+/ : new RegExp(`\\d+(?:${decimalSeparator}\\d*)?`);
  const regexResult = regex.exec(newValue);
  if (!regexResult) return regexResult;

  return regexResult[0];
};

const FormNumberField = ({
  value, min, max, isInteger = false,
  onChangeText, onChangeValue = () => {}, ...otherProps
}) => (
  <FormTextField
    value={value || ''}
    maskFunction={newValue => maskNumber({ oldValue: value, newValue, min, max, isInteger })}
    inputProps={{ inputMode: isInteger ? 'numeric' : 'decimal' }}
    onChangeValue={newValue => onChangeValue(Number.parseFloat(newValue))}
    onChangeText={onChangeText}
    {...otherProps}
  />
);

FormNumberField.propTypes = {
  value: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  min: PropTypes.number,
  max: PropTypes.number,
  isInteger: PropTypes.bool,
  onChangeText: PropTypes.func,
  onChangeValue: PropTypes.func
};

export default FormNumberField;
