/* global document */
import 'moment/locale/pt-br';
import React, { Suspense, PureComponent } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import _get from 'lodash/get';
import _map from 'lodash/map';
import _flatten from 'lodash/flatten';
import moment from 'moment';
import CssBaseline from '@material-ui/core/CssBaseline';
import { MuiThemeProvider } from '@material-ui/core/styles';
import MemberService from './services/member';
import Session from './utils/session';
import withTracker from './utils/with-tracker';
import Store from './utils/store';
import ErrorTracker from './utils/error-tracker';
import withErrorBoundary from './utils/with-error-boundary';
import Loading from './components/Loading';
import theme from './theme';
import config from './config';
import Routes from './routes';
import * as Pages from './pages';

ErrorTracker.setup();
moment.locale(config.locale);

const flatRoutes = _flatten(_map(Routes, (r, rkey) => {
  if (r.submenus) {
    return _map(r.submenus, (rs, rskey) => ({
      ...rs,
      path: `${r.path}${rs.path}`,
      key: rskey,
      page: Pages[rskey]
    }));
  }
  return { ...r, key: rkey, page: Pages[rkey] };
}));

class AppBase extends PureComponent {
  state = {
    loading: true
  }

  componentDidMount() {
    this.setIOSManifest();
    this.setState({ loading: true }, async () => {
      try {
        if (Session.isLoggedIn()) {
          const response = await MemberService.getViewer();
          Session.setUserData(_get(response, 'member'));
        }
      } catch (err) {
        if (Session.isUnauthorized(err)) {
          Session.clear();
        }
      } finally {
        this.setState({ loading: false });
      }
    });
  }

  setIOSManifest() {
    if (config.isiOS) {
      document.querySelector('link[rel="manifest"]').setAttribute('rel', 'no-on-ios');
    }
  }

  render() {
    const { loading } = this.state;
    return (
      <MuiThemeProvider theme={theme}>
        <CssBaseline />
        <Suspense fallback={<Loading hasRefreshMsg />}>
          {
            loading ?
              <Loading hasRefreshMsg />
              :
              <React.Fragment>
                <Switch>
                  {
                    _map(flatRoutes, (r) => (
                      <Route
                        key={r.path}
                        exact={r.exact}
                        path={r.path}
                        render={(p) => {
                          try {
                            if ((r.private && Session.isLoggedIn()) || !r.private) {
                              const TrackedPage = withTracker(r.page, r.trackPage);
                              return <TrackedPage {...p} />;
                            }
                            const redirectUrl = `${p.location.pathname}${p.location.search}`;
                            Store.save('redirectUrl', redirectUrl);
                            return <Redirect to={{ pathname: '/', state: { from: p.location } }} />;
                          } catch {
                            return <Redirect to={{ pathname: '/', state: { from: p.location } }} />;
                          }
                        }}
                      />
                    ))
                  }
                </Switch>
              </React.Fragment>
          }
        </Suspense>
      </MuiThemeProvider>
    );
  }
}

const App = withErrorBoundary(AppBase);
export default App;
