import React from 'react';
import { FormControl, FormHelperText, TextField } from '@material-ui/core';
import { isEmpty, isArray, size } from 'lodash';
import PropTypes from 'prop-types';

import styles from './styles';

const FormTextField = ({
  errors, variant = 'outlined', InputLabelProps = { shrink: true },
  maskFunction, value, onChangeValue = () => {}, onChangeText = () => {},
  margin = 'normal',
  ...otherProps
}) => {
  otherProps = variant !== 'standard' ? { ...otherProps, variant } : otherProps;
  return (
    <FormControl fullWidth margin={margin} error={!isEmpty(errors)}>
      <TextField
        error={!isEmpty(errors)}
        InputLabelProps={InputLabelProps}
        value={value}
        onChange={event => {
          const newValue = event.target.value;
          if (!maskFunction || (value && size(newValue) < size(value))) {
            onChangeValue(newValue);
            onChangeText(newValue);
            return null;
          }

          const handledNewValue = maskFunction(newValue);
          onChangeValue(handledNewValue);
          onChangeText(handledNewValue);
          return null;
        }}
        {...otherProps}
      />
      {!isEmpty(errors) &&
        <FormHelperText style={styles.errors}>
          {isArray(errors) ? errors.join(', ') : errors}
        </FormHelperText>}
    </FormControl>
  );
};

FormTextField.propTypes = {
  errors: PropTypes.any,
  variant: PropTypes.string,
  InputLabelProps: PropTypes.shape({ shrink: PropTypes.bool }),
  maskFunction: PropTypes.func,
  value: PropTypes.any,
  onChangeValue: PropTypes.func,
  onChangeText: PropTypes.func,
  margin: PropTypes.string,
  className: PropTypes.any
};

export default FormTextField;
