import React, { memo } from 'react';
import { compose, withState } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';

import withErrorBoundary from '../../utils/with-error-boundary';
import Header from '../Header';
import AlertBox from '../AlertBox';
import styles from './styles';

const MainLayoutBase = memo(({
  classes, alert, setAlert, children
}) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }

  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} />
      <section className={classes.container}>
        { React.cloneElement(children, { setAlert }) }
      </section>
    </div>
  );
});

MainLayoutBase.propTypes = {
  children: PropTypes.object,
  classes: PropTypes.object,
  alert: PropTypes.object,
  setAlert: PropTypes.func
};

const MainLayout = compose(
  withStyles(styles, { withTheme: true }),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  })
)(MainLayoutBase);

const withMainLayout = WrappedComponent => props => {
  const EnhancedWrappedComponent = withErrorBoundary(WrappedComponent);
  return (
    <MainLayout {...props}>
      <EnhancedWrappedComponent {...props} />
    </MainLayout>
  );
};

export {
  MainLayout, withMainLayout
};
