import React, { memo } from 'react';
import { size as _size, map as _map } from 'lodash';
import { compose } from 'recompose';
import PropTypes from 'prop-types';
import { useTheme, withStyles } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Paper
} from '@material-ui/core';
import Loading from '../../../components/Loading';
import MyRequestsItemRow from './MyRequestsItemRow';
import styles from './styles';

const MyRequestsTable = memo(({ classes, serviceRequests, serviceRequestsLoading }) => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('md'));
  const maxColSpan = isSmallScreen ? 1 : 3;
  const loading = serviceRequestsLoading;
  return (
    <React.Fragment>
      <div>
        <Paper>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>SERVIO</TableCell>
                { !isSmallScreen && (
                  <React.Fragment>
                    <TableCell className={classes.tableLargeCell}>SOLICITANTE</TableCell>
                    <TableCell className={classes.tableLargeCell} />
                  </React.Fragment>
                )}
              </TableRow>
            </TableHead>
            <TableBody>
              {
                loading ?
                  <TableRow>
                    <TableCell colSpan={maxColSpan} align="center">
                      <Loading />
                    </TableCell>
                  </TableRow>
                  :
                  (
                    _size(serviceRequests) ?
                      _map(serviceRequests, (serviceRequest, i) => {
                        return (
                          <MyRequestsItemRow
                            serviceRequest={serviceRequest}
                            index={i}
                            isSmallScreen={isSmallScreen}
                            key={serviceRequest.id}
                          />
                        );
                      })
                      :
                      <TableRow>
                        <TableCell colSpan={maxColSpan} align="center">
                          Nenhum servio solicitado
                        </TableCell>
                      </TableRow>
                  )
              }
            </TableBody>
          </Table>
        </Paper>
      </div>
    </React.Fragment>
  );
});

MyRequestsTable.propTypes = {
  classes: PropTypes.object,
  serviceRequests: PropTypes.array,
  serviceRequestsLoading: PropTypes.bool
};

export default compose(
  withStyles(styles, { withTheme: true })
)(MyRequestsTable);
