import React, { memo } from 'react';
import { compose } from 'recompose';
import moment from 'moment';
import PropTypes from 'prop-types';
import { get as _get, find as _find } from 'lodash';
import { TableCell, TableRow } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import Formatter from '../../../../../utils/formatter';
import PAYMENT_TYPES from '../../../../../enums/invoice';
import INVOICE_STATUSES from '../../../../../enums/invoice-statuses';
import styles from './styles';

const InvoicesItemRow = ({ index, classes, invoice }) => {
  const paymentTypeTranslated = _get(_find(
    PAYMENT_TYPES, r => r.key === invoice.payment_type
  ), 'label');
  const statusTranslated = _get(_find(
    INVOICE_STATUSES, r => r.value === invoice.status
  ), 'label');
  const createAt = moment(invoice.created_at).format('DD/MM/YYYY');

  return (
    <TableRow className={index % 2 ? classes.bgWhite : classes.bgGray}>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{invoice.description}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{Formatter.toCurrency(invoice.amount)}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={`${classes.cellText}`}>{paymentTypeTranslated}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        {
          invoice.boleto_link && (
            <a
              href={invoice.boleto_link}
              className={classes.cellText}
              target="_blank"
              rel="noopener noreferrer"
            >
              Ver boleto
            </a>
          )
        }
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{statusTranslated}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{createAt}</span>
      </TableCell>
    </TableRow>
  );
};

InvoicesItemRow.propTypes = {
  index: PropTypes.number,
  invoice: PropTypes.object,
  classes: PropTypes.object
};

export default compose(
  memo,
  withStyles(styles, { withTheme: true })
)(InvoicesItemRow);
