import Api from '../utils/api';

const TicketComments = {
  getComments(ticketId) {
    if (ticketId) {
      return Api.get(`/tickets/${ticketId}/ticket_comments`);
    }
    return null;
  },
  sendComment({ id, content, isInternal, attachmentData: receivedAttachmentData }) {
    let attachmentData = null;
    if (receivedAttachmentData) {
      attachmentData = {
        id: receivedAttachmentData.id,
        storage: receivedAttachmentData.storage,
        metadata: receivedAttachmentData.metadata,
        base64Data: receivedAttachmentData.base64Data
      };
    }
    return Api.post(`/tickets/${id}/ticket_comments`, {
      content, is_internal: isInternal, attachment_data: attachmentData
    });
  },
  deleteComment({ id }) {
    return Api.delete(`/ticket_comments/${id}`);
  }
};

export default TicketComments;
