import { compose, withState, withProps, withHandlers, lifecycle } from 'recompose';
import { pickBy, isEqual } from 'lodash';

const withFormData = (
  initialFieldValuesFunc
) => {
  return compose(
    withProps(props => {
      const previousValues = initialFieldValuesFunc(props);
      return { previousValues };
    }),
    withState('formData', 'setFormDataRaw', ({ previousValues }) => ({
      validationErrors: {},
      changedFields: {},
      ...previousValues
    })),
    withHandlers({
      setFormData: ({ setFormDataRaw, formData, previousValues }) => (attrs) => {
        const updatedFormData = { ...formData, ...attrs };
        const {
          validationErrors,
          changedFields: oldChangedFields,
          ...formFields
        } = updatedFormData;

        const changedFields = pickBy(formFields, (value, key) => {
          const previousVal = previousValues[key];
          return !isEqual(previousVal, value);
        });

        setFormDataRaw({
          ...updatedFormData,
          changedFields
        });
      },
      resetFormData: ({ setFormDataRaw, previousValues }) => () => {
        setFormDataRaw({
          validationErrors: {},
          changedFields: {},
          ...previousValues
        });
      }
    }),
    lifecycle({
      componentDidUpdate(prevProps) {
        if (this.props.previousValues !== prevProps.previousValues) {
          this.props.setFormData({ ...this.props.previousValues });
        }
      }
    })
  );
};

export default withFormData;
