import React, { memo } from 'react';
import { compose, withState, withPropsOnChange } from 'recompose';
import PropTypes from 'prop-types';
import { get as _get, find as _find, map as _map } from 'lodash';
import { TableCell, TableRow } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import AvatarImage from '../../../components/AvatarImage';
import MemberFormModal from '../MemberFormModal';
import { ALL_ROLES, USER_MANAGER_ROLES } from '../../../enums/user-roles';
import MembersService from '../../../services/members';
import withRequest from '../../../utils/with-request';
import withViewerData from '../../../utils/with-viewer-data';
import Tracker from '../../../utils/tracker';
import Session from '../../../utils/session';
import styles from './styles';
import UnblockMemberButton from './UnblockMemberButton';

const ResendButtonBase = ({
  classes, canResendInivitation, setCanResendInivitation, setAlert, member
}) => {
  const onResendInivitation = async () => {
    try {
      setCanResendInivitation(false);
      await MembersService.resendInvitationEmail(member.id);
      Tracker.hit('NewMemberInvitation - Resend');
      setAlert({ alertMsg: 'Convite enviado com sucesso', alertType: 'success', showAlert: true });
    } catch {
      setAlert({ alertMsg: 'Erro ao enviar email', alertType: 'error', showAlert: true });
    }
  };
  return (
    <React.Fragment>
      <Button
        className={classes.smallScreenResendButton}
        variant="outlined"
        size="large"
        onClick={onResendInivitation}
        disabled={!canResendInivitation}
      >
        {canResendInivitation ? 'Reenviar convite' : 'Convite enviado'}
      </Button>
    </React.Fragment>
  );
};

ResendButtonBase.propTypes = {
  classes: PropTypes.object,
  setAlert: PropTypes.func,
  canResendInivitation: PropTypes.bool,
  setCanResendInivitation: PropTypes.func,
  member: PropTypes.object
};

const ResendButton = compose(
  withState('openModal', 'setOpenModal', false),
  withState('canResendInivitation', 'setCanResendInivitation', true)
)(ResendButtonBase);

const EditButtonBase = ({
  memberData, memberLoading, refetch,
  openModal, setOpenModal, setAlert
}) => {
  const member = _get(memberData, 'member');
  const onClick = () => {
    Tracker.hit('MembersFormModal - Opened');
    setOpenModal(true);
  };
  const onClose = async ({ shouldRefetch }) => {
    if (shouldRefetch) {
      await refetch(false);
    }
    Tracker.hit('MembersFormModal - Closed');
    setOpenModal(false);
  };
  return (
    <React.Fragment>
      <Button
        variant="outlined"
        size="large"
        onClick={onClick}
      >
        Editar
      </Button>
      {
        openModal && (
          <MemberFormModal
            isLoading={memberLoading}
            member={member}
            open={openModal}
            onClose={onClose}
            setAlert={setAlert}
          />
        )
      }
    </React.Fragment>
  );
};

EditButtonBase.propTypes = {
  memberData: PropTypes.object,
  memberLoading: PropTypes.bool,
  refetch: PropTypes.func,
  openModal: PropTypes.bool,
  setOpenModal: PropTypes.func,
  setAlert: PropTypes.func
};

const withMember = withRequest(MembersService.getMember, {
  name: 'member',
  init: false,
  options: ({ memberId }) => ({
    variables: memberId
  })
});

const EditButton = compose(
  withState('openModal', 'setOpenModal', false),
  withMember,
  withPropsOnChange(['openModal'], ({ openModal, memberFetch }) => {
    if (openModal) {
      memberFetch(true);
    }
  })
)(EditButtonBase);

const MembersItemRow = memo(({
  viewer, index, member, classes, refetch, setAlert
}) => {
  if (!viewer) return null;
  const viewerId = _get(Session.getUserData(), 'id');
  const userRole = _get(_find(ALL_ROLES, r => r.value === _get(member, 'role')), 'label');
  const memberId = _get(member, 'id');
  const memberFullName = `${_get(member, 'name')} ${_get(member, 'lastname')}`;
  const memberEmail = _get(member, 'email');
  const memberPhotoURL = _get(member, 'photo_url');
  const memberMustChangePassword = _get(member, 'must_change_password', false);
  const memberBlockedAt = _get(member, 'blocked_at');
  const locations = _get(member, 'locations');
  const locationsNames = _map(locations, l => l.name).join(', ');
  const canEdit = USER_MANAGER_ROLES.includes(viewer.role);
  const memberCanReceiveNewInvitation = viewerId !== memberId && memberMustChangePassword;
  return (
    <TableRow className={index % 2 ? classes.bgWhite : classes.bgGray}>
      <TableCell className={classes.tableSmallCell}>
        <div className={classes.avatarNameContainer}>
          <AvatarImage name={memberFullName} photoUrl={memberPhotoURL} />
          <span className={`${classes.cellText} ${classes.userName}`}>
            {memberFullName}
          </span>
        </div>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{memberEmail}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{userRole}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{locationsNames}</span>
      </TableCell>
      <TableCell className={classes.tableMediumCell}>
        <Grid container item>
          <Grid item xs={12}>
            {
              memberBlockedAt &&
                <UnblockMemberButton
                  memberId={member.id}
                  refetch={refetch}
                  setAlert={setAlert}
                />
            }
          </Grid>
          <Grid item xs={12}>
            {
              canEdit && !memberBlockedAt &&
                <EditButton
                  memberId={member.id}
                  refetch={refetch}
                  setAlert={setAlert}
                />
            }
          </Grid>
          <Grid item xs={12}>
            { memberCanReceiveNewInvitation && !memberBlockedAt &&
              <ResendButton
                member={member}
                refetch={refetch}
                setAlert={setAlert}
                classes={classes}
              />}
          </Grid>
        </Grid>
      </TableCell>
    </TableRow>
  );
});

MembersItemRow.propTypes = {
  index: PropTypes.number,
  member: PropTypes.object,
  viewer: PropTypes.object,
  classes: PropTypes.object,
  refetch: PropTypes.func,
  setAlert: PropTypes.func
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withViewerData
)(MembersItemRow);
