import React, { memo } from 'react';
import { map, get, sumBy, max, isEmpty } from 'lodash';
import { compose, withHandlers } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import {
  Typography, Table, TableBody, TableRow, TableCell
} from '@material-ui/core';
import PropTypes from 'prop-types';

import Formatter from '../../../utils/formatter';
import { FormNumberWithButtonsField } from '../../../components/FormFields';
import styles from './styles';

const SimpleItemGrid = ({ item, classes }) => (
  <TableRow>
    <TableCell className={classes.tableMediumCell}>
      <Typography variant="body1">{item.title}</Typography>
      {
        get(item, 'description', '').split('\n').map(line => (
          <div key={line}>
            <Typography variant="body1">
              <small>{line}</small>
            </Typography>
          </div>
        ))
      }
    </TableCell>
    <TableCell className={classes.priceCell}>
      <Typography variant="h6" component="span">
        <b>{Formatter.toCurrency(item.value)}</b>
      </Typography>
    </TableCell>
  </TableRow>
);

SimpleItemGrid.propTypes = {
  item: PropTypes.object,
  classes: PropTypes.object
};

const ItemGrid = ({ item, disabled, classes, onAmountChange }) => (
  <TableRow>
    <TableCell className={classes.tableMediumCell}>
      <Typography variant="body1">
        {item.title}
        &nbsp;-&nbsp;
        <b>{Formatter.toCurrency(item.unitPrice)}</b>
      </Typography>
      {
        get(item, 'description', '').split('\n').map(line => (
          <div key={line}>
            <Typography variant="body1">
              <small>{line}</small>
            </Typography>
          </div>
        ))
      }
    </TableCell>
    <TableCell className={classes.numberFieldCell}>
      {
        !item.disableAmount && (
          <FormNumberWithButtonsField
            value={item.amount}
            disabled={disabled}
            onChangeText={onAmountChange}
            min={1}
          />
        )
      }
    </TableCell>
  </TableRow>
);

ItemGrid.propTypes = {
  item: PropTypes.object,
  disabled: PropTypes.bool,
  classes: PropTypes.object,
  onAmountChange: PropTypes.func
};

const CheckoutSummary = ({
  classes, isDowngrade, formData: { items, refundItems }, nextCharge, disabled, setItemAmount
}) => {
  const total = isDowngrade ? 0 : max([
    0, sumBy(items, i => i.unitPrice * i.amount) - sumBy(refundItems, i => i.value)
  ]);
  return (
    <React.Fragment>
      <Typography variant="h6">
        <b>Sumrio</b>
      </Typography>
      <Table>
        <TableBody>
          {
            map(items, (item, index) => (
              <ItemGrid
                key={`#${index}${item.title}`}
                item={item}
                disabled={disabled}
                classes={classes}
                onAmountChange={newVal => setItemAmount(index, newVal)}
              />
            ))
          }
          {
            !isEmpty(nextCharge) && (
              <SimpleItemGrid item={nextCharge} classes={classes} />
            )
          }
          {
            !isDowngrade && map(refundItems, (item, index) => (
              <SimpleItemGrid
                key={`#${index}${item.title}`}
                item={{ title: item.title, value: (item.value * -1) || 0 }}
                classes={classes}
              />
            ))
          }
          <SimpleItemGrid
            item={{
              title: 'Valor cobrado hoje',
              description: isDowngrade ? '* Ao reduzir lugares, o plano ser atualizado na prxima cobrana.' : '',
              value: total
            }}
            classes={classes}
          />
        </TableBody>
      </Table>
    </React.Fragment>
  );
};

CheckoutSummary.propTypes = {
  classes: PropTypes.object,
  nextCharge: PropTypes.object,
  isDowngrade: PropTypes.bool,
  formData: PropTypes.shape({
    items: PropTypes.arrayOf(PropTypes.shape({
      title: PropTypes.string,
      description: PropTypes.string,
      unitPrice: PropTypes.number,
      amount: PropTypes.number
    })),
    refundItems: PropTypes.arrayOf(PropTypes.shape({
      title: PropTypes.string,
      description: PropTypes.string,
      value: PropTypes.number
    }))
  }),
  disabled: PropTypes.bool,
  setItemAmount: PropTypes.func
};

export default compose(
  memo,
  withStyles(styles, { withTheme: true }),
  withHandlers({
    setItemAmount: ({ formData: { items }, setFormData }) => (index, newAMount) => {
      const preItem = items.slice(0, index);
      const updatedItem = { ...items[index], amount: newAMount };
      const postItem = items.slice(index + 1, items.length - 1);
      setFormData({ items: preItem.concat(updatedItem).concat(postItem) });
    }
  })
)(CheckoutSummary);
