import React, { memo } from 'react';
import { compose, withState } from 'recompose';
import { Box, Button, Collapse, IconButton } from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import { Alert } from '@material-ui/lab';
import { withRouter } from 'react-router';
import PropTypes from 'prop-types';
import moment from 'moment';
import Routes from '../../routes';

const trialExpirationWarning = (trialExpirationDays) => {
  if (trialExpirationDays > 1) {
    return `O plano trial expira em ${trialExpirationDays} dias.`;
  }
  if (trialExpirationDays === 1) {
    return 'O plano trial expira em 1 dia.';
  }
  return 'O plano trial expira hoje.';
};

const willDowngradeWarning = (willDowngradeDays) => {
  if (willDowngradeDays > 1) {
    return `O plano profissional ter reduo de lugares daqui a ${willDowngradeDays} dias.`;
  }
  if (willDowngradeDays === 1) {
    return 'O plano profissional ter reduo de lugares daqui a 1 dia.';
  }
  return 'O plano profissional ter reduo de lugares hoje.';
};

const willCancelWarning = (willCancelDays) => {
  if (willCancelDays > 1) {
    return `O plano profissional ser cancelado em ${willCancelDays} dias.`;
  }
  if (willCancelDays === 1) {
    return 'O plano profissional ser cancelado em 1 dia.';
  }
  return 'O plano profissional ser cancelado hoje.';
};

const PlanAlert = ({ trialExpiration, willDowngradeAt, willCancelAt, open, setOpen, history }) => {
  const trialExpirationDate = trialExpiration && moment(trialExpiration).startOf('day');
  const downgradeDate = willDowngradeAt && moment(willDowngradeAt).startOf('day');
  const willCancelDate = willCancelAt && moment(willCancelAt).startOf('day');
  const today = moment().startOf('day');
  let alertMsg = null;
  let alertType = null;
  let alertPath = null;
  let alertPathLabel = null;
  if (trialExpirationDate && !downgradeDate && !willCancelDate) {
    const inDays = trialExpirationDate.diff(today, 'days');
    alertMsg = trialExpirationWarning(inDays);
    alertType = 'warning';
    alertPath = Routes.PlanSubscription.path;
    alertPathLabel = 'Assinar plano';
  }
  if (downgradeDate && !willCancelDate && !alertMsg) {
    const inDays = downgradeDate.diff(today, 'days');
    alertMsg = willDowngradeWarning(inDays);
    alertType = 'info';
    alertPath = Routes.MyPlan.path;
    alertPathLabel = 'Saiba mais';
  }
  if (willCancelDate && !alertMsg) {
    const inDays = willCancelDate.diff(today, 'days');
    alertMsg = willCancelWarning(inDays);
    alertType = 'warning';
    alertPath = Routes.PlanSubscription.path;
    alertPathLabel = 'Assinar plano';
  }
  if (alertMsg && alertType) {
    return (
      <Collapse in={open}>
        <Box my={2}>
          <Alert
            severity={alertType}
            action={
              <IconButton color="inherit" size="small" onClick={() => setOpen(false)}>
                <CloseIcon fontSize="inherit" />
              </IconButton>
            }
          >
            {alertMsg}
            &nbsp;
            <Button
              onClick={() => history.push(alertPath)}
              color="inherit"
              variant="outlined"
              size="small"
            >
              {alertPathLabel}
            </Button>
          </Alert>
        </Box>
      </Collapse>
    );
  }
  return null;
};

PlanAlert.propTypes = {
  trialExpiration: PropTypes.any,
  willDowngradeAt: PropTypes.any,
  willCancelAt: PropTypes.any,
  open: PropTypes.bool,
  setOpen: PropTypes.func,
  history: PropTypes.object
};

export default compose(
  memo,
  withState('open', 'setOpen', true),
  withRouter
)(PlanAlert);
