/* global window */
import React, { memo } from 'react';
import { compose, withState } from 'recompose';
import _get from 'lodash/get';
import {
  Grid, IconButton, Input, InputLabel, Tooltip,
  InputAdornment, FormControl, Typography
} from '@material-ui/core';
import AssignmentIcon from '@material-ui/icons/Assignment';
import AssignmentTurnedInIcon from '@material-ui/icons/AssignmentTurnedIn';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import QRCode from 'qrcode.react';
import PropTypes from 'prop-types';
import Session from '../../../../utils/session';
import StandardDialog from '../../../../components/StandardDialog';

const { host, protocol } = window.location;
const publicNewTicketsURL = `${protocol}//${host}/#/public/tickets/novo`;

const PublicNewTicketLinkModalBase = ({
  isModalOpen, onClose, setCopyURL, copyURL, setAlert
}) => {
  const handleCopyURLToClipboard = () => {
    setCopyURL(true);
    setAlert({
      alertMsg: 'Link copiado com sucesso',
      alertType: 'success',
      showAlert: true
    });
    setTimeout(() => setCopyURL(false), 3000);
  };
  const member = Session.getUserData();
  const formURL = `${publicNewTicketsURL}?c=${_get(member, 'company.id')}`;
  return (
    isModalOpen && (
      <StandardDialog
        open={isModalOpen}
        onClose={onClose}
        modalTitle="Formulrio pblico de novo ticket"
        content={
          <Grid container direction="row" justify="center" align="center" spacing={2}>
            <Grid item xs={12}>
              <Typography variant="body2" color="textSecondary">
                Cdigo QR do formulrio (no requer login do usurio)
              </Typography>
              <br />
              <QRCode value={formURL} size={256} />
              <br />
            </Grid>
            <Grid item xs={12}>
              <CopyToClipboard text={formURL} onCopy={handleCopyURLToClipboard}>
                <FormControl fullWidth>
                  <InputLabel htmlFor="url">Link do formulrio (no requer login do usurio)</InputLabel>
                  <Input
                    id="url"
                    type="text"
                    readOnly
                    value={formURL}
                    endAdornment={
                      <InputAdornment position="end">
                        <Tooltip title="Copiar link" arrow placement="top">
                          <IconButton onClick={handleCopyURLToClipboard}>
                            {copyURL ?
                              <AssignmentTurnedInIcon color="primary" />
                              :
                              <AssignmentIcon />}
                          </IconButton>
                        </Tooltip>
                      </InputAdornment>
                    }
                  />
                </FormControl>
              </CopyToClipboard>
            </Grid>
          </Grid>
        }
      />
    )
  );
};

PublicNewTicketLinkModalBase.propTypes = {
  isModalOpen: PropTypes.bool,
  onClose: PropTypes.func,
  copyURL: PropTypes.bool,
  setCopyURL: PropTypes.func,
  setAlert: PropTypes.func
};

const PublicNewTicketLinkModal = compose(
  memo,
  withState('copyURL', 'setCopyURL', false)
)(PublicNewTicketLinkModalBase);

export default PublicNewTicketLinkModal;
