/* global FileReader, FormData */

import React, { memo } from 'react';
import { map as _map, merge as _merge } from 'lodash';
import { Button } from '@material-ui/core';
import { AttachFile as AttachFileIcon } from '@material-ui/icons';
import PropTypes from 'prop-types';

const parseFileFormat = (file) => {
  return {
    metadata: {
      size: file.size,
      filename: file.name,
      mime_type: file.type
    },
    originalFileObj: file
  };
};

const getFilesData = (files) => {
  const promises = _map(files, file => {
    const parsedFile = parseFileFormat(file);
    const reader = new FileReader();

    const formData = new FormData();
    formData.append('file', file);

    return new Promise((resolve) => {
      reader.onload = (e) => {
        resolve(_merge(parsedFile, {
          url: e.target.result,
          data: e.target.result,
          base64Data: e.target.result,
          formData
        }));
      };
      reader.readAsDataURL(file);
    });
  });

  return Promise.all(promises);
};

const EmptyStateButton = memo(({
  accept, onChange, buttonProps, disabled
}) => {
  let fileInput = null;
  const onClick = () => fileInput && fileInput.click();

  return (
    <React.Fragment>
      <Button
        color="primary"
        variant="outlined"
        size="large"
        onClick={onClick}
        startIcon={<AttachFileIcon />}
        disabled={disabled}
        {...buttonProps}
      >
        Anexar
      </Button>
      <input
        type="file"
        accept={accept}
        style={{ display: 'none' }}
        ref={component => { fileInput = component; }}
        onChange={(e) => getFilesData(e.target.files).then(files => onChange(files[0]))}
      />
    </React.Fragment>
  );
});
EmptyStateButton.propTypes = {
  accept: PropTypes.string,
  onChange: PropTypes.func,
  disabled: PropTypes.bool,
  buttonProps: PropTypes.object
};

export default EmptyStateButton;
