import PubSub from 'PubSub';

import Store from './store';

const ON_VIEWER_CHANGE = 'ON_VIEWER_CHANGE';
const pubsub = new PubSub();

const Session = {
  setUserToken(userToken) {
    return Store.save('userToken', userToken);
  },
  getUserToken() {
    return Store.get('userToken') || '';
  },
  setUserData(userData) {
    const result = Store.save('userData', userData);
    pubsub.publish(ON_VIEWER_CHANGE, userData);
    return result;
  },
  getUserData() {
    return Store.get('userData') || {};
  },
  clear() {
    pubsub.publish(ON_VIEWER_CHANGE, null);
    Store.delete('userToken');
    Store.delete('userData');
  },
  getAuthToken() {
    const authToken = Store.get('userToken');
    if (authToken) {
      return authToken;
    }
    throw new Error('No authToken');
  },
  getBearerToken() {
    const userToken = Store.get('userToken');
    return `Bearer: ${userToken}`;
  },
  isTicketsWelcomeVisited() {
    return !!Store.get('ticketsWelcomeVisited');
  },
  isServicesWelcomeVisited() {
    return !!Store.get('servicesWelcomeVisited');
  },
  setTicketsWelcomeVisited() {
    Store.save('ticketsWelcomeVisited', true);
  },
  setServicesWelcomeVisited() {
    Store.save('servicesWelcomeVisited', true);
  },
  isLoggedIn() {
    const isLoggedIn = Store.get('userToken');
    return !!isLoggedIn;
  },
  isUnauthorized(err) {
    return err && err.response && err.response.status === 401;
  },
  subscribeOnViewerChange(callback) {
    return pubsub.subscribe(ON_VIEWER_CHANGE, (data) => callback(data));
  },
  unsubscribeOnViewerChange(subscriptionReference) {
    return pubsub.unsubscribe(subscriptionReference);
  }
};

export default Session;
