import React, { memo } from 'react';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import { compose, withState } from 'recompose';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import SettingsMenu from '../../components/SettingsMenu';
import TicketCategoriesTable from './TicketCategoriesTable';
import styles from './styles';

const TicketCategories = memo(({
  alert, setAlert, classes
}) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} />
      <section>
        <div className={classes.container}>
          <div className={classes.headerView}>
            <aside>
              <Typography variant="h5">
                <b>Configuraes</b>
              </Typography>
            </aside>
          </div>
        </div>
        <div className={classes.container}>
          <SettingsMenu />
        </div>
        <div className={classes.container}>
          <TicketCategoriesTable setAlert={setAlert} />
        </div>
      </section>
    </div>
  );
});

TicketCategories.propTypes = {
  classes: PropTypes.object,
  alert: PropTypes.object,
  setAlert: PropTypes.func
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  })
)(TicketCategories);
