import React, { memo } from 'react';
import { Grid, Box, Typography } from '@material-ui/core';
import PropTypes from 'prop-types';

import { FormDateField } from '../../../../../components/FormFields';
import Loading from '../../../../../components/Loading';

const ExportTicketsCsvForm = ({
  setFormData, isLoading,
  formData: { start_date, end_date, validationErrors: errors }
}) => {
  if (isLoading) return <Loading />;

  return (
    <React.Fragment>
      <Grid container direction="row" spacing={1}>
        <Grid item xs={12} sm={6}>
          <FormDateField
            errors={errors.start_date}
            label="Data de incio"
            disabled={isLoading}
            value={start_date}
            onChangeValue={newVal => setFormData({ start_date: newVal })}
            disableFuture
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormDateField
            errors={errors.end_date}
            label="Data de fim"
            disabled={isLoading}
            value={end_date}
            onChangeValue={newVal => setFormData({ end_date: newVal })}
            disableFuture
          />
        </Grid>
      </Grid>
      <Box display="flex" justifyContent="end" mt={2}>
        <Typography variant="body2" color="textSecondary" component="small" align="right">
          Formato de arquivo .csv
        </Typography>
      </Box>
    </React.Fragment>
  );
};

ExportTicketsCsvForm.propTypes = {
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  isLoading: PropTypes.bool
};

export default memo(ExportTicketsCsvForm);
