/* global window */
import React, { memo } from 'react';
import { size as _size } from 'lodash';
import { compose, withState } from 'recompose';
import { IconButton, Menu, MenuItem } from '@material-ui/core';
import { MoreVert as MoreVertIcon } from '@material-ui/icons';
import CircularProgress from '@material-ui/core/CircularProgress';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import styles from './styles';

import withRequest from '../../../utils/with-request';
import TicketsService from '../../../services/tickets';

const ITEM_HEIGHT = 48;

const MenuForDone = memo(({
  anchorEl, setAnchorEl, archiveFetch, archiveLoading,
  doneTickets, theme, classes
}) => {
  const open = Boolean(anchorEl);
  const { mainColor } = theme;
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleArchive = () => {
    if (window.confirm('Deseja arquivar os tickets?')) {
      setAnchorEl(false);
      archiveFetch(doneTickets);
    }
  };
  return (
    <div>
      {archiveLoading ?
        <CircularProgress
          color={mainColor}
          className={classes.progress}
          variant="indeterminate"
          disableShrink
          thickness={5}
          size={25}
        />
        :
        <React.Fragment>
          <IconButton
            aria-label="more"
            aria-controls="long-menu"
            aria-haspopup="true"
            onClick={handleClick}
          >
            <MoreVertIcon />
          </IconButton>
          <Menu
            id="long-menu"
            anchorEl={anchorEl}
            keepMounted
            open={open}
            onClose={handleClose}
            PaperProps={{
              style: {
                maxHeight: ITEM_HEIGHT * 4.5,
                width: '23ch'
              }
            }}
          >
            <MenuItem onClick={handleArchive}>
              Arquivar concludos
            </MenuItem>
          </Menu>
        </React.Fragment>}
    </div>
  );
});

MenuForDone.propTypes = {
  anchorEl: PropTypes.object,
  setAnchorEl: PropTypes.func,
  archiveFetch: PropTypes.func,
  archiveLoading: PropTypes.bool,
  doneTickets: PropTypes.array,
  theme: PropTypes.object,
  classes: PropTypes.object
};

const withArchive = withRequest(TicketsService.archiveTicket, {
  name: 'archive',
  init: false,
  options: ({ setAlert, ticketsFetch, doneTickets }) => ({
    variables: { ticketsIds: doneTickets },
    onRequestFinished: () => {
      const pluralizedMessage = _size(doneTickets) > 1 ? 'tickets concludos foram arquivados' : 'ticket concludo foi arquivado';
      const finalMessage = _size(doneTickets) === 0 ? 'Nenhum ticket foi arquivado' : pluralizedMessage;
      ticketsFetch(true);
      setAlert({
        alertMsg: `${_size(doneTickets) === 0 ? '' : _size(doneTickets)} ${finalMessage}`,
        alertType: 'success',
        showAlert: true
      });
    },
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao arquivar tickets',
        alertType: 'warning',
        showAlert: true
      });
    }
  })
});

export default compose(
  memo,
  withStyles(styles, { withTheme: true }),
  withArchive,
  withState('anchorEl', 'setAnchorEl', null),
  withState('isExportModalOpen', 'setIsExportModalOpen', false)
)(MenuForDone);
