import React, { memo } from 'react';
import { compose, withHandlers } from 'recompose';
import PropTypes from 'prop-types';

import uploadFile from '../../utils/upload-files';
import EmptyStateButton from './EmptyStateButton';
import FilePreviewButton from './FilePreviewButton';

const AttachButtonBase = ({
  value, onChange, disabled, onFileChange,
  accept = 'image/*,.doc,.docx,.xls,.xlsx,.pdf,.xml', buttonProps
}) => {
  if (!value || (!value.progressPercent && !value.id)) {
    return (
      <EmptyStateButton
        accept={accept}
        onChange={onFileChange}
        disabled={disabled}
        buttonProps={buttonProps}
      />
    );
  }

  return (
    <FilePreviewButton
      file={value}
      onRemoveFile={fileToRemove => {
        if (fileToRemove.cancelUpload && fileToRemove.progressPercent < 100) {
          fileToRemove.cancelUpload();
        } else {
          onChange(null);
        }
      }}
      disabled={disabled}
    />
  );
};
AttachButtonBase.propTypes = {
  value: PropTypes.object,
  onChange: PropTypes.func,
  disabled: PropTypes.bool,
  onFileChange: PropTypes.func,
  accept: PropTypes.string,
  buttonProps: PropTypes.object
};

const AttachButton = compose(
  memo,
  withHandlers({
    onFileChange: ({
      onChange, sizeLimit,
      onUploadProgress, onUploadSuccess, onUploadError
    }) => (newFile) => {
      uploadFile({
        file: newFile,
        sizeLimit,
        progressHandler: ({ progressPercent, cancelUpload }) => {
          if (progressPercent >= 100) return;

          const updatedFile = { ...newFile, cancelUpload, progressPercent };
          onChange(updatedFile);
          if (onUploadProgress) onUploadProgress(updatedFile);
        }
      })
        .then(result => {
          const updatedFile = { ...newFile, ...result };
          onChange(updatedFile);
          if (onUploadSuccess) onUploadSuccess(updatedFile);
        })
        .catch(e => {
          onChange(null);
          if (onUploadError) onUploadError(e);
        });
    }
  })
)(AttachButtonBase);

export { FilePreviewButton };
export default AttachButton;
