import React, { memo } from 'react';
import { compose } from 'recompose';
import { withStyles, Box, Grid, Typography } from '@material-ui/core';
import CreateIcon from '@material-ui/icons/Create';
import PropTypes from 'prop-types';
import { filter, last, capitalize } from 'lodash';
import moment from 'moment';

import { FormTextField, FormNumberField } from '../../../components/FormFields';
import styles from './styles';

const maskCreditCardNumber = (newValue) => {
  if (newValue === null) return null;

  const regex = /(\d{1,4})(?:-(\d{0,4}))?(?:-(\d{0,4}))?(?:-(\d{0,4}))?/;
  const regexResult = regex.exec(newValue);
  if (!regexResult) return regexResult;

  const parts = filter(regexResult.slice(1), x => x);
  if (parts.length < 4 && last(parts).length === 4) {
    return `${parts.join('-')}-`;
  }

  return parts.join('-');
};

const maskCreditCardExpiration = ({ oldValue, newValue }) => {
  if (newValue === null) return null;

  const regex = /(\d{1,2})(?:\/?(\d{0,2}))?/;
  const regexResult = regex.exec(newValue);
  if (!regexResult) return regexResult;

  const [first, second] = filter(regexResult.slice(1), x => x);
  if (Number(first) > 12) return oldValue;

  if (first.length === 1 && Number(first) > 1) return `0${first}/`;

  if (first.length !== 2) {
    return first;
  }

  return `${first}/${second || ''}`;
};

const CreditCardFieldset = ({
  isLoading, currentCreditCard, onSetNewCreditCard, classes,
  setFormData, formData: { cardHolderName, cardNumber, cardCvv, cardExpirationDate }
}) => {
  if (currentCreditCard) {
    return (
      <React.Fragment>
        <Typography variant="h6">
          <b>Dados do carto de crdito</b>
        </Typography>
        <Box pt={2}>
          <Typography variant="body1">
            <b>Titular: </b>
            {currentCreditCard.holderName}
          </Typography>
          <Typography variant="body1">
            <b>Carto: </b>
            {`****-****-****-${currentCreditCard.lastDigits} (${capitalize(currentCreditCard.brand)})`}
          </Typography>
          <Typography variant="body1">
            <b>Data de expirao: </b>
            {moment(currentCreditCard.expiration, 'MM/YY').format('MM/YY')}
          </Typography>
        </Box>
        <span className={classes.changeCard} onClick={onSetNewCreditCard}>
          <CreateIcon className={classes.changeCardIcon} />
          Mudar de carto
        </span>
      </React.Fragment>
    );
  }

  return (
    <React.Fragment>
      <Typography variant="h6">
        <b>Dados do carto de crdito</b>
      </Typography>
      <Grid container alignItems="center">
        <Grid item xs={12} sm={12}>
          <FormTextField
            label="Titular"
            placeholder="Nome do ttular"
            disabled={isLoading}
            value={cardHolderName}
            onChangeText={newVal => setFormData({ cardHolderName: newVal })}
          />
        </Grid>
        <Grid container alignItems="center" spacing={1}>
          <Grid item xs={12} sm={6}>
            <FormTextField
              label="Nmero"
              placeholder="Nmero do carto"
              disabled={isLoading}
              value={cardNumber || ''}
              onChangeText={newVal => setFormData({ cardNumber: newVal })}
              maskFunction={maskCreditCardNumber}
            />
          </Grid>
          <Grid item xs={5} sm={3}>
            <FormNumberField
              label="CVV"
              disabled={isLoading}
              value={cardCvv}
              onChangeText={newVal => setFormData({ cardCvv: newVal })}
              isInteger
              max={999}
            />
          </Grid>
          <Grid item xs={7} sm={3}>
            <FormTextField
              placeholder="MM/AA"
              label="Data de expirao"
              disabled={isLoading}
              value={cardExpirationDate || ''}
              onChangeText={newVal => setFormData({ cardExpirationDate: newVal })}
              maskFunction={
                newValue => maskCreditCardExpiration({ newValue, oldValue: cardExpirationDate })
              }
            />
          </Grid>
        </Grid>
      </Grid>
    </React.Fragment>
  );
};

CreditCardFieldset.propTypes = {
  classes: PropTypes.object,
  isLoading: PropTypes.bool,
  currentCreditCard: PropTypes.object,
  onSetNewCreditCard: PropTypes.func,
  setFormData: PropTypes.func,
  formData: PropTypes.shape({
    cardHolderName: PropTypes.string,
    cardNumber: PropTypes.string,
    cardCvv: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    cardExpirationDate: PropTypes.string
  })
};

export default compose(
  memo,
  withStyles(styles, { withTheme: true })
)(CreditCardFieldset);
