import React from 'react';
import { isEmpty, isArray, get } from 'lodash';
import { FormControl, FormHelperText, InputLabel, Select, MenuItem } from '@material-ui/core';
import PropTypes from 'prop-types';

import styles from './styles';
import FormTextField from './FormTextField';

const buildSelectMultiple = ({
  errors, label, onChangeValue, onChangeText,
  InputLabelProps,
  options, optionValueField, optionLabelField, margin,
  variant,
  ...otherProps
}) => {
  const labelId = `label-${Math.random()}`;
  return (
    <FormControl
      fullWidth
      margin={margin}
      variant={variant}
      error={!isEmpty(errors)}
    >
      <InputLabel shrink id={labelId} {...InputLabelProps}>{label}</InputLabel>
      <Select
        label={label}
        multiple
        labelId={labelId}
        error={!isEmpty(errors)}
        onChange={event => {
          const newValue = event.target.value;
          onChangeValue(newValue);
          onChangeText(newValue);
        }}
        {...otherProps}
      >
        {
          options.map(i => (
            <MenuItem value={get(i, optionValueField)} key={get(i, optionValueField)}>
              {get(i, optionLabelField)}
            </MenuItem>
          ))
        }
      </Select>
      {
        !isEmpty(errors) && (
          <FormHelperText style={styles.errors}>
            {isArray(errors) ? errors.join(', ') : errors}
          </FormHelperText>
        )
      }
    </FormControl>
  );
};

const buildSelectSingle = ({
  errors, label, onChangeValue, onChangeText,
  emptyOptionLabel, options, optionValueField, optionLabelField,
  margin,
  ...otherProps
}) => (
  <FormTextField
    select
    errors={errors}
    SelectProps={{ native: true }}
    label={label}
    onChangeValue={onChangeValue}
    onChangeText={onChangeText}
    margin={margin}
    {...otherProps}
  >
    {emptyOptionLabel ? <option value={null}>{emptyOptionLabel}</option> : ''}
    {options.map(r => (
      <option value={get(r, optionValueField)} key={get(r, optionValueField)}>
        {get(r, optionLabelField)}
      </option>))}
  </FormTextField>
);

const FormSelectField = ({
  errors, variant = 'outlined', InputLabelProps = { shrink: true },
  onChangeValue = () => {}, onChangeText = () => {}, margin = 'normal',
  // until this point, those are params in common w/ FOrmTextField
  label, multiple, options, emptyOptionLabel = null, readOnly,
  optionValueField = 'value', optionLabelField = 'label',
  ...otherProps
}) => {
  otherProps = variant !== 'standard' ? { ...otherProps, variant } : otherProps;

  const selectComponent = multiple
    ? buildSelectMultiple({
      errors,
      label,
      onChangeValue,
      onChangeText,
      InputLabelProps,
      options,
      optionValueField,
      optionLabelField,
      margin,
      variant,
      ...otherProps
    })
    : buildSelectSingle({
      errors,
      label,
      onChangeValue,
      onChangeText,
      emptyOptionLabel,
      options,
      optionValueField,
      optionLabelField,
      margin,
      ...otherProps
    });

  return (
    readOnly
      ? (
        <FormTextField
          errors={errors}
          {...otherProps}
        />
      )
      : selectComponent
  );
};

FormSelectField.propTypes = {
  errors: PropTypes.arrayOf(PropTypes.string),
  variant: PropTypes.string,
  InputLabelProps: PropTypes.shape({ shrink: PropTypes.bool }),
  onChangeValue: PropTypes.func,
  onChangeText: PropTypes.func,
  margin: PropTypes.string,
  multiple: PropTypes.bool,
  options: PropTypes.array,
  readOnly: PropTypes.bool,
  emptyOptionLabel: PropTypes.string,
  optionValueField: PropTypes.string,
  optionLabelField: PropTypes.string,
  label: PropTypes.string
};

export default FormSelectField;
