import React, { memo } from 'react';
import { get } from 'lodash';
import { compose } from 'recompose';
import { Typography } from '@material-ui/core';
import moment from 'moment';
import PropTypes from 'prop-types';

import { AGENT_ROLES } from '../../enums/user-roles';
import withNotAuthorizedHandler from '../../utils/with-not-authorized-handler';
import withFormData from '../../utils/with-form-data';
import withRequest from '../../utils/with-request';
import TicketsService from '../../services/tickets';
import {
  withMainLayout
} from '../../components/MainLayout';
import Form from './Form';
import Result from './Result';

const TicketsReport = ({
  viewer,
  formData, setFormData,
  reportFetch, reportLoading, reportData
}) => {
  const onSubmit = () => reportFetch(true);

  return (
    <React.Fragment>
      <Typography variant="h5">
        <b>Relatrio de Tickets</b>
      </Typography>
      <Form
        viewer={viewer}
        formData={formData}
        setFormData={setFormData}
        isLoading={reportLoading}
        onSubmit={onSubmit}
      />
      <Result
        reportData={reportData}
        isLoading={reportLoading}
      />
    </React.Fragment>
  );
};

TicketsReport.propTypes = {
  viewer: PropTypes.object,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  reportFetch: PropTypes.func,
  reportLoading: PropTypes.bool,
  reportData: PropTypes.object
};

const withReport = withRequest(TicketsService.report, {
  name: 'report',
  init: false,
  options: ({ formData, setFormData, setAlert }) => {
    const { validationErrors, ...formFields } = formData;
    return {
      variables: formFields,
      onRequestError: error => {
        setAlert({
          alertMsg: 'Erro ao tentar gerar relatrio', alertType: 'error', showAlert: true
        });
        setFormData({ validationErrors: get(error, 'errors', {}) });
      }
    };
  }
});

export default compose(
  memo,
  withNotAuthorizedHandler({
    condition: (viewer) => !AGENT_ROLES.includes(viewer.role)
  }),
  withMainLayout,
  withFormData(() => {
    const monthSubtract = moment().date() > 1 ? 0 : 1;
    const startDate = moment().subtract(monthSubtract, 'month').startOf('month');
    const endDate = moment();
    return {
      location_id: 'ALL',
      start_date: startDate,
      end_date: endDate
    };
  }),
  withReport
)(TicketsReport);
