// import digitalOceanUpload from './digital-ocean-upload';
import UploadsService from '../../services/uploads';

const DEFAULT_SIZE_LIMIT_MB = 10;
const mbToBytes = sizeMb => sizeMb * 1028 * 1028;

// sizeLimit: limit size for file in megabytes.
const uploadFile = ({
  file,
  progressHandler: progressHandlerProp,
  sizeLimit = DEFAULT_SIZE_LIMIT_MB
}) => {
  if (file.metadata.size > mbToBytes(sizeLimit)) {
    return Promise.reject(new Error(`Tamanho do arquivo ultrapassou o limite de ${sizeLimit}mb`));
  }

  let resolve;
  let reject;
  let requestObject;
  const promise = new Promise((res, rej) => {
    resolve = res;
    reject = rej;
  });

  promise.cancelUpload = () => {
    promise.wasCanceled = true;
    requestObject.cancelRequest();
    reject(new Error('Upload cancelado'));
  };
  const progressHandler = (args) => {
    if (promise.wasCanceled) return;
    progressHandlerProp({ ...file,
      ...args,
      cancelUpload: promise.cancelUpload,
      wasCanceled: promise.wasCanceled });
  };
  progressHandler({ progressPercent: 0 });

  const uploaderFunc = UploadsService.uploadToApi;

  requestObject = uploaderFunc({ file, progressHandler });
  requestObject.then(resolve).catch(reject);

  return promise;
};

export default uploadFile;
