import React from 'react';
import PropTypes from 'prop-types';

import FormTextField from './FormTextField';

const CELLPHONE = 'cellphone';
const LANDLINE = 'landline';

const CELLPHONE_REGEX = /(\d{0,2})(\d{0,5})(\d{0,4})/;
const LANDLINE_REGEX = /(\d{0,2})(\d{0,4})(\d{0,4})/;
const maskPhone = (value, { phoneType }) => {
  if (value == null) return null;

  const numbersOnly = value.replace(/\D/g, '');
  const regex = phoneType === CELLPHONE
    ? CELLPHONE_REGEX
    : LANDLINE_REGEX;

  // eslint-disable-next-line
  const [_, first, second, third] = regex.exec(numbersOnly);
  if (first === '') return null;

  if (third === '') {
    if (second === '') {
      return `(${first})`;
    }

    return `(${first}) ${second}`;
  }

  return `(${first}) ${second}-${third}`;
};

const FormPhoneField = ({
  value, phoneType = CELLPHONE, placeholder = '(00) 00000-0000', ...otherProps
}) => (
  <FormTextField
    value={value || ''}
    maxLength={9}
    maskFunction={newValue => maskPhone(newValue, { phoneType })}
    type="tel"
    placeholder={placeholder}
    {...otherProps}
  />
);

FormPhoneField.propTypes = {
  value: PropTypes.string,
  phoneType: PropTypes.oneOf([CELLPHONE, LANDLINE]),
  placeholder: PropTypes.string
};

export default FormPhoneField;
