import React, { memo } from 'react';
import PropTypes from 'prop-types';
import { map, cloneDeep, get, filter } from 'lodash';
import { compose, withState, withPropsOnChange, lifecycle } from 'recompose';
import { DragDropContext } from 'react-beautiful-dnd';
import TicketsEnum from '../../enums/tickets';
import Column from './Column';
import styles from './styles';

const {
  STATUSES, STATUS_PENDING,
  STATUS_IN_PROGRESS, STATUS_DONE
} = TicketsEnum;

const getColumnIndexById = (columns, status) => {
  return map(columns, c => c.status).indexOf(status);
};

const generateColumns = (tickets) => {
  return map(STATUSES, (k, i) => {
    const kanbanStatus = get(k, 'key');
    const selectedTickets = filter(tickets, t => t.status === kanbanStatus);
    return {
      index: i,
      status: kanbanStatus,
      title: get(k, 'label', '').toUpperCase(),
      rows: selectedTickets
    };
  });
};

const Kanban = memo(({
  columns, setColumns, onDragEnd, renderCard, loading,
  columnWrapperStyle, cardWrapperStyle, columnStyle,
  columnHeaderStyle, columnTitleStyle, disableDragDrop,
  currentDraggingSource, setCurrentDraggingSource,
  actionPending, actionInProgress, actionDone
}) => {
  const handleDragStart = (start) => {
    setCurrentDraggingSource(start.source);
  };
  const handleDragEnd = (result) => {
    setCurrentDraggingSource(null);
    if (!result.destination) return;

    const { source, destination } = result;
    // If trying to move inside same column
    if (source.droppableId === destination.droppableId) {
      return;
    }

    const blockDrag = (
      !destination ||
      (source.droppableId === destination.droppableId &&
      source.index === destination.index)
    );
    if (blockDrag) {
      return;
    }
    const newColumns = cloneDeep(columns);
    const sourceIndex = getColumnIndexById(columns, source.droppableId);
    const destinationIndex = getColumnIndexById(columns, destination.droppableId);

    const newSourceRows = cloneDeep(columns[sourceIndex].rows);
    const newDestinationRows = cloneDeep(columns[destinationIndex].rows);
    const [removed] = newSourceRows.splice(source.index, 1);
    newDestinationRows.splice(destination.index, 0, removed);
    newColumns[sourceIndex].rows = cloneDeep(newSourceRows);
    newColumns[destinationIndex].rows = cloneDeep(newDestinationRows);
    setColumns(cloneDeep(newColumns));
    onDragEnd(result);
  };
  const renderAction = (status) => {
    if (status === STATUS_PENDING && actionPending) {
      return actionPending;
    }
    if (status === STATUS_IN_PROGRESS && actionInProgress) {
      return actionInProgress;
    }
    if (status === STATUS_DONE && actionDone) {
      return actionDone;
    }
    return null;
  };
  return (
    <DragDropContext onDragStart={handleDragStart} onDragEnd={handleDragEnd}>
      <div style={styles.kanban}>
        {
          map(STATUSES, (status, index) => (
            <Column
              key={index}
              index={index}
              loading={loading}
              column={columns[index]}
              renderCard={renderCard}
              columnHeaderStyle={columnHeaderStyle}
              columnTitleStyle={columnTitleStyle}
              columnStyle={{ ...columnStyle, ...(loading ? styles.columnLoading : {}) }}
              columnWrapperStyle={columnWrapperStyle}
              cardWrapperStyle={cardWrapperStyle}
              disableDragDrop={disableDragDrop}
              actionComponent={renderAction(status.key)}
              isDraggingFrom={get(currentDraggingSource, 'droppableId') === status.key}
            />
          ))
        }
      </div>
    </DragDropContext>
  );
});

Kanban.propTypes = {
  columns: PropTypes.array,
  loading: PropTypes.bool,
  setColumns: PropTypes.func,
  renderCard: PropTypes.func,
  onDragEnd: PropTypes.func,
  columnWrapperStyle: PropTypes.object,
  columnHeaderStyle: PropTypes.object,
  columnStyle: PropTypes.object,
  columnTitleStyle: PropTypes.object,
  cardWrapperStyle: PropTypes.object,
  disableDragDrop: PropTypes.bool,
  actionPending: PropTypes.object,
  actionInProgress: PropTypes.object,
  actionDone: PropTypes.object,
  currentDraggingSource: PropTypes.object,
  setCurrentDraggingSource: PropTypes.func
};

export default compose(
  withState('columns', 'setColumns', []),
  withState('currentDraggingSource', 'setCurrentDraggingSource', null),
  lifecycle({
    componentDidMount() {
      const { data, setColumns } = this.props;
      setColumns(generateColumns(data));
    }
  }),
  withPropsOnChange(['data', 'loading'], ({ data, setColumns }) => {
    setColumns(generateColumns(data));
  })
)(Kanban);
