import React, { memo } from 'react';
import {
  useMediaQuery,
  Dialog,
  DialogContent,
  DialogActions,
  DialogTitle,
  IconButton,
  Grid,
  Button
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import { withStyles, useTheme } from '@material-ui/core/styles';
import PropTypes from 'prop-types';

import LocationForm from './LocationForm';
import styles from './styles';

const LocationFormModal = memo(({
  classes, open, onClose, modalTitle,
  formData, setFormData, onSubmit, isLoading
}) => {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

  return (
    <Dialog
      fullScreen={fullScreen}
      keepMounted
      fullWidth
      maxWidth="md"
      classes={{ paper: classes.dialogMemberForm }}
      open={open}
      onClose={onClose}
    >
      <DialogTitle>
        {modalTitle}
        <IconButton
          className={classes.closeButton}
          onClick={() => onClose({ shouldRefetch: false })}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.content}>
        <LocationForm
          formData={formData}
          setFormData={setFormData}
          isLoading={isLoading}
        />
      </DialogContent>
      <DialogActions className={classes.actions}>
        <Grid container justify="flex-end" spacing={2}>
          <Grid item xs={6} sm={2}>
            <Button
              fullWidth
              color="default"
              size="large"
              variant="contained"
              onClick={onClose}
              disabled={isLoading}
            >
              CANCELAR
            </Button>
          </Grid>
          <Grid item xs={6} sm={2}>
            <Button
              fullWidth
              color="primary"
              size="large"
              variant="contained"
              onClick={onSubmit}
              disabled={isLoading}
            >
              SALVAR
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
});

LocationFormModal.propTypes = {
  classes: PropTypes.object,
  open: PropTypes.bool,
  onClose: PropTypes.func,
  modalTitle: PropTypes.string,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  onSubmit: PropTypes.func,
  isLoading: PropTypes.bool
};

export default withStyles(styles, { withTheme: true })(LocationFormModal);
