import React, { memo } from 'react';
import { get as _get, size as _size } from 'lodash';
import { compose } from 'recompose';
import PropTypes from 'prop-types';
import {
  Table, TableBody, TableCell, TableHead, TableRow, Paper, Typography
} from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import withRequest from '../../../../utils/with-request';
import InvoicesService from '../../../../services/invoices';
import { USER_MANAGER_ROLES } from '../../../../enums/user-roles';
import withNotAuthorizedHandler from '../../../../utils/with-not-authorized-handler';
import Loading from '../../../../components/Loading';
import InvoicesItemRow from './InvoicesItemRow';
import styles from './styles';

const InvoicesTable = memo(({
  invoicesData, invoicesLoading, classes, setAlert
}) => {
  const invoices = _get(invoicesData, 'invoices');
  return (
    <React.Fragment>
      <div className={classes.container}>
        <div className={classes.headerView}>
          <aside>
            <Typography variant="h6">
              <b>{`Pagamentos (${_size(invoices)})`}</b>
            </Typography>
          </aside>
        </div>
      </div>
      <div className={classes.container}>
        <Paper className={classes.tablePaper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell className={classes.tableSmallCell}>DESCRIO</TableCell>
                <TableCell className={classes.tableSmallCell}>VALOR</TableCell>
                <TableCell className={classes.tableSmallCell}>FORMA DE PGTO</TableCell>
                <TableCell className={classes.tableSmallCell}>LINK BOLETO</TableCell>
                <TableCell className={classes.tableSmallCell}>STATUS</TableCell>
                <TableCell className={classes.tableSmallCell}>DATA</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {
                invoicesLoading ?
                  <TableRow>
                    <TableCell colSpan={6} align="center">
                      <Loading />
                    </TableCell>
                  </TableRow>
                  :
                  (
                    _size(invoices) ?
                      (invoices.map((invoice, i) => {
                        return (
                          <InvoicesItemRow
                            setAlert={setAlert}
                            index={i}
                            key={invoice.id}
                            invoice={invoice}
                            loading={invoicesLoading}
                          />
                        );
                      }))
                      :
                      <TableRow>
                        <TableCell className={classes.empty} colSpan={6} align="center">
                          Nenhum pagamento realizado
                        </TableCell>
                      </TableRow>
                  )
              }
            </TableBody>
          </Table>
        </Paper>
      </div>
    </React.Fragment>
  );
});

InvoicesTable.propTypes = {
  invoicesLoading: PropTypes.bool,
  invoicesData: PropTypes.object,
  classes: PropTypes.object,
  setAlert: PropTypes.func
};

const withInvoices = withRequest(InvoicesService.getInvoices, {
  name: 'invoices',
  options: ({ setAlert }) => ({
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao carregar histrico de pagamentos',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withNotAuthorizedHandler({
    condition: (viewer) => !USER_MANAGER_ROLES.includes(viewer.role)
  }),
  withInvoices
)(InvoicesTable);
