/* global window */
import React, { memo } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import _get from 'lodash/get';
import { compose } from 'recompose';
import _lowerFirst from 'lodash/lowerFirst';
import { withStyles } from '@material-ui/core/styles';
import { Typography } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import AvatarImage from '../../../../components/AvatarImage';
import { FilePreviewButton } from '../../../../components/AttachButton';
import withRequest from '../../../../utils/with-request';
import Session from '../../../../utils/session';
import Tracker from '../../../../utils/tracker';
import Linkfy from '../../../../utils/linkfy';
import TicketCommentsService from '../../../../services/ticket-comments';
import styles from './styles';
import config from '../../../../config';

const TicketComment = memo(({
  comment, classes, deleteCommentFetch, deleteCommentLoading
}) => {
  const isInternal = _get(comment, 'is_internal');
  const isInternalStyleClass = isInternal ? classes.internalBallon : '';
  const member = Session.getUserData();
  const memberId = _get(member, 'id');
  const ticketCommentMemberId = _get(comment, 'member_id', '');
  const jobRole = _get(comment, 'member.job_role') ? `- ${_get(comment, 'member.job_role')}` : '';
  const fullName = _get(comment, 'member.fullname', config.systemName);
  const photoUrl = _get(comment, 'member.photo_url', config.systemPhotoUrl);
  const isActivity = _get(comment, 'is_activity');
  const content = _lowerFirst(comment.content || '');
  const isInternalLabel = isInternal && !isActivity ? ' - Interno' : '';
  const activityOrJobRole = isActivity ? content : jobRole;
  const onDeleteComment = () => {
    if (window.confirm('Deseja excluir o comentrio? Essa ao  irreversvel')) {
      deleteCommentFetch(true);
    }
  };
  return (
    <div>
      <div className={classes.userNameContainer}>
        <AvatarImage name={fullName} photoUrl={photoUrl} />
        <Typography variant="subtitle2" className={classes.userName}>
          <b>{fullName}</b>
          &nbsp;
          <Typography variant="body2" component="span">{activityOrJobRole}</Typography>
          {
            isActivity && (
              <Typography variant="body2" color="textSecondary" component="small">
                <br />
                {`${moment(_get(comment, 'created_at')).calendar()}${isInternalLabel}`}
              </Typography>
            )
          }
        </Typography>
      </div>
      <div className={classes.content}>
        {
          !isActivity && (
            <React.Fragment>
              {
                comment.attachment_data && (
                  <FilePreviewButton
                    file={comment.attachment_data}
                    outterContainerClassName={classes.previewButtonContainer}
                  />
                )
              }
              {
                comment.content && (
                  <div className={`${classes.baloon} ${isInternalStyleClass}`}>
                    <div className={comment.attachment_data && classes.contentWithMargin}>
                      {(comment.content || '').split('\n').map((line, index) => (
                        <span key={`${Date.now()}_${index.toString()}`}>
                          <span dangerouslySetInnerHTML={{ __html: Linkfy(line) }} />
                          <br />
                        </span>
                      ))}
                    </div>
                  </div>
                )
              }
              <Typography variant="body2" color="textSecondary" component="small">
                {`${moment(_get(comment, 'created_at')).calendar()}${isInternalLabel}`}
              </Typography>
              {
                ticketCommentMemberId && memberId === ticketCommentMemberId &&
                  <DeleteIcon
                    className={classes.iconButton}
                    onClick={onDeleteComment}
                    disabled={deleteCommentLoading}
                  />
              }
            </React.Fragment>
          )
        }
      </div>
    </div>
  );
});

TicketComment.propTypes = {
  classes: PropTypes.object,
  comment: PropTypes.object,
  deleteCommentFetch: PropTypes.func,
  deleteCommentLoading: PropTypes.bool
};

const withDeleteComment = withRequest(TicketCommentsService.deleteComment, {
  name: 'deleteComment',
  init: false,
  options: ({ comment, setAlert, fetchComments }) => ({
    variables: { id: _get(comment, 'id') },
    onRequestFinished: () => {
      setAlert({
        alertMsg: 'Comentrio excludo com sucesso',
        alertType: 'success',
        showAlert: true
      });
      const trackProps = { id: _get(comment, 'id'), content: _get(comment, 'content') };
      Tracker.hit('TicketComment - Deleted', trackProps);
      fetchComments();
    },
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao excluir comentrio',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  memo,
  withStyles(styles, { withTheme: true }),
  withDeleteComment
)(TicketComment);
