import React, { memo } from 'react';
import PropTypes from 'prop-types';
import {
  Table, TableBody, TableCell,
  TableHead, TableRow, Paper, Button
} from '@material-ui/core';
import _size from 'lodash/size';
import _sortBy from 'lodash/sortBy';
import _isEmpty from 'lodash/isEmpty';
import { compose } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import Loading from '../../../components/Loading';
import TicketModal from '../TicketModal';
import TicketItemRow from './TicketItemRow';
import styles from './styles';

const TicketsArchived = memo(({
  tickets, loading, setAlert, categories, assignees, classes,
  ticketToOpenModal, onCloseTicketModal, onOpenTicketModal,
  showMore, showMoreLoading, onShowMoreArchiveds
}) => {
  const archivedTickets = _sortBy(tickets, ['archived_at']).reverse();
  return (
    <React.Fragment>
      <div className={classes.container}>
        <Paper className={classes.paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell className={classes.tableTinyCell}>STATUS</TableCell>
                <TableCell className={classes.tableSmallCell}>TICKET</TableCell>
                <TableCell className={classes.tableTinyCell}>CATEGORIA</TableCell>
                <TableCell className={classes.tableSmallCell}>SOLICITADO EM</TableCell>
                <TableCell className={classes.tableSmallCell}>RESPONSVEIS</TableCell>
                <TableCell className={classes.tableSmallCell}>ARQUIVADO EM</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {
                loading ?
                  <TableRow>
                    <TableCell colSpan={6} align="center">
                      <Loading />
                    </TableCell>
                  </TableRow>
                  :
                  (
                    _size(archivedTickets) ?
                      archivedTickets.map((ticket, i) => (
                        <TicketItemRow
                          index={i}
                          key={ticket.id}
                          ticket={ticket}
                          onOpenTicket={onOpenTicketModal}
                          loading={loading}
                        />
                      ))
                      :
                      <TableRow>
                        <TableCell className={classes.empty} colSpan={6} align="center">
                          Nenhum ticket arquivado
                        </TableCell>
                      </TableRow>
                  )
              }
            </TableBody>
          </Table>
          {
            showMore &&
              <Button
                color="primary"
                size="large"
                onClick={onShowMoreArchiveds}
                disabled={showMoreLoading}
                className={classes.showMore}
              >
                {showMoreLoading ? 'Carregando...' : 'Ver mais arquivados'}
              </Button>
          }
        </Paper>
      </div>
      <TicketModal
        open={!_isEmpty(ticketToOpenModal)}
        ticket={ticketToOpenModal}
        categories={categories}
        assignees={assignees}
        onClose={onCloseTicketModal}
        setAlert={setAlert}
      />
    </React.Fragment>
  );
});

TicketsArchived.propTypes = {
  tickets: PropTypes.array,
  setAlert: PropTypes.func,
  categories: PropTypes.array,
  assignees: PropTypes.array,
  loading: PropTypes.bool,
  classes: PropTypes.object,
  ticketToOpenModal: PropTypes.object,
  showMoreLoading: PropTypes.bool,
  showMore: PropTypes.bool,
  onShowMoreArchiveds: PropTypes.func,
  onOpenTicketModal: PropTypes.func,
  onCloseTicketModal: PropTypes.func
};

export default compose(
  withStyles(styles, { withTheme: true })
)(TicketsArchived);
