import React from 'react';
import { camelCase, upperFirst, isEmpty, omitBy } from 'lodash';
import PropTypes from 'prop-types';

import {
  TEXT_FIELD, TEXT_AREA, SELECT, CHECKBOX, ATTACHMENT, DATE_FIELD
} from '../../enums/fields';
import FormTextField from './FormTextField';
import FormSelectField from './FormSelectField';
import FormDateField from './FormDateField';
import FormCheckboxListField from './FormCheckboxListField';
import FormAttachmentField from './FormAttachmentField';

const ALL_FIELD_TYPES = [
  TEXT_FIELD, TEXT_AREA, SELECT, CHECKBOX, ATTACHMENT, DATE_FIELD
];

const fieldBuilders = {
  buildTextField({ ...props }) {
    return <FormTextField {...props} />;
  },
  buildDateField({ value, ...props }) {
    return <FormDateField disablePast value={isEmpty(value) ? null : value} {...props} />;
  },
  buildTextArea({ ...props }) {
    return <FormTextField multiline rows={4} rowsMax={4} {...props} />;
  },
  buildSelect({ options, ...props }) {
    return <FormSelectField options={options} {...props} />;
  },
  buildCheckbox({ ...props }) {
    return <FormCheckboxListField {...props} />;
  },
  buildAttachment({ ...props }) {
    return <FormAttachmentField {...props} />;
  }
};

fieldBuilders.buildSelect.propTypes = {
  options: PropTypes.array
};

fieldBuilders.buildDateField.propTypes = {
  value: PropTypes.any
};

const SPECIFIC_PROPS_OWNERS = {
  emptyOptionLabel: ['Select'],
  uploadFileFunc: ['Attachment']
};
const filterProps = (fieldType, props) => (
  omitBy(props, (value, key) => (
    SPECIFIC_PROPS_OWNERS[key] && !SPECIFIC_PROPS_OWNERS[key].includes(fieldType)
  ))
);

const FormDynamicField = ({ fieldType: fieldTypeProp, ...otherProps }) => {
  if (!ALL_FIELD_TYPES.includes(fieldTypeProp)) {
    throw new Error(`Invalid field type: ${fieldTypeProp}`);
  }
  const fieldType = upperFirst(camelCase(fieldTypeProp));
  const filteredProps = filterProps(fieldType, otherProps);

  return fieldBuilders[`build${fieldType}`](filteredProps);
};

FormDynamicField.propTypes = {
  fieldType: PropTypes.oneOf(ALL_FIELD_TYPES)
};

export default FormDynamicField;
