import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _get from 'lodash/get';
import moment from 'moment';
import { withRouter } from 'react-router';
import { compose } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Chip from '@material-ui/core/Chip';
import Typography from '@material-ui/core/Typography';
import Skeleton from '@material-ui/lab/Skeleton';
import CircleIcon from '@material-ui/icons/FiberManualRecord';
import AvatarImage from '../../../components/AvatarImage';
import styles from './styles';

const renderBadge = (deadline, classes) => {
  if (deadline) {
    const deadlineLabel = moment(deadline).format('MMM D');
    const startOfToday = moment().startOf('day');
    const startOfDeadline = moment(deadline).startOf('day');
    const isToday = startOfDeadline.isSame(startOfToday, 'day');
    const isTomorrow = startOfDeadline.diff(startOfToday, 'days') === 1;
    const isExpired = startOfDeadline.isBefore(startOfToday, 'day');
    if (isToday) {
      return <Chip label={deadlineLabel} size="small" className={classes.cardChipToday} />;
    }
    if (isTomorrow) {
      return <Chip label={deadlineLabel} size="small" className={classes.cardChipTomorrow} />;
    }
    if (isExpired) {
      return <Chip label={deadlineLabel} size="small" className={classes.cardChipExpired} />;
    }
    return <Chip label={deadlineLabel} size="small" className={classes.cardChipFarFromExpiration} />;
  }
  return null;
};

const TicketCard = memo(({
  classes, ticket, loading, onClickCard
}) => {
  const onClick = () => {
    if (!loading) {
      onClickCard(ticket);
    }
  };
  const requesterName = `${_get(ticket, 'requester.name')} ${_get(ticket, 'requester.lastname', '')}`;
  const assignees = _get(ticket, 'assignees');
  const assigneesData = assignees.map(assignee => ({
    assigneeName: `${_get(assignee, 'name', '')} ${_get(assignee, 'lastname', '')}`,
    assignePhotoUrl: _get(assignee, 'photo_url')
  }));
  const createdAtLabel = _get(ticket, 'created_at') ? `${moment(ticket.created_at).fromNow()} por ` : '';
  const ticketTitle = _get(ticket, 'title', '');
  const ticketCategory = _get(ticket, 'category.title', '');
  return (
    <React.Fragment>
      <Card className={classes.card} onClick={onClick}>
        <CardContent className={classes.cardContent}>
          {!_get(ticket, 'viewer_has_read') && <CircleIcon className={classes.notReadIndicator} />}
          {!loading && renderBadge(_get(ticket, 'deadline'), classes)}
          <Typography variant="subtitle1" className={classes.cardTitle}>
            {loading ? <Skeleton animation="wave" /> : <b>{ticketTitle}</b>}
          </Typography>
          {
            loading ?
              <Skeleton animation="wave" width={150} />
              :
              <Typography variant="body2" color="textSecondary" component="small">
                {createdAtLabel}
                <b>{requesterName}</b>
              </Typography>
          }
          <div className={classes.cardAction}>
            <div className={classes.cardAvatar}>
              {
                loading ?
                  <Skeleton animation="wave" width={100} />
                  :
                  assignees &&
                    <div className={classes.cardAvatarContainer}>
                      {
                        assigneesData.map((a, i) => (
                          <div key={`${a.assigneeName}_${i.toString()}`} className={classes.cardAvatarImage}>
                            <AvatarImage name={a.assigneeName} photoUrl={a.assignePhotoUrl} />
                          </div>
                        ))
                      }
                    </div>
              }
            </div>
            <Typography variant="body2" color="textSecondary" component="small">
              {loading ? <Skeleton animation="wave" width={50} /> : ticketCategory}
            </Typography>
          </div>
        </CardContent>
      </Card>
    </React.Fragment>
  );
});

TicketCard.propTypes = {
  classes: PropTypes.object,
  ticket: PropTypes.object,
  loading: PropTypes.bool,
  onClickCard: PropTypes.func
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter
)(TicketCard);
