import React, { memo } from 'react';
import { compose } from 'recompose';
import PropTypes from 'prop-types';
import { get as _get, find as _find } from 'lodash';
import { TableCell, TableRow } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import moment from 'moment';
import AvatarImage from '../../../../components/AvatarImage';
import withViewerData from '../../../../utils/with-viewer-data';
import TicketsEnum from '../../../../enums/tickets';
import styles from './styles';

const { STATUSES } = TicketsEnum;

const TicketItemRow = memo(({
  ticket, viewer, index, classes, onOpenTicket
}) => {
  if (!viewer) return null;
  const createdAt = _get(ticket, 'created_at');
  const createdAtFormat = createdAt ? `${moment(createdAt).format('DD/MM/YYYY hh:mm')}` : '--';
  const archivedAt = _get(ticket, 'archived_at');
  const archivedAtFormat = archivedAt ? `${moment(archivedAt).format('DD/MM/YYYY hh:mm')}` : '--';
  const assignees = _get(ticket, 'assignees', []);
  const statusLabel = _get(_find(STATUSES, s => s.key === _get(ticket, 'status')), 'label', '--');
  const tableColorClass = index % 2 ? classes.bgWhite : classes.bgGray;
  return (
    <TableRow
      className={tableColorClass}
      style={{ cursor: 'pointer' }}
      onClick={() => onOpenTicket(ticket)}
    >
      <TableCell className={classes.tableTinyCell}>
        <span className={classes.cellText}>{statusLabel}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{_get(ticket, 'title')}</span>
      </TableCell>
      <TableCell className={classes.tableTinyCell}>
        <span className={classes.cellText}>{_get(ticket, 'category.title')}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{createdAtFormat}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <div className={classes.cardAvatarContainer}>
          {
            assignees.map((a) => (
              <div key={a.id} className={classes.cardAvatarImage}>
                <AvatarImage
                  name={`${_get(a, 'name')} ${_get(a, 'lastname')}`}
                  photoUrl={_get(a, 'photo_url')}
                />
              </div>
            ))
          }
        </div>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{archivedAtFormat}</span>
      </TableCell>
    </TableRow>
  );
});

TicketItemRow.propTypes = {
  index: PropTypes.number,
  ticket: PropTypes.object,
  viewer: PropTypes.object,
  classes: PropTypes.object,
  onOpenTicket: PropTypes.func
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withViewerData
)(TicketItemRow);
