import axios, { CancelToken } from 'axios';
import { get as _get } from 'lodash';
import Session from './session';
import config from '../config';

const getErrorMsg = (err) => {
  let errorData = _get(err, 'response.data') || {};
  errorData.response = _get(err, 'response');
  if (!errorData) {
    errorData = { error: _get(err, 'message') };
  }
  return errorData;
};
const baseURL = `${config.apiURL}/${config.appNamespace}`;

const Api = {
  request(method = 'get', endpoint, body, params, options) {
    let resolve;
    let reject;
    const source = CancelToken.source();
    const result = new Promise((res, rej) => {
      resolve = res;
      reject = rej;
    });
    result.cancelRequest = () => source.cancel('Operation canceled by the user.');

    const bearerToken = Session.getBearerToken();
    const agent = axios.create({
      baseURL,
      timeout: config.apiRequestTimeout,
      headers: {
        'Content-Type': 'application/json',
        Authorization: bearerToken
      },
      responseType: 'json',
      onUploadProgress: _get(options, 'onUploadProgress'),
      cancelToken: source.token
    });

    agent[method](endpoint, body, params)
      .then(response => resolve(response.data))
      .catch(err => reject(getErrorMsg(err)));
    return result;
  },
  get(endpoint, params, options) {
    return this.request('get', endpoint, { params }, options);
  },
  post(endpoint, body, params, options) {
    return this.request('post', endpoint, body, params, options);
  },
  put(endpoint, body, params, options) {
    return this.request('put', endpoint, body, params, options);
  },
  delete(endpoint, body, params, options) {
    return this.request('delete', endpoint, body, params, options);
  }
};

export { baseURL };
export default Api;
