import pagarme from 'pagarme';

import config from '../config';

const PagarmeApi = {
  encryptCreditCard: async ({
    cardNumber, cardHolderName, cardExpirationDate, cardCvv
  }) => {
    const pagarmeClient = await pagarme.client.connect({
      encryption_key: config.pagarmeEncryptionKey
    });
    const creditCardHash = await pagarmeClient.security.encrypt({
      card_number: cardNumber.replace(/\D+/g, ''),
      card_holder_name: cardHolderName,
      card_expiration_date: cardExpirationDate.replace(/\D+/g, ''),
      card_cvv: cardCvv
    });
    return creditCardHash;
  }
};

export default PagarmeApi;
