/* global window */
import React, { memo } from 'react';
import {
  get as _get, map as _map, isEqual as _isEqual,
  pickBy as _pickBy, mapValues as _mapValues
} from 'lodash';
import { compose, withState, withHandlers } from 'recompose';
import { withStyles, useTheme } from '@material-ui/core/styles';
import {
  useMediaQuery, Dialog, DialogContent, DialogActions,
  DialogTitle, IconButton, Grid, Button
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import PropTypes from 'prop-types';
import MembersService from '../../../services/members';
import MemberService from '../../../services/member';
import withRequest from '../../../utils/with-request';
import Session from '../../../utils/session';
import MemberForm from './MemberForm';
import styles from './styles';

const MemberFormModal = memo(({
  member, isLoading: isLoadingProp, open,
  classes, formData, setFormData, onClose,
  updateMemberFetch, updateMemberLoading,
  blockMemberFetch, blockMemberLoading
}) => {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const handleBlockMember = () => {
    if (window.confirm('Tem certeza em bloquear esse usurio?')) {
      blockMemberFetch(true, '', null, _get(member, 'id', ''));
    }
  };
  const isLoading = isLoadingProp;
  const isSubmiting = updateMemberLoading || blockMemberLoading;
  return (
    <Dialog
      fullScreen={fullScreen}
      keepMounted
      fullWidth
      maxWidth="md"
      classes={{ paper: classes.dialogMemberForm }}
      open={open}
      onClose={() => onClose({ shouldRefetch: false })}
    >
      <DialogTitle>
        Editar usurio
        <IconButton
          className={classes.closeButton}
          onClick={() => onClose({ shouldRefetch: false })}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.content}>
        <MemberForm
          member={member}
          formData={formData}
          setFormData={setFormData}
          isLoading={isLoading}
          isSubmiting={isSubmiting}
        />
      </DialogContent>
      <DialogActions className={classes.actions}>
        <Grid container justify="space-between" spacing={2}>
          <Grid item xs={12} sm={8}>
            <Button
              fullWidth={fullScreen}
              color="secondary"
              size="large"
              variant={fullScreen ? 'outlined' : 'contained'}
              onClick={handleBlockMember}
              disabled={isLoading || isSubmiting}
            >
              DESATIVAR USURIO
            </Button>
          </Grid>
          <Grid item xs={6} sm={2}>
            <Button
              fullWidth
              color="default"
              size="large"
              variant="contained"
              onClick={() => onClose({ shouldRefetch: false })}
              disabled={isLoading || isSubmiting}
            >
              CANCELAR
            </Button>
          </Grid>
          <Grid item xs={6} sm={2}>
            <Button
              fullWidth
              color="primary"
              size="large"
              variant="contained"
              onClick={() => updateMemberFetch(true).catch(() => {})}
              disabled={isLoading || isSubmiting}
            >
              SALVAR
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
});
MemberFormModal.propTypes = {
  member: PropTypes.object,
  isLoading: PropTypes.bool,
  open: PropTypes.bool,
  onClose: PropTypes.func,
  classes: PropTypes.object,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  updateMemberFetch: PropTypes.func,
  updateMemberLoading: PropTypes.bool,
  blockMemberFetch: PropTypes.func,
  blockMemberLoading: PropTypes.bool
};

const initialFormData = {
  locationsOptions: [], previousValues: {}, validationErrors: {}
};

const withUpdateMember = withRequest(MembersService.updateMember, {
  name: 'updateMember',
  init: false,
  options: ({ formData, setFormData, member, setAlert, onClose }) => {
    const changes = _pickBy(formData, (value, key) => {
      const previousVal = formData.previousValues[key];
      return !_isEqual(previousVal, value);
    });
    let locationsData = {};
    if (changes.locationsOptions) {
      const locationIds = _map(_pickBy(changes.locationsOptions, { checked: true }), b => b.value);
      locationsData = { locationIds };
    }
    return {
      variables: {
        id: member.id,
        ...changes,
        ...locationsData
      },
      onRequestFinished: async () => {
        const currentMember = Session.getUserData();
        if (member.id === currentMember.id) {
          const response = await MemberService.getViewer();
          Session.setUserData(_get(response, 'member'));
        }
        setAlert({
          alertMsg: 'Usurio alterado com sucesso',
          alertType: 'success',
          showAlert: true
        });
        onClose({ shouldRefetch: true });
      },
      onRequestError: error => {
        const errorMessage = _get(error, 'error', '');
        setAlert({
          alertMsg: errorMessage || 'Erro ao alterar este usurio',
          alertType: 'error',
          showAlert: true
        });
        const errors = _mapValues(_get(error, 'errors', {}), (err) => err[0]);
        setFormData({ validationErrors: errors });
      }
    };
  }
});

const withBlockMember = withRequest(MembersService.blockMember, {
  name: 'blockMember',
  init: false,
  options: ({ member, onClose, setAlert }) => ({
    variables: _get(member, 'id'),
    onRequestFinished: () => {
      setAlert({
        alertMsg: 'Usurio bloqueado com sucesso',
        alertType: 'success',
        showAlert: true
      });
      onClose({ shouldRefetch: true });
    },
    onRequestError: (err) => {
      setAlert({
        alertMsg: Object.values(err.errors).flat().join() || 'Erro ao bloquear este usurio',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});
export default compose(
  withStyles(styles, { withTheme: true }),
  withState('formData', 'setFormDataRaw', initialFormData),
  withHandlers({
    setFormData: ({ setFormDataRaw, formData }) => (attrs) => {
      setFormDataRaw({ ...formData, ...attrs });
    }
  }),
  withBlockMember,
  withUpdateMember
)(MemberFormModal);
