import React, { memo } from 'react';
import { get as _get, size as _size } from 'lodash';
import {
  Table, TableHead, TableBody, TableRow, TableCell
} from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';

import Loading from '../../../../components/Loading';
import LocationsItemRow from '../LocationsItemRow';
import styles from './styles';

const LocationsTable = memo(({
  locationsData, locationsLoading: loading, locationsFetch, setAlert,
  classes
}) => {
  const locations = _get(locationsData, 'locations', []);

  return (
    <Table>
      <TableHead>
        <TableRow>
          <TableCell className={classes.tableCell}>NOME</TableCell>
          <TableCell className={classes.tableCell}>ENDEREO</TableCell>
          <TableCell className={classes.tableCell}>CIDADE</TableCell>
          <TableCell className={classes.tableCell}>HABILITAR</TableCell>
          <TableCell className={classes.tableCell} />
        </TableRow>
      </TableHead>
      <TableBody>
        {
          loading ?
            <TableRow>
              <TableCell colSpan={5} align="center">
                <Loading />
              </TableCell>
            </TableRow>
            :
            (
              _size(locations) ?
                (locations.map((location, i) => {
                  return (
                    <LocationsItemRow
                      key={location.id}
                      setAlert={setAlert}
                      index={i}
                      location={location}
                      refetch={locationsFetch}
                    />
                  );
                }))
                :
                <TableRow>
                  <TableCell className={classes.empty} colSpan={5} align="center">
                    Nenhum local cadastrado
                  </TableCell>
                </TableRow>
            )
        }
      </TableBody>
    </Table>
  );
});

LocationsTable.propTypes = {
  locationsData: PropTypes.object,
  locationsLoading: PropTypes.bool,
  locationsFetch: PropTypes.func,
  setAlert: PropTypes.func,
  classes: PropTypes.object
};

export default withStyles(styles, { withTheme: true })(LocationsTable);
