import React, { memo } from 'react';
import PropTypes from 'prop-types';
import { compose } from 'recompose';
import { withStyles, Typography, Button } from '@material-ui/core';
import { withRouter } from 'react-router';
import Session from '../../../utils/session';
import styles from './styles';

const ServicesWelcome = memo(({ classes, history }) => (
  <section className={classes.welcome}>
    <div className={classes.container}>
      <div className={classes.root}>
        <Typography variant="h5" className={classes.center}>
          <b>Solicite servios e conecte-se com nossos parceiros</b>
        </Typography>
        <p className={classes.center}>
          Receba oramentos e contrate o que precisar sem a dor de cabea de
          lidar com vrios fornecedores.
          <br />
          Ns lidamos com eles com carinho e voc fica no controle.
        </p>
        <p className={classes.center}>
          <img src={require('../../../assets/images/services-welcome.png')} width={400} alt="Todos os servios em um nico lugar" />
        </p>
        <br />
        <p className={classes.center}>
          <Button
            variant="contained"
            color="primary"
            size="large"
            onClick={() => {
              Session.setServicesWelcomeVisited();
              history.replace('/servicos');
            }}
          >
            Comear
          </Button>
        </p>
      </div>
    </div>
  </section>
));

ServicesWelcome.propTypes = {
  classes: PropTypes.object,
  history: PropTypes.object
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter
)(ServicesWelcome);
