import React, { memo } from 'react';
import { compose, withState } from 'recompose';
import { withStyles, useTheme } from '@material-ui/core/styles';
import {
  useMediaQuery,
  Dialog,
  DialogContent,
  DialogActions,
  DialogTitle,
  IconButton,
  Grid,
  Button,
  Typography,
  FormControl,
  FormHelperText,
  Checkbox,
  FormControlLabel
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import {
  get as _get,
  first as _first,
  isEmpty as _isEmpty,
  mapValues as _mapValues,
  includes as _includes,
  pull as _pull
} from 'lodash';
import PropTypes from 'prop-types';
import { ALL_ROLES } from '../../../enums/user-roles';
import MembersService from '../../../services/members';
import LocationsService from '../../../services/locations';
import withRequest from '../../../utils/with-request';
import Tracker from '../../../utils/tracker';
import FormTextField from '../../../components/FormFields/FormTextField';
import FormSelectField from '../../../components/FormFields/FormSelectField';
import FormPhoneField from '../../../components/FormFields/FormPhoneField';
import styles from './styles';

const NewMemberModal = memo(({
  classes, onClose, setAlert, formData, setFormData, checkEmailStep, refetch,
  setCheckEmailStep, loading, setLoading, errors, setErrors, locationsData
}) => {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const disableNextButton = (loading || _isEmpty(formData.email));
  const locations = _get(locationsData, 'locations', []);
  const onCancelPressed = () => {
    onClose();
  };
  const onChangeLocations = (id) => {
    let updatedLocationsIds = [];
    if (_includes(_get(formData, 'locationIds', []), id)) {
      updatedLocationsIds = _pull(formData.locationIds, id);
    } else {
      updatedLocationsIds = formData.locationIds.concat([id]);
    }
    setFormData({ ...formData, locationIds: updatedLocationsIds });
  };

  const updateFormData = (object) => {
    setFormData({ ...formData, ...object });
  };

  const onCheckEmail = async () => {
    if (checkEmailStep) {
      try {
        setErrors({});
        setLoading(true);
        await MembersService.emailCheck(formData.email);
        setCheckEmailStep(false);
        Tracker.hit('EmailChecked - Success');
      } catch (err) {
        setErrors(_mapValues(_get(err, 'errors', {}), (e) => e[0]));
      }
      setLoading(false);
    } else {
      try {
        setErrors({});
        setLoading(true);
        await MembersService.createMember({ ...formData });
        Tracker.hit('MemberCreated - Success');
        setAlert({ alertMsg: 'Usurio criado com sucesso', alertType: 'success', showAlert: true });
        setLoading(false);
        onClose();
        refetch(true);
      } catch (err) {
        setErrors(_mapValues(_get(err, 'errors', {}), (e) => e[0]));
        setAlert({ alertMsg: 'No foi possvel criar o usurio', alertType: 'error', showAlert: true });
        setLoading(false);
      }
    }
  };

  return (
    <Dialog
      fullScreen={fullScreen}
      keepMounted
      fullWidth={!checkEmailStep}
      maxWidth={checkEmailStep ? 'sm' : 'md'}
      classes={{ paper: classes.dialogMemberForm }}
      open
    >
      <DialogTitle>
        Convidar usurio
        <IconButton
          className={classes.closeButton}
          onClick={onClose}
          disabled={loading}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.content}>
        <Grid container direction="row" spacing={1}>
          {
            checkEmailStep &&
            <Grid item xs={12} className={classes.gridSizeMedium}>
              <FormTextField
                label="Email"
                disabled={loading}
                errors={_get(errors, 'email', [])}
                value={_get(formData, 'email', '')}
                onChangeText={email => updateFormData({ email })}
              />
            </Grid>
          }
          { !checkEmailStep &&
            <React.Fragment>
              <Grid item xs={12}>
                <FormTextField
                  label="E-mail"
                  disabled
                  errors={_get(errors, 'email', [])}
                  value={_get(formData, 'email', '')}
                  onChangeText={email => updateFormData({ email })}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <FormTextField
                  label="Nome"
                  disabled={loading}
                  errors={_get(errors, 'name', [])}
                  value={_get(formData, 'name', '')}
                  onChangeText={name => updateFormData({ name })}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <FormTextField
                  label="Sobrenome"
                  disabled={loading}
                  errors={_get(errors, 'lastname', [])}
                  value={_get(formData, 'lastname', '')}
                  onChangeText={lastname => updateFormData({ lastname })}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <FormTextField
                  label="Cargo"
                  disabled={loading}
                  value={_get(formData, 'jobRole', '')}
                  onChangeText={jobRole => updateFormData({ jobRole })}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <FormPhoneField
                  label="Telefone celular"
                  disabled={loading}
                  value={_get(formData, 'phone', '')}
                  onChange={(e) => updateFormData({ phone: (e.target.value || '').replace(/[\D]+/g, '').slice(0, 15) })}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <Typography variant="h6" className={classes.blockTitle}>
                  Permisso
                </Typography>
                <FormSelectField
                  label="Nvel de acesso"
                  errors={_get(errors, 'role', [])}
                  value={_get(formData, 'role', '')}
                  disabled={loading}
                  onChange={(e) => updateFormData({ role: e.target.value })}
                  options={ALL_ROLES}
                />
              </Grid>
              <Grid item xs={12}>
                <Typography variant="h6" className={classes.blockTitle}>
                  Locais de acesso
                </Typography>
                <FormControl fullWidth error={!!errors.location_ids}>
                  {errors.location_ids && <FormHelperText>{errors.location_ids}</FormHelperText>}
                  {
                    locations.map((option) => (
                      <FormControlLabel
                        key={option.id}
                        className={classes.checkBox}
                        classes={{ label: classes.checkboxLabel }}
                        control={
                          <Checkbox
                            checked={option.checked}
                            disabled={loading}
                            onChange={() => onChangeLocations(option.id)}
                            value={option.id}
                          />
                        }
                        label={option.name}
                      />
                    ))
                  }
                </FormControl>
              </Grid>
            </React.Fragment>}
        </Grid>
      </DialogContent>
      <DialogActions className={classes.actions}>
        <Grid container justify={checkEmailStep ? 'flex-start' : 'space-between'} spacing={2}>
          {!checkEmailStep && <Grid item xs={12} sm={8} />}
          <Grid item xs={6} sm={checkEmailStep ? 6 : 2}>
            <Button
              fullWidth
              color="default"
              size="large"
              variant="contained"
              onClick={onCancelPressed}
              disabled={loading}
            >
              CANCELAR
            </Button>
          </Grid>
          <Grid item xs={6} sm={checkEmailStep ? 6 : 2}>
            <Button
              fullWidth
              color="primary"
              size="large"
              variant="contained"
              onClick={onCheckEmail}
              disabled={disableNextButton}
            >
              { checkEmailStep ? 'PRXIMO' : 'SALVAR' }
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
});

NewMemberModal.propTypes = {
  classes: PropTypes.object,
  onClose: PropTypes.func,
  loading: PropTypes.bool,
  setLoading: PropTypes.func,
  errors: PropTypes.object,
  setErrors: PropTypes.func,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  checkEmailStep: PropTypes.bool,
  setCheckEmailStep: PropTypes.func,
  locationsData: PropTypes.object,
  setAlert: PropTypes.func,
  refetch: PropTypes.func
};

const withLocations = withRequest(LocationsService.getLocations, {
  name: 'locations',
  options: () => ({
    variables: { listInactive: false, listAll: true }
  })
});
const initialFormData = { role: _first(ALL_ROLES).value, locationIds: [] };

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('formData', 'setFormData', initialFormData),
  withState('loading', 'setLoading', false),
  withState('errors', 'setErrors', {}),
  withState('checkEmailStep', 'setCheckEmailStep', true),
  withLocations
)(NewMemberModal);
