/* global window */
import React, { memo } from 'react';
import PropTypes from 'prop-types';
import {
  find as _find, map as _map, filter as _filter,
  isEmpty as _isEmpty, get as _get,
  sortBy as _sortBy, size as _size
} from 'lodash';
import { compose, lifecycle, withState } from 'recompose';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';
import { withStyles, useTheme } from '@material-ui/core/styles';
import {
  TextField, Box, FormControl, Grid, Button, Typography, useMediaQuery,
  ExpansionPanelDetails, ExpansionPanelSummary, ExpansionPanel
} from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ErrorIcon from '@material-ui/icons/Error';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import MomentUtils from '@date-io/moment';
import moment from 'moment';
import TicketBoard from './TicketBoard';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import PlanAlert from '../../components/PlanAlert';
import TicketAds from '../../components/TicketAds';
import TicketsMenuButton from './TicketsMenuButton';
import TicketsArchived from './TicketsArchived';
import TicketsWelcome from './TicketsWelcome';
import TicketsService from '../../services/tickets';
import TicketCategoriesService from '../../services/ticket-categories';
import SubscriptionsService from '../../services/subscriptions';
import Session from '../../utils/session';
import Screen from '../../utils/screen';
import Tracker from '../../utils/tracker';
import withRequest from '../../utils/with-request';
import { MEMBER_ROLE, AGENT_ROLES, USER_MANAGER_ROLES } from '../../enums/user-roles';
import TicketsEnum from '../../enums/tickets';
import styles from './styles';
import config from '../../config';

const DEFAULT_PER_PAGE = 20;
const { STATUSES, STATUS_DONE } = TicketsEnum;
const DONE = _find(STATUSES, s => s.key === STATUS_DONE).key;
const Tickets = memo(({
  alert, setAlert, classes, assigneeId, setAssigneeId, code, setCode,
  deadline, setDeadline, locationId, setLocationId, archived, setArchived,
  tickets, ticketsFetch, ticketByCodeFetch, priority, setPriority,
  ticketsError, ticketsLoading, ticketCategoriesData, ticketLoading, status, setStatus,
  ticketCategoriesError, ticketCategoriesLoading, categoryId, setCategoryId,
  ticketAssigneesData, ticketAssigneesError, ticketAssigneesLoading, history,
  ticketToOpenModal, setTicketToOpenModal, ticketByCodeLoading, moveTicketFetch,
  showMoreArchiveds, setPerPage, setPage, page, setTickets,
  showMoreLoading, setShowMoreLoading, subscriptionData
}) => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const member = Session.getUserData();
  const hasTicketSupport = _get(member, 'company.has_ticket_support');
  const currentRole = _get(member, 'role', MEMBER_ROLE);
  const isAgent = AGENT_ROLES.includes(currentRole);
  const isManager = USER_MANAGER_ROLES.includes(currentRole);
  if (hasTicketSupport) {
    if (!Session.isTicketsWelcomeVisited()) {
      return (
        <div className={classes.root}>
          <Header />
          <TicketsWelcome />
        </div>
      );
    }
    const locations = _sortBy(_get(member, 'locations', []), ['name']);
    const lockBoard = currentRole === MEMBER_ROLE;
    const doneTickets = _filter(tickets, (t) => t.status === DONE);
    const idsFromDoneTickets = _map(doneTickets, t => t.id);
    const categories = _sortBy(_get(ticketCategoriesData, 'ticketCategories', []), ['title']);
    const assignees = _sortBy(_get(ticketAssigneesData, 'members', []), ['name', 'lastname']);
    const subscription = _get(subscriptionData, 'subscription');
    const trialExpiration = _get(member, 'company.trial_expiration');
    const loading = (
      ticketsLoading || ticketCategoriesLoading ||
      ticketAssigneesLoading || ticketByCodeLoading || ticketLoading
    );
    const isDisabled = loading;
    const { PRIORITIES } = TicketsEnum;
    const error = (
      ticketsError || ticketCategoriesError || ticketAssigneesError
    );
    const resetPagination = (isArchived) => {
      setPerPage(isArchived ? DEFAULT_PER_PAGE : undefined);
      setPage(isArchived ? 0 : undefined);
      setTickets([]);
    };
    const onOpenTicket = (ticket) => {
      setTicketToOpenModal(ticket);
      window.history.pushState('', '', `/#/tickets/${ticket.id}`);
    };
    const onCloseTicket = (err, alertMsg) => {
      if (alertMsg) {
        setAlert({
          alertMsg,
          alertType: err ? 'error' : 'success',
          showAlert: true,
          actionButton: err ? null : 'Ver ticket',
          onClickAction: err ? null : () => history.push(`/tickets/${ticketToOpenModal.id}`)
        });
      }
      setTicketToOpenModal(null);
      if (!archived) {
        ticketsFetch(false);
      }
      window.history.pushState('', '', '/#/tickets');
    };
    const onDragCard = async (params) => {
      await moveTicketFetch(false, '', null, params);
      ticketsFetch(false);
    };
    const onChangeTicketCode = (e) => {
      const newCode = e.target.value.replace(/[\D]+/g, '');
      if (code !== newCode) {
        setCode(newCode);
      }
    };
    const onMobileRequestTicketByCode = () => {
      if (_size(code) && Screen.isMobile()) {
        const trackProps = { select_code: code };
        Tracker.hit('Tickets - FilteredByCode', trackProps);
        setTicketToOpenModal(null);
        ticketByCodeFetch(true);
      }
    };
    const onRequestTicketByCode = (e) => {
      if (_size(code) && e.keyCode === 13) {
        const trackProps = { select_code: code };
        Tracker.hit('Tickets - FilteredByCode', trackProps);
        setTicketToOpenModal(null);
        ticketByCodeFetch(true);
      }
    };
    const onSelectAssignee = (e) => {
      const selectedAssigneeId = _isEmpty(e.target.value) ? undefined : e.target.value;
      setAssigneeId(selectedAssigneeId);
      const trackProps = { select_assignee: selectedAssigneeId };
      Tracker.hit('Tickets - FilteredByAssignee', trackProps);
      resetPagination(archived);
      ticketsFetch(true);
    };
    const onSelectDeadline = (e) => {
      const selectedDeadline = e;
      setDeadline(selectedDeadline);
      Tracker.hit('Tickets - FilteredByDeadline');
      resetPagination(archived);
      ticketsFetch(true);
    };
    const onSelectPriority = (e) => {
      const selectedPriority = _isEmpty(e.target.value) ? undefined : e.target.value;
      setPriority(selectedPriority);
      const trackProps = { priority };
      Tracker.hit('Tickets - FilteredByPriority', trackProps);
      resetPagination(archived);
      ticketsFetch(true);
    };
    const onSelectCategory = (e) => {
      const selectedCategoryId = _isEmpty(e.target.value) ? undefined : e.target.value;
      setCategoryId(selectedCategoryId);
      const trackProps = { select_category: selectedCategoryId };
      Tracker.hit('Tickets - FilteredByCategory', trackProps);
      resetPagination(archived);
      ticketsFetch(true);
    };
    const onSelectBuilding = (e) => {
      const selectedBuildingId = _isEmpty(e.target.value) ? undefined : e.target.value;
      setLocationId(selectedBuildingId);
      const trackProps = { select_building: selectedBuildingId };
      Tracker.hit('Tickets - FilteredByBuilding', trackProps);
      resetPagination(archived);
      ticketsFetch(true);
    };
    const onSelectStatus = (e) => {
      const selectedStatus = _isEmpty(e.target.value) ? undefined : e.target.value;
      setStatus(selectedStatus);
      const trackProps = { select_status: selectedStatus };
      Tracker.hit('Tickets - FilteredByStatus', trackProps);
      resetPagination(archived);
      ticketsFetch(true);
    };
    const onToggleArchivedTickets = (isArchived) => {
      setArchived(isArchived);
      resetPagination(isArchived);
      ticketsFetch(true);
    };
    const onShowMoreArchivedTickets = () => {
      setShowMoreLoading(true);
      setPerPage(DEFAULT_PER_PAGE);
      setPage(page + 1);
      ticketsFetch(false);
    };
    const onClearAlert = () => setAlert({
      alertMsg: '', alertType: '', showAlert: false, actionButton: null, onClickAction: null
    });
    const { showAlert, alertType, alertMsg, actionButton, onClickAction } = alert;
    let alertBox = null;
    if (showAlert) {
      alertBox = (
        <AlertBox
          message={alertMsg}
          type={alertType}
          actionButton={actionButton}
          onClickAction={onClickAction}
          onClose={onClearAlert}
        />
      );
    }
    const renderFilters = () => (
      <Grid container spacing={2}>
        <Grid item xs={6} sm={6} md={1}>
          <FormControl fullWidth>
            <TextField
              label="Busca"
              placeholder="N"
              disabled={isDisabled}
              value={code}
              type="tel"
              InputLabelProps={{ shrink: true }}
              onKeyDown={onRequestTicketByCode}
              onBlur={onMobileRequestTicketByCode}
              onChange={onChangeTicketCode}
            />
          </FormControl>
        </Grid>
        {
          _size(locations) > 1 ?
            <Grid item xs={6} sm={6} md={2}>
              <FormControl fullWidth>
                <TextField
                  select
                  label="Local"
                  className={classes.filterSelect}
                  disabled={isDisabled}
                  value={locationId}
                  InputLabelProps={{ shrink: true }}
                  SelectProps={{ native: true }}
                  onChange={onSelectBuilding}
                >
                  <option value="">Todos</option>
                  {locations.map(b => (
                    <option key={b.id} value={b.id}>
                      {b.name}
                    </option>
                  ))}
                </TextField>
              </FormControl>
            </Grid>
            :
            <React.Fragment />
        }
        {
          !lockBoard ?
            <Grid item xs={6} sm={6} md={2}>
              <FormControl fullWidth>
                <TextField
                  select
                  label="Responsvel"
                  className={classes.filterSelect}
                  disabled={isDisabled}
                  value={assigneeId}
                  InputLabelProps={{ shrink: true }}
                  SelectProps={{ native: true }}
                  onChange={onSelectAssignee}
                >
                  <option value="all">Todos</option>
                  <option value="">Nenhum</option>
                  {assignees.map(a => (
                    <option key={a.id} value={a.id}>
                      {`${a.name} ${a.lastname}`}
                    </option>
                  ))}
                </TextField>
              </FormControl>
            </Grid>
            :
            <React.Fragment />
        }
        <Grid item xs={6} sm={6} md={2}>
          <FormControl fullWidth>
            <TextField
              select
              label="Categoria"
              className={classes.filterSelect}
              disabled={isDisabled}
              value={categoryId}
              InputLabelProps={{ shrink: true }}
              SelectProps={{ native: true }}
              onChange={onSelectCategory}
            >
              <option value="">Todos</option>
              {categories.map(b => (
                <option key={b.id} value={b.id}>
                  {b.title}
                </option>
              ))}
            </TextField>
          </FormControl>
        </Grid>
        {
          archived ?
            <Grid item xs={6} sm={6} md={2}>
              <FormControl fullWidth>
                <TextField
                  select
                  label="Status"
                  className={classes.filterSelect}
                  disabled={isDisabled}
                  value={status}
                  InputLabelProps={{ shrink: true }}
                  SelectProps={{ native: true }}
                  onChange={onSelectStatus}
                >
                  <option value="">Todos</option>
                  {STATUSES.map(b => (
                    <option key={b.key} value={b.key}>
                      {b.label}
                    </option>
                  ))}
                </TextField>
              </FormControl>
            </Grid>
            :
            <React.Fragment />
        }
        <Grid item xs={6} sm={6} md={2}>
          <FormControl fullWidth>
            <TextField
              select
              label="Prioridade"
              className={classes.filterSelect}
              disabled={isDisabled}
              value={priority}
              InputLabelProps={{ shrink: true }}
              SelectProps={{ native: true }}
              onChange={onSelectPriority}
            >
              <option value="all">Todos</option>
              {PRIORITIES.map(b => (
                <option key={b.key} value={b.key}>
                  {b.label}
                </option>
              ))}
            </TextField>
          </FormControl>
        </Grid>
        <Grid item xs={6} sm={6} md={2}>
          <FormControl fullWidth>
            <MuiPickersUtilsProvider
              libInstance={moment}
              utils={MomentUtils}
              locale={config.locale}
            >
              <DatePicker
                autoOk
                label="Prazo"
                clearable
                animateYearScrolling
                className={classes.filterSelect}
                disabled={isDisabled}
                invalidDateMessage="Formato invlido"
                InputLabelProps={{ shrink: true }}
                value={deadline}
                onChange={onSelectDeadline}
                format="DD/MM/YYYY"
                cancelLabel="Cancelar"
                clearLabel="Limpar"
              />
            </MuiPickersUtilsProvider>
          </FormControl>
        </Grid>
      </Grid>
    );
    const headTitle = `Tickets${archived ? ' arquivados' : ''}`;
    return (
      <div className={classes.root}>
        <Header alertBox={alertBox} error={error} />
        <section>
          <div className={classes.container}>
            <PlanAlert
              trialExpiration={trialExpiration}
              willDowngradeAt={_get(subscription, 'willDowngradeAt')}
              willCancelAt={_get(subscription, 'willCancelAt')}
            />
            <Box display="flex" justifyContent="space-between" mb={2}>
              <Typography variant="h5">
                <b>{_size(tickets) ? `${headTitle} (${_size(tickets)})` : headTitle}</b>
              </Typography>
              <Box display="flex">
                <Button
                  variant="contained"
                  component={Link}
                  to="/tickets/novo"
                  color="primary"
                  size="large"
                  disabled={isDisabled}
                  className={isSmallScreen ? classes.fab : null}
                >
                  Novo ticket
                </Button>
                {
                  isAgent && (
                    <Box ml={1}>
                      <TicketsMenuButton
                        setAlert={setAlert}
                        isArchived={archived}
                        onToggleArchived={onToggleArchivedTickets}
                      />
                    </Box>
                  )
                }
              </Box>
            </Box>
            {
              isSmallScreen ?
                isAgent &&
                <ExpansionPanel>
                  <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                    <Typography variant="subtitle1">Filtros</Typography>
                  </ExpansionPanelSummary>
                  <ExpansionPanelDetails>
                    {renderFilters()}
                  </ExpansionPanelDetails>
                </ExpansionPanel>
                :
                isAgent && renderFilters()
            }
          </div>
          {
            archived ?
              <TicketsArchived
                tickets={tickets}
                ticketToOpenModal={ticketToOpenModal}
                categories={categories}
                assignees={assignees}
                loading={loading}
                onOpenTicketModal={onOpenTicket}
                onCloseTicketModal={onCloseTicket}
                onShowMoreArchiveds={onShowMoreArchivedTickets}
                showMore={showMoreArchiveds}
                showMoreLoading={showMoreLoading}
                setAlert={setAlert}
              />
              :
              <div className={classes.container}>
                <Grid container>
                  <Grid item xs={12}>
                    <TicketBoard
                      isManager={isAgent}
                      ticketsFetch={ticketsFetch}
                      doneTickets={idsFromDoneTickets}
                      lockBoard={lockBoard}
                      tickets={tickets}
                      ticketToOpenModal={ticketToOpenModal}
                      categories={categories}
                      assignees={assignees}
                      loading={loading}
                      onClickCard={onOpenTicket}
                      onDragCard={onDragCard}
                      onCloseCardModal={onCloseTicket}
                      setAlert={setAlert}
                    />
                  </Grid>
                </Grid>
              </div>
          }
        </section>
      </div>
    );
  }
  return (
    <div className={classes.root}>
      <Header />
      <section>
        <div className={classes.container}>
          <Grid container spacing={1}>
            <div className={classes.centerHeaderView}>
              {
                isManager ?
                  <TicketAds />
                  :
                  <React.Fragment>
                    <ErrorIcon className={classes.emptyHeaderIcon} />
                    <b className={classes.emptyHeaderTitle}>
                      Essa conta no possui o servio de tickets
                    </b>
                  </React.Fragment>
              }
            </div>
          </Grid>
        </div>
      </section>
    </div>
  );
});

Tickets.propTypes = {
  classes: PropTypes.object,
  history: PropTypes.object,
  setPerPage: PropTypes.func,
  setPage: PropTypes.func,
  page: PropTypes.number,
  showMoreArchiveds: PropTypes.bool,
  alert: PropTypes.object,
  setAlert: PropTypes.func,
  archived: PropTypes.any,
  setArchived: PropTypes.func,
  assigneeId: PropTypes.string,
  setAssigneeId: PropTypes.func,
  priority: PropTypes.string,
  setPriority: PropTypes.func,
  categoryId: PropTypes.string,
  setCategoryId: PropTypes.func,
  deadline: PropTypes.string,
  setDeadline: PropTypes.func,
  locationId: PropTypes.string,
  setLocationId: PropTypes.func,
  code: PropTypes.string,
  setCode: PropTypes.func,
  status: PropTypes.string,
  setStatus: PropTypes.func,
  moveTicketFetch: PropTypes.func,
  ticketToOpenModal: PropTypes.object,
  setTicketToOpenModal: PropTypes.func,
  setTickets: PropTypes.func,
  showMoreLoading: PropTypes.bool,
  setShowMoreLoading: PropTypes.func,
  subscriptionData: PropTypes.object,
  ticketByCodeFetch: PropTypes.func,
  ticketByCodeLoading: PropTypes.bool,
  ticketsError: PropTypes.object,
  ticketsFetch: PropTypes.func,
  tickets: PropTypes.array,
  ticketsLoading: PropTypes.bool,
  ticketLoading: PropTypes.bool,
  ticketCategoriesData: PropTypes.object,
  ticketCategoriesError: PropTypes.object,
  ticketCategoriesLoading: PropTypes.bool,
  ticketAssigneesData: PropTypes.object,
  ticketAssigneesError: PropTypes.object,
  ticketAssigneesLoading: PropTypes.bool
};

const withSubscription = withRequest(SubscriptionsService.get, {
  name: 'subscription'
});

const withTicketCategories = withRequest(TicketCategoriesService.getCategories, {
  name: 'ticketCategories'
});

const withTicketAssignees = withRequest(TicketsService.getAssignables, {
  name: 'ticketAssignees'
});

const withMoveTicket = withRequest(TicketsService.moveTicket, {
  name: 'moveTicket',
  init: false,
  options: ({ setAlert }) => ({
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao mover esse ticket',
        alertType: 'warning',
        showAlert: true
      });
    }
  })
});

const withTicketByCode = withRequest(TicketsService.getTicketByCode, {
  name: 'ticketByCode',
  init: false,
  options: ({ setAlert, setCode, setTicketToOpenModal, code }) => ({
    variables: code,
    onRequestFinished: (data) => {
      const ticketByCode = _get(data, 'ticket');
      setTicketToOpenModal(ticketByCode);
      setCode('');
      window.history.pushState('', '', `/#/tickets/${ticketByCode.id}`);
    },
    onRequestError: () => {
      setTicketToOpenModal(null);
      setAlert({
        alertMsg: `Ticket ${code} no foi identificado`,
        alertType: 'warning',
        showAlert: true
      });
      window.history.pushState('', '', '/#/tickets');
    }
  })
});

const withTicket = withRequest(TicketsService.getTicket, {
  name: 'ticket',
  init: false,
  options: ({ match, setAlert, setTicketToOpenModal }) => ({
    variables: _get(match, 'params.id'),
    onRequestFinished: (data) => {
      const ticketById = _get(data, 'ticket');
      setTicketToOpenModal(ticketById);
      window.history.pushState('', '', `/#/tickets/${ticketById.id}`);
    },
    onRequestError: () => {
      setTicketToOpenModal(null);
      setAlert({
        alertMsg: 'Este ticket no foi identificado',
        alertType: 'warning',
        showAlert: true
      });
      window.history.pushState('', '', '/#/tickets');
    }
  })
});

const withTickets = withRequest(TicketsService.getTickets, {
  name: 'tickets',
  init: false,
  options: ({
    locationId, assigneeId, categoryId, deadline, priority,
    archived, status, perPage, page, setShowMoreArchiveds,
    setTickets, tickets, setShowMoreLoading
  }) => ({
    variables: {
      locationId, assigneeId, categoryId, deadline, priority, archived, status, perPage, page
    },
    onRequestFinished: (data) => {
      const newTickets = _get(data, 'tickets', []);
      if (archived) {
        setShowMoreArchiveds(_size(newTickets) >= perPage);
        setTickets(tickets.concat(newTickets));
      } else {
        setTickets(newTickets);
      }
      setShowMoreLoading(false);
    },
    onRequestError: () => {
      setShowMoreLoading(false);
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter,
  withState('assigneeId', 'setAssigneeId', 'all'),
  withState('code', 'setCode', ''),
  withState('tickets', 'setTickets', []),
  withState('ticketToOpenModal', 'setTicketToOpenModal'),
  withState('categoryId', 'setCategoryId'),
  withState('locationId', 'setLocationId'),
  withState('priority', 'setPriority'),
  withState('status', 'setStatus'),
  withState('deadline', 'setDeadline', null),
  withState('archived', 'setArchived', false),
  withState('perPage', 'setPerPage'),
  withState('page', 'setPage'),
  withState('showMoreArchiveds', 'setShowMoreArchiveds', false),
  withState('showMoreLoading', 'setShowMoreLoading', false),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false,
    actionButton: null,
    onClickAction: null
  }),
  withSubscription,
  withTickets,
  withTicket,
  withTicketByCode,
  withTicketCategories,
  withTicketAssignees,
  withMoveTicket,
  lifecycle({
    componentDidMount() {
      const { ticketsFetch, ticketFetch, ticketToOpenModal, match } = this.props;
      const paramsId = _get(match, 'params.id');
      if (_isEmpty(ticketToOpenModal)) {
        ticketsFetch(true);
      }
      if (_size(paramsId)) {
        ticketFetch(false);
      }
    }
  })
)(Tickets);
