import React, { memo } from 'react';
import { compose } from 'recompose';
import { map } from 'lodash';
import { withStyles } from '@material-ui/core/styles';
import {
  Typography, Box,
  Table, TableHead, TableBody, TableRow, TableCell
} from '@material-ui/core';
import moment from 'moment';
import PropTypes from 'prop-types';

import Loading from '../../../components/Loading';
import styles from './styles';

const formatDuration = (duration) => {
  return duration ? moment.duration(duration).humanize() : ' - ';
};

const TotalsTable = ({ data: { created, done, in_progress, time_to_reply, time_to_finish } }) => (
  <Table>
    <TableHead>
      <TableRow>
        <TableCell>Criados</TableCell>
        <TableCell>Concludos</TableCell>
        <TableCell>Em Andamento</TableCell>
        <TableCell>Tempo de Resposta</TableCell>
        <TableCell>Tempo de Resoluo</TableCell>
      </TableRow>
    </TableHead>
    <TableBody>
      <TableRow>
        <TableCell>
          {created}
        </TableCell>
        <TableCell>
          {done}
        </TableCell>
        <TableCell>
          {in_progress}
        </TableCell>
        <TableCell>
          {formatDuration(time_to_reply)}
        </TableCell>
        <TableCell>
          {formatDuration(time_to_finish)}
        </TableCell>
      </TableRow>
    </TableBody>
  </Table>
);
TotalsTable.propTypes = {
  data: PropTypes.object
};

const ByCategoryTable = ({ data, classes }) => (
  <Table>
    <TableHead>
      <TableRow>
        <TableCell className={classes.tableSmallCell}>Categoria</TableCell>
        <TableCell>Criados</TableCell>
        <TableCell>Concludos</TableCell>
        <TableCell>Em Andamento</TableCell>
        <TableCell>Tempo de Resposta</TableCell>
        <TableCell>Tempo de Resoluo</TableCell>
      </TableRow>
    </TableHead>
    <TableBody>
      {
        map(data, (item) => (
          <TableRow key={item.category_title}>
            <TableCell className={classes.tableSmallCell}>
              {item.category_title}
            </TableCell>
            <TableCell>
              {item.created}
            </TableCell>
            <TableCell>
              {item.done}
            </TableCell>
            <TableCell>
              {item.in_progress}
            </TableCell>
            <TableCell>
              {formatDuration(item.time_to_reply)}
            </TableCell>
            <TableCell>
              {formatDuration(item.time_to_finish)}
            </TableCell>
          </TableRow>
        ))
      }
    </TableBody>
  </Table>
);
ByCategoryTable.propTypes = {
  data: PropTypes.array,
  classes: PropTypes.object
};

const ByAssigneeTable = ({ data, classes }) => (
  <Table>
    <TableHead>
      <TableRow>
        <TableCell className={classes.tableSmallCell}>Responsvel</TableCell>
        <TableCell>Criados</TableCell>
        <TableCell>Concludos</TableCell>
        <TableCell>Em Andamento</TableCell>
        <TableCell>Tempo de Resposta</TableCell>
        <TableCell>Tempo de Resoluo</TableCell>
      </TableRow>
    </TableHead>
    <TableBody>
      {
        map(data, (item) => (
          <TableRow key={item.assignee_full_name}>
            <TableCell className={classes.tableSmallCell}>
              {item.assignee_full_name}
            </TableCell>
            <TableCell>
              {item.created}
            </TableCell>
            <TableCell>
              {item.done}
            </TableCell>
            <TableCell>
              {item.in_progress}
            </TableCell>
            <TableCell>
              {formatDuration(item.time_to_reply)}
            </TableCell>
            <TableCell>
              {formatDuration(item.time_to_finish)}
            </TableCell>
          </TableRow>
        ))
      }
    </TableBody>
  </Table>
);
ByAssigneeTable.propTypes = {
  data: PropTypes.array,
  classes: PropTypes.object
};

const Result = ({
  classes, reportData, isLoading
}) => {
  if (isLoading) return <Loading />;
  if (!reportData || !reportData.totals) return null;

  return (
    <React.Fragment>
      <Box mt={2}>
        <Typography variant="h6">
          <b>Total</b>
        </Typography>
        <TotalsTable data={reportData.totals} />
      </Box>

      <Box mt={2}>
        <Typography variant="h6">
          <b>Por Categoria</b>
        </Typography>
        <ByCategoryTable data={reportData.by_category} classes={classes} />
      </Box>

      <Box mt={2}>
        <Typography variant="h6">
          <b>Por Responsvel</b>
        </Typography>
        <ByAssigneeTable data={reportData.by_assignee} classes={classes} />
      </Box>
    </React.Fragment>
  );
};

Result.propTypes = {
  reportData: PropTypes.object,
  isLoading: PropTypes.bool,
  classes: PropTypes.object
};

export default compose(
  withStyles(styles, { withTheme: true }),
  memo
)(Result);
