import FieldNormalizer from 'field-normalizer';
import Api from '../utils/api';

const Locations = {
  getLocations({ listInactive, listAll }) {
    const body = FieldNormalizer.toSnakeCase({ listInactive, listAll });
    return Api.get('/locations', body);
  },
  createLocation(location) {
    return Api.post('/locations', location);
  },
  updateLocation({ id, changes }) {
    return Api.put(`/locations/${id}`, changes);
  },
  toggleActive(id) {
    return Api.delete(`/locations/${id}/toggle_active`);
  },
  getLocationsByCompany(companyId) {
    if (companyId) {
      const body = FieldNormalizer.toSnakeCase({ companyId });
      return Api.get('/public_locations', body);
    }
    return null;
  }
};

export default Locations;
