import React from 'react';
import { isEmpty, isArray } from 'lodash';
import moment from 'moment';
import MomentUtils from '@date-io/moment';
import { FormControl, FormHelperText } from '@material-ui/core';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import PropTypes from 'prop-types';
import config from '../../config';
import FormTextField from './FormTextField';
import styles from './styles';

const FormDateField = ({
  label, disabled, value, onChangeValue, minDate, errors,
  disableFuture, disablePast, readOnly, variant = 'outlined', ...otherProps
}) => (
  readOnly ?
    <FormTextField
      disabled
      readOnly
      label={label}
      variant={variant}
      {...otherProps}
    />
    :
    <FormControl fullWidth margin="normal" error={!isEmpty(errors)}>
      <MuiPickersUtilsProvider
        libInstance={moment}
        utils={MomentUtils}
        locale={config.locale}
      >
        <DatePicker
          label={label}
          value={value}
          onChange={onChangeValue}
          minDate={minDate}
          autoOk
          clearable
          animateYearScrolling
          invalidDateMessage="Formato invlido"
          minDateMessage=""
          inputVariant={variant}
          InputLabelProps={{ shrink: true }}
          format="DD/MM/YYYY"
          cancelLabel="Cancelar"
          clearLabel="Limpar"
          disableFuture={disableFuture}
          disablePast={disablePast}
        />
      </MuiPickersUtilsProvider>
      {!isEmpty(errors) &&
        <FormHelperText style={styles.errors}>
          {isArray(errors) ? errors.join(', ') : errors}
        </FormHelperText>}
    </FormControl>
);

FormDateField.propTypes = {
  errors: PropTypes.any,
  variant: PropTypes.string,
  label: PropTypes.string,
  disabled: PropTypes.bool,
  readOnly: PropTypes.bool,
  value: PropTypes.any,
  onChangeValue: PropTypes.func,
  minDate: PropTypes.any,
  disableFuture: PropTypes.bool,
  disablePast: PropTypes.bool
};

export default FormDateField;
